import { Component, OnInit, Input, ChangeDetectionStrategy } from '@angular/core';
import { Formats } from '../../Classes/Formats';
import { ServiceDataService } from '../../services/service-data.service';
import { ProductService } from '../../services/product.service';
import { ProductsComponent } from '../../product/products/products.component';
import { NgbModal, NgbModalRef } from '@ng-bootstrap/ng-bootstrap';
import { ServiceComponent } from '../../product/service/service.component';
import { HttpErrorResponse } from '@angular/common/http';
import * as XLSX from 'ts-xlsx';
import { User } from '../../models/user';
import { CustomerService } from '../../services/customer.service';
import { CommonService } from '../../services/common.service';

@Component({
  selector: 'app-bulk-import-modal',
  templateUrl: './bulk-import-modal.component.html',
  styleUrls: ['./bulk-import-modal.component.scss'],
})

export class BulkImportModalComponent implements OnInit {
  currentUser: User;
  file: File;
  rawFileValue;
  selectedFileName = 'No file selected';
  excelData = [];
  invalidExcelImport: boolean = false;
  validatedBulkData = [];
  private formater = new Formats();
  
  constructor(
    private serviceService: ServiceDataService,
    private productService: ProductService,
    private modalService: NgbModal,
    private commonService: CommonService
  ) {
    this.currentUser = JSON.parse(localStorage.getItem("CurrentUser"));
   }

  ngOnInit() {

  }




  @Input('bulkType') bulkType: string;
  @Input('templateLink') templateLink: string;

  hideErrorMessage(){
    this.invalidExcelImport = false;
  }
  

  upload(){
    this.validatedBulkData = [];
    switch (this.bulkType) {
      case "service":
      if (this.formater.getDataAsWorkSheet() != null) {
        this.validateBulkService();
        if(this.validatedBulkData.length >= 1 ){
          this.serviceService.postBulkService(this.validatedBulkData).subscribe(
           data => {
             this.rawFileValue.target.value = "";
             this.commonService.updateService(this.validatedBulkData);
             this.validatedBulkData =[];
             this.modalService.dismissAll();
           }, (error: HttpErrorResponse) => {
             this.validatedBulkData=[];
           }
          )
          }else{
          this.invalidExcelImport = true;
          setTimeout(() => {
            this.invalidExcelImport = false;
          }, 2000);
          this.validatedBulkData =[];
            
          }
      } else {
        this.invalidExcelImport = true;
        setTimeout(() => {
          this.invalidExcelImport = false;
        }, 2000);
      }
      break;
      case "product":
      if (this.formater.getDataAsWorkSheet() != null) {
        this.validateBulkProduct();
        if(this.validatedBulkData.length >= 1 ){
          this.productService.postBulkProduct(this.validatedBulkData).subscribe(
           data => {
             this.rawFileValue.target.value = "";
             this.commonService.updateProduct(this.validatedBulkData);
             this.validatedBulkData =[];
             this.modalService.dismissAll();
           }, (error: HttpErrorResponse) => {
             this.validatedBulkData=[];
           }
          )
          }else{
          this.invalidExcelImport = true;
          setTimeout(() => {
            this.invalidExcelImport = false;
          }, 2000);
          this.validatedBulkData =[];
            
          }
      } else {
        this.invalidExcelImport = true;
        setTimeout(() => {
          this.invalidExcelImport = false;
        }, 2000);
      }break;
    //   case "customer":
    // this.formater.getDataAsWorkSheet() != null?this.validateBulkCustomer(): this.invalidExcelImport = true;
    //       if(this.validatedBulkData.length >= 1 ){
    //         this.customerServie.postBulkCustomer(this.validateBulkCustomer).subscribe(
    //          data => {
    //            this.rawFileValue.target.value = "";
    //            this.validatedBulkData =[];
    //            this.product.loadData();
    //            this.modalService.dismissAll();
    //            this.uploadSuccess = true;
    //            setTimeout(() => {
    //              this.uploadSuccess = false;
    //            }, 2000);
    //          }, (error: HttpErrorResponse) => {
    //            this.validatedBulkData=[];
    //          }
    //         )
    //         }else{
    //         this.invalidExcelImport = true;
    //         setTimeout(() => {
    //           this.invalidExcelImport = !this.invalidExcelImport;
    //         }, 2000);
    //         this.validatedBulkData =[];
              
    //         }
    //   break;
      default:
        break;
    }
    
  }
  readFile(file){
    this.selectedFileName = file.target.files[0].name;
    this.rawFileValue = file;
    this.invalidExcelImport = false;
    this.file = this.formater.process(file);
    this.formater.extractDData(this.file);
  }
  validateBulkProduct(){
    let excelData = this.formater.getDataAsWorkSheet();
    if(excelData.A1.h === "Name" && excelData.B1.h =="Code" && excelData.C1.h == "Cost_Price" && excelData.D1.h == "Sell_Price" && excelData.E1.h =="Opening_Qty"){
      excelData.A1.w = "name";excelData.B1.w = "code";excelData.C1.w ="costPrice"; excelData.D1.w = "sellPrice";excelData.E1.w = "openingQty";
      this.validatedBulkData = XLSX.utils.sheet_to_json(excelData, {raw: true});
      this.validatedBulkData.forEach(data => {
        data.companyID = this.currentUser.companyID;
        data.locationID = this.currentUser.locationID;
        data.userCreated = this.currentUser.id;
      });
      excelData = null;
    }else{
      this.invalidExcelImport = true;
      setTimeout(() => {
        this.invalidExcelImport = false;
      }, 2000);

    }
    this.formater = new Formats();
  }

  validateBulkService(){
    let excelData = this.formater.getDataAsWorkSheet();
    if(excelData.A1.h === "Name" && excelData.B1.h =="Description" && excelData.C1.h == "Code" && excelData.D1.h == "Price"){
      excelData.A1.w = "name";excelData.B1.w = "description";excelData.C1.w ="code"; excelData.D1.w = "price";
      this.validatedBulkData = XLSX.utils.sheet_to_json(excelData, {raw: true});
      this.validatedBulkData.forEach(data => {
        data.companyID = this.currentUser.companyID;
        data.userCreated = this.currentUser.id;
      });
      excelData = null;
    }else{
      this.invalidExcelImport = true;
      setTimeout(() => {
        this.invalidExcelImport = false;
      }, 2000);
    }
    this.formater = new Formats();
  }

  // validateBulkCustomer(){
  //   let excelData = this.formater.getDataAsWorkSheet();
  //   console.log(excelData);
  //   if(excelData.A1.h === "Name" && excelData.B1.h =="Phone" && excelData.C1.h == "Email"){
  //     excelData.A1.w = "name";excelData.B1.w = "phoneNo";excelData.C1.w ="email";
  //     this.validatedBulkData = XLSX.utils.sheet_to_json(excelData, {raw: true});
  //     this.validatedBulkData.forEach(data => {
  //       data.companyID = this.currentUser.companyID;
  //       data.userCreated = this.currentUser.id;
  //     });
  //     console.log(this.validatedBulkData);
  //     excelData = null;
  //   }else{
  //     this.invalidExcelImport = true;

  //   }
  //   this.formater = new Formats();
  // }

  c(){
    this.modalService.dismissAll();
  }



}
