import { Component, OnInit } from '@angular/core';
import{StoreComponent} from '../store/store.component';
import{Location} from '../../models/location';
import { FormGroup } from '@angular/forms';
@Component({
  selector: 'app-editstore',
  templateUrl: './editstore.component.html',
  styleUrls: ['./editstore.component.scss']
})
export class EditstoreComponent implements OnInit {
 store:Location;
 editstoreform:FormGroup;
  constructor(private storecomp:StoreComponent) {
    this.store=this.storecomp.currentstore;
   }
  ngOnInit() {
    console.log(this.store);
  }
 onclick(){
   this.storecomp.modalService.dismissAll('click');
 }
}
