import { Component, OnInit } from '@angular/core';
import { ReportingService } from '../../services/reporting.service';
import { IStockAdjust } from '../../models/Stocks';
import { User } from '../../models/user';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { formatDate } from '@angular/common';
import { HttpErrorResponse } from '@angular/common/http';

@Component({
  selector: 'app-adjust-stock',
  templateUrl: './adjust-stock.component.html',
  styleUrls: ['./adjust-stock.component.scss', '../../../vendor/libs/ngx-sweetalert2/ngx-sweetalert2.scss','../../../vendor/libs/ng-select/ng-select.scss','../../../vendor/libs/angular-2-dropdown-multiselect/angular-2-dropdown-multiselect.scss']
})
export class AdjustStockComponent implements OnInit {

  adjustStocks: IStockAdjust[];
  user: User;
  selectFrom: any[] = [];
  searchbyterm;
  loading: boolean = true;
  connectionError: boolean = false;
  startDate;
   endDate;

  constructor(
    private reportService: ReportingService
  ) {
    this.user = JSON.parse(localStorage.getItem("CurrentUser"));
    this.reportService.getAdjustSummary(this.user.companyID, this.user.locationID, "0", "0")
    .pipe(
      map(stocks => {
        this.adjustStocks = stocks;
      })
    ).subscribe(data => {
      this.loading = false;
    }, (error: HttpErrorResponse) => {
      error.status == 0 ? this.connectionError = true: this.connectionError = false;
      this.loading = false;
    });
    
   }

   filterChange(){
      this.reportService.getLocationsByCompanyID(this.user.companyID).pipe(
        map((locations: any[]) => {
          locations.push({
            name: "All",
            id: 0
          })
          locations.reverse();
          this.selectFrom = locations;
        })
      ).subscribe()
   }


 onfilter(){
   this.loading = true;
   var startDate = this.startDate ===undefined  || this.startDate.trim() ==""?undefined: formatDate(this.startDate, 'dd-MM-yyyy', 'en');
   var endDate = this.endDate ===undefined  || this.endDate.trim() ==""?undefined: formatDate(this.endDate, 'dd-MM-yyyy', 'en');


   if((typeof startDate !== "undefined" || startDate !="") && (typeof endDate === "undefined" || endDate == "")){
     endDate = "0";
   }else if((typeof startDate === "undefined" || startDate == "") && (typeof endDate !== "undefined" || endDate != "")){
     startDate = "0";
   }else if((typeof startDate === "undefined" || startDate == "") &&  (typeof endDate === "undefined" || endDate == "")){
     startDate = "0", endDate = "0";
   }

  this.reportService.getAdjustSummary(this.user.companyID, this.searchbyterm.id, startDate, endDate)
  .pipe(
    map(stocks => {
      this.adjustStocks = stocks;
    })
  ).subscribe(data => {
    this.loading = false;
  }, (error: HttpErrorResponse) => {
    error.status == 0 ? this.connectionError = true: this.connectionError = false;
    this.loading = false;
  });
}


  ngOnInit() {
  }

}
