import { Component, OnInit } from '@angular/core';
import{ReportingService} from '../../services/reporting.service'
import * as numeral from 'numeral';
import { User } from '../../models/user';
import { Report } from '../../models/report';
@Component({
  selector: 'app-topinfobar',
  templateUrl: './topinfobar.component.html',
  styleUrls: ['./topinfobar.component.scss']
})
export class TopinfobarComponent implements OnInit {
 sales:Report[];
 stock:Report[];
 dailysale=0;
 dailyrev=0;
 totalproducts=0;
 totalcustomers=0
 currentuser:User;
  constructor(private reportservice:ReportingService) {
    this.currentuser=<User> JSON.parse(localStorage.getItem("CurrentUser"));
   }

  ngOnInit() {
 this.loadtodaysale();
 this.loadproductnumbers();
 this.loadcustomernumber();
  }
  formatPrice(price, dropDecimals = false) {
    return numeral(price).format(dropDecimals ? '0,0' : '0,0.00');
  }
  loadtodaysale(){
    
    this.reportservice.getdailysale(this.currentuser.companyID).subscribe(data=>{
     this.sales=<Report[]> data;
     this.sales.map((sale)=>{this.dailysale=this.dailysale+sale.vol; this.dailyrev=this.dailyrev+sale.val})
    },error=>{

    })
  }

  loadproductnumbers(){
    this.reportservice.getproductnumbers(this.currentuser.companyID).subscribe(data=>{
      this.stock =<Report[]> data;
      this.stock.map((stock)=>{this.totalproducts=this.totalproducts+stock.noOfProduct;})
    },error=>{

    })
  }
  loadcustomernumber(){
this.reportservice.getcustomernumbers(this.currentuser.companyID).subscribe(data=>{
  this.totalcustomers=<number>data;
},error=>{

})
  }

}
