import { Component, OnInit } from '@angular/core';
import{ReportingService} from '../../services/reporting.service'
import { Report } from '../../models/report';
import { User } from '../../models/user';
import * as numeral from 'numeral';


@Component({
  selector: 'app-popularitems',
  templateUrl: './popularitems.component.html',
  styleUrls: ['./popularitems.component.scss']
})
export class PopularitemsComponent implements OnInit {
 popularprod:Report[];
 currentuser:User;
 
  constructor(private reportservice:ReportingService) { 
    this.currentuser=JSON.parse(localStorage.getItem("CurrentUser"));
  }

  ngOnInit() {
    this.loaddata();
  }
 loaddata(){
   this.reportservice.getpopularproduct(this.currentuser.companyID,0).subscribe(data=>{
     this.popularprod=<Report[]>data;
   })
 }
 formatPrice(price, dropDecimals = false) {
  return numeral(price).format(dropDecimals ? '0,0' : '0,0.00');
}
}
