import { Component, OnInit } from '@angular/core';
import { NgbModal, ModalDismissReasons } from '@ng-bootstrap/ng-bootstrap';
import{FormBuilder,FormGroup,Validators} from '@angular/forms';
import { Router } from '@angular/router';
import{Product} from '../../models/product';
import {ServiceObj} from '../../models/service-data';
import{ProductService} from '../../services/product.service';
import{SalesService} from '../../services/sales.service';
import { ServiceDataService } from '../../services/service-data.service';
import { User } from '../../models/user';
import { Item } from '../../models/item';
import{Customer} from '../../models/customer';
import{CustomerService} from '../../services/customer.service';
import{Response} from '../../models/response';
import{Cart} from '../../models/cart';
import * as numeral from 'numeral';
import{debounceTime} from 'rxjs/operators'
import{ReportingService} from '../../services/reporting.service';
import { Report } from '../../models/report';
import { Country } from '../../models/country';
import { State } from '../../models/state';
import { Lga } from '../../models/lga';
import { CompanyService } from '../../services/company.service';

@Component({
  selector: 'app-makesale',
  templateUrl: './makesale.component.html',
  styleUrls: ['./makesale.component.scss','../../../vendor/libs/ng-select/ng-select.scss','../../../vendor/libs/angular-2-dropdown-multiselect/angular-2-dropdown-multiselect.scss']
})
export class MakesaleComponent implements OnInit {
  key:number
  discount=false;
  identifier:string;
  response:Response;
  newcustomer=false;
  currentitem:object;
  currentuser:User;
  customer:Customer;
  Cart:Item[]=[];
  carts:Cart[]=[];
  popular:Report[];
  total=0;
savedcartid:number;
  savedcart:Cart;
  customerform:FormGroup;
 filteredproducts:Product[];
 realproducts:Product[];
 services:ServiceObj[];
 displayeditems:object[];
 displayedcustomers:Customer[];
 searchform:FormGroup;
 searchcusform:FormGroup;
 customername="Guest"
 loading=false;
 errormsg=""
 error=false;
 create=false;
 cartloading=true;
  validationqty:boolean[]=[];
  addDetails:boolean=false;
  countries:Country[];
  states:State[];
  lga:Lga[];
  days:any = ["day"]
  months:any = ["month"]

  constructor(public router:Router,private reportservice:ReportingService,private modalService: NgbModal, private dataService: ServiceDataService,private salesservice:SalesService,private productservice:ProductService,private formbuilder:FormBuilder,private customerservice:CustomerService,private companyservice:CompanyService) {
    this.currentuser=<User> JSON.parse(localStorage.getItem("CurrentUser"));
    this.customerform= this.formbuilder.group({
      name:['',Validators.required],
      phoneNo:['',Validators.required],
      email:[''],
      companyID:[this.currentuser.companyID],
      birthday:[''],
      birthmonth:[''],
      birthdate:[''],
      billingAddress:this.formbuilder.group({
        street:[''],
        city:[''],
        country:[''],
        state:[''],
        lga:[''],
        lgaid:[],
        stateID:[],
        countryID:[]
      })
    })
   
  this.searchform=this.formbuilder.group({
    searchterm:null,
    productmode:[true]
  })

  this.searchcusform=this.formbuilder.group({
    searchterm:null,
  })

  for (let i=1; i<32; i++){
    this.days.push(i)
  }
  for (let i=1; i<13; i++){
  this.months.push(i)
  }
 
   }



 


  
  ngOnInit() {
    
    if(localStorage.getItem("CurrentCart")){
      localStorage.removeItem("CurrentCart")
    }else if(localStorage.getItem("CurrentCustomer")){
      localStorage.removeItem("CurrentCustomer")
    }
    this.loadpopitems();
    this.loadcountries()
  }
  loadpopitems(){
    this.reportservice.getpopularproduct(this.currentuser.companyID,this.currentuser.locationID).subscribe(data=>{
      console.log(data);
   this.popular=<Report[]> data;
   if(this.popular.length>6){
     this.popular=this.popular.slice(0,5);
   }
    },error=>{
  
    })
  }
  quickselect(item:Report){
   if(item.type==="p"){
    this.productservice.validateqty(this.currentuser.companyID,this.currentuser.locationID,item.psid,1).subscribe(res=>{
      this.validationqty.push((<Response> res).status);
      this.Cart.push(new Item(1,item.name,item.price,item.psid,"p"));
      this.calculatetotal();
    },error=>{
 
    })
    }else{
     this.Cart.push(new Item(1,item.name,item.price,item.psid,"s"));
     this.validationqty.push(true);
     this.calculatetotal();
   }
 
  }

  displayerror(){
    this.error=true;
    setTimeout(()=>this.error=false,20000);
  }
  ontogglechanged(){
    this.dataService.getAllServicesByCompanyID(this.currentuser.companyID).subscribe(data=>{
      this.displayeditems=data
    },error=>{
      
    })
  
  }
  formatPrice(price, dropDecimals = false) {
    return numeral(price).format(dropDecimals ? '0,0' : '0,0.00');
  }

  validateproductadd(pid,qty){
   this.productservice.validateqty(this.currentuser.companyID,this.currentuser.locationID,pid,qty).subscribe(res=>{
     this.validationqty.push((<Response> res).status);
     console.log(this.validationqty);
   
   },error=>{

   })
  }
  validateproductadjust(index,pid,qty){
    this.productservice.validateqty(this.currentuser.companyID,this.currentuser.locationID,pid,qty).subscribe(res=>{
      this.validationqty[index]=((<Response> res).status);
    
    },error=>{
 
    })

  }

  open(content, options = {}) {
    this.modalService.open(content, options).result.then((result) => {
      console.log(`Closed with: ${result}`);
    }, (reason) => {
      console.log(`Dismissed ${this.getDismissReason(reason)}`);
    });
  }

  private getDismissReason(reason: any): string {
    if (reason === ModalDismissReasons.ESC) {
      return 'by pressing ESC';
    } else if (reason === ModalDismissReasons.BACKDROP_CLICK) {
      return 'by clicking on a backdrop';
    } else {
      return  `with: ${reason}`;
    }
  }
  additem(){
  
   this.currentitem=this.searchform.controls.searchterm.value;
   if(this.currentitem){
     if(this.searchform.controls.productmode.value===true){
    let name=this.realproducts.filter(p=>(<Product>this.currentitem).id===p.id)[0].name;
    this.productservice.validateqty(this.currentuser.companyID,this.currentuser.locationID,(<Product>this.currentitem).id,1).subscribe(res=>{
      this.validationqty.push((<Response> res).status);
      console.log(this.validationqty);
      this.Cart.push(new Item(1,name,(<Product> this.currentitem).price.sellPrice,(<Product> this.currentitem).id,"p"));
      this.calculatetotal();
    },error=>{
 
    })  
      }else{
    this.validationqty.push(true);
     this.Cart.push(new Item(1,(<ServiceObj> this.currentitem).name,(<ServiceObj> this.currentitem).price,(<ServiceObj> this.currentitem).id,"s"));
     this.calculatetotal();
     
   }
  }
  this.searchform.controls.searchterm.reset();

  }
  calculatetotal(){
    this.total=0;
  for(let item of this.Cart){
    this.total=this.total+item.amount;
    
  }
  
  }
  onsearch(value:string){
    
    if(this.searchform.controls.productmode.value==true&&value.length!==0){
    this.productservice.searchproducts(this.currentuser.companyID,this.currentuser.locationID,value).subscribe(data=>{
      this.filteredproducts=<Product[]>data
      this.realproducts=<Product[]>JSON.parse(JSON.stringify(this.filteredproducts))
      this.filteredproducts.map((i)=>{i.name=i.name+"/"+ this.getdisplay(i)+i.code; return i})
      this.displayeditems=this.filteredproducts;
    
  
      
    },error=>{

    })
  }
  }
  getdisplay(i:Product){
    let display="";
    if(i.brand){
    display=display+ (<Product> i).brand+"/"
    }
    if(i.tags[0]!==null){
      for(let tag of i.tags){
         display=display+tag.name+":"+tag.description+"/"
      }
    }
return display
  }
  calculateamount(item:Item){
    item.amount=item.quantity*item.price;
    this.calculatetotal();
    if(item.type==="p"){
      var index = this.Cart.indexOf(item,0);
      this.validateproductadjust(index,item.psid,item.quantity);
    }
  }
 
  deleteitem(id:number){
    this.Cart.splice(id,1);
    this.validationqty.splice(id,1);
    this.calculatetotal()
  }
onadddiscount(){
  this.discount=!this.discount;
}
addnewcustomer(){
  this.newcustomer=!this.newcustomer;

}
oncheckout(){
  if(this.validationqty.includes(false,0)){
    this.errormsg="One or More Items are out of Stock! PLEASE ADJUST STOCK LEVELS BEFORE CHECKOUT"
    this.error=true;
 this.displayerror();
  }else{
 if(this.savedcartid){
   console.log("deleting");
   this.salesservice.deletecart(this.savedcartid).subscribe(res=>{

   },error=>{

   })
 }
  if(this.Cart.length>0){
  localStorage.setItem("CurrentCart",JSON.stringify(this.Cart));
  if(!this.customer){
   this.customerservice.getindividualcustomer(1).subscribe(data=>{
      this.customer=<Customer> data;
      localStorage.setItem("CurrentCustomer",JSON.stringify(this.customer));
      this.router.navigateByUrl('/layout/checkout');
    })
  }else{
  localStorage.setItem("CurrentCustomer",JSON.stringify(this.customer));
  this.router.navigateByUrl('/layout/checkout');
  }
  }
}
}
onsavecustomer(){
  this.customerform.value.billingAddress.countryID=this.customerform.value.billingAddress.country.id
  this.customerform.value.billingAddress.stateID=this.customerform.value.billingAddress.state.id
  this.customerform.value.billingAddress.lgaid=this.customerform.value.billingAddress.lga.id
  this.customerform.value.birthday=`${this.customerform.value.birthdate}/${this.customerform.value.birthmonth}`
  this.customerservice.savecustomer(<Customer>this.customerform.value).subscribe(data=>{
    console.log(data);
   this.response=<Response> data;
   this.customer= <Customer> this.customerform.value;
   this.customer.id=(<Customer>this.response.message).id;
   this.customername=this.customer.name;
   this.create=false;
  },error=>{
    this.errormsg=(<Response> error).statusText;
    this.displayerror();
    
  })
  
  
}
resetsale(){
  this.Cart=[];
  this.total=0;
  this.validationqty=[];
  this.customer=null;
  this.customername="Guest"
  if(this.savedcartid){
  this.savedcartid=null;
  }
}
searchcustomer(value:string){
  this.loading=true
  if(value.length>0){
  this.customerservice.getcustomer(this.currentuser.companyID,value).subscribe(data=>{
    this.displayedcustomers=<Customer[]>data;
    this.displayedcustomers.map((i)=>{i.name=i.name+"/"+ i.phoneNo+"/"+i.email; return i})
    this.loading=false;

    
  },error=>{
    this.loading=false;
    this.errormsg=(<Response> error).statusText;
    this.displayerror();
    
    

  })
}
}
onsave(){
  this.customer=<Customer>this.searchcusform.controls.searchterm.value
  let index=  (<Customer> this.customer).name.indexOf("/");
   this.customername=(<Customer> this.customer).name.substring(0,index);
   this.customer.name=this.customername;
 this.newcustomer=false;
}
onsavecart(){
  if(this.Cart.length>0){
  if(this.customer){
  this.savedcart= new Cart(this.Cart,this.customer.id,this.currentuser.companyID);
  }else{
    this.savedcart= new Cart(this.Cart,1,this.currentuser.companyID);
  }
  this.savedcart.code="p";
  this.savedcart.userCreated=this.currentuser.id;
  if(!this.savedcartid){
  this.salesservice.savecart(this.savedcart).subscribe(data=>{
   const response=data;
   console.log(response);
   this.loadcarts();
   this.resetsale();
  })
}else{
  console.log("yes");
  this.savedcart.id=this.savedcartid;
  this.salesservice.updatecart(this.savedcart).subscribe(res=>{
    console.log(res);
    this.loadcarts();
    this.resetsale();
  })
}
  }

}

loadcarts(){
  this.cartloading=true;
this.salesservice.getsavedcart(this.currentuser.companyID,this.currentuser.locationID,"p").subscribe(data=>{
  this.carts=<Cart[]>data;
 this.cartloading=false;

},error=>{

})
}
oncreate(){
  this.create=!this.create;
}

deletesavedcart(){
  if(this.savedcartid){
    this.salesservice.deletecart(this.savedcartid).subscribe(res=>{
  console.log(res);
  this.resetsale();
    },error=>{

    })
  }
}

 async selectcart(cart:Cart){
this.Cart=cart.items;
await this.checkstocks();
this.customer=cart.customer;
this.customername=cart.customer.name;
this.savedcartid=cart.id;
this.calculatetotal();
this.modalService.dismissAll("click");
this.validationqty=[];

}


checkstocks(){
  for(let item  of this.Cart){
   this.productservice.validateqty(this.currentuser.companyID,this.currentuser.locationID,item.psid,item.quantity).subscribe(res=>{
    this.validationqty.push((<Response> res).status);
   })
  }

}

addCustomerDetails(){
  this.addDetails = !this.addDetails
}

loadcountries(){
  this.companyservice.getcountries().subscribe(data=>{
    this.countries=<Country[]> data;
  },error=>{
    return;
  })
}

onchangec(){
  this.companyservice.getstates((<Country>this.customerform.controls.billingAddress.value.country).id).subscribe(data=>{
    this.states=<State[]> data;
  },error=>{
    return
  })
 }
 onchangest(){
   this.companyservice.getlga((<State>this.customerform.controls.billingAddress.value.state).id).subscribe(data=>{
     this.lga=<Lga[]> data;
   },error=>{
     return;
   })
 }

}
