import { Component, OnInit } from '@angular/core';
import {formatDate} from '@angular/common';
import { ReportingService } from '../../services/reporting.service';
import { ICustomerSale } from '../../models/customerSaleSummary';
import { map } from 'rxjs/operators';
import { Observable } from 'rxjs';
import { Location } from '../../models/location';
import { ICustomer } from '../../models/customerSaleSummary';
import * as numeral from 'numeral'
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { Salesregister } from '../../models/salesregister';
import { IPaymentSummary } from '../../models/PaymentSummary';
import {Formats} from '../../Classes/Formats';
import { ExcelService } from '../../services/excel.service';

@Component({
  selector: 'app-customer',
  templateUrl: './customer.component.html',
  styleUrls: ['./customer.component.scss']
})
export class CustomerComponent implements OnInit {
  private format = new Formats();
  APIDataIsLoading: boolean = true;
  modalContentIsLoading = true;
  locationLoaded: boolean = false;
  locations: Observable<Location[]>
  customers: Observable<ICustomer[]>
  user;
  selectedCustomerName: string = "";
  customerID: number = 0;
  customerObj: ICustomerSale[] = [];
  customerSales: Salesregister[] = [];
  customerPayments: IPaymentSummary[] = [];
  startDate;
   endDate;
  
  constructor(
    private reportService: ReportingService,
    private modalService: NgbModal,
    private excelService: ExcelService
  ) {
    this.user = JSON.parse(localStorage.getItem("CurrentUser"))
    this.reportService.getPaymentSaleSummaryByCustomer(this.user.companyID, this.customerID, 0,  "0", "0")
    .pipe(
      map(customers => {
        customers.forEach(customer=> {
          customer.amountLeft = customer.totalSales - customer.totalPayment;
        })
      this.customerObj = customers;
      this.APIDataIsLoading = false;
      this.loadCustomers();
      })
    ).subscribe();
    
   }

  ngOnInit() {
  }
  form

  search(){
      var startDate = this.startDate ===undefined  || this.startDate.trim() ==""?undefined: formatDate(this.startDate, 'dd-MM-yyyy', 'en');
      var endDate = this.endDate ===undefined  || this.endDate.trim() ==""?undefined: formatDate(this.endDate, 'dd-MM-yyyy', 'en');


      if((typeof startDate !== "undefined" || startDate !="") && (typeof endDate === "undefined" || endDate == "")){
        endDate = "0";
      }else if((typeof startDate === "undefined" || startDate == "") && (typeof endDate !== "undefined" || endDate != "")){
        startDate = "0";
      }else if((typeof startDate === "undefined" || startDate == "") &&  (typeof endDate === "undefined" || endDate == "")){
        startDate = "0", endDate = "0";
      }
  
      this.APIDataIsLoading = true;
    this.reportService.getPaymentSaleSummaryByCustomer(this.user.companyID, this.customerID, 0, startDate, endDate)
    .pipe(
      map(customers=> {
        customers.forEach(customer => {
          customer.amountLeft = customer.totalSales - customer.totalPayment;
        });
        this.customerObj = customers;
        this.APIDataIsLoading = false;
      })
    ).subscribe();
  }

  formatPrice(price, dropDecimals = false) {
    return numeral(price).format(dropDecimals ? '0,0' : '0,0.00');
  }

  loadCustomers(){
    this.customers  = this.reportService.getcustomers(this.user.companyID);
  }

  
  setCustomerID($id){
    this.customerID = $id;
  }

  calTotal(customers: ICustomerSale[]){
    let totals = {
      totalSale: 0,
      totalPayment: 0,
      totalAmountLeft: 0
    }
    customers.forEach(customer => {
      totals.totalSale = totals.totalSale+customer.totalSales;
      totals.totalPayment = totals.totalPayment + customer.totalPayment;
      totals.totalAmountLeft = totals.totalAmountLeft + (customer.totalSales-customer.totalPayment);
    });
    return totals;
  }

 
  resetTable(){

  }

  showTotalSalesDetails( content,customerName, customerID){
    this.customerSales = [];
    this.selectedCustomerName = customerName;
    this.modalContentIsLoading = true;
    this.modalService.open(content, { centered: true,  size: 'lg', backdrop: 'static' });
    this.reportService.getSaleByCustomer(this.user.companyID, customerID)
    .subscribe(customerSales => {
      this.customerSales = customerSales;
      this.modalContentIsLoading = false;
    })
    
  }

  showTotalPaymentDetails(paymentContent, customerName, customerID){
    this.modalContentIsLoading = true;
    this.refreshPaymentMethods();
    this.selectedCustomerName = customerName;
    this.reportService.getPaymentByCustomer(this.user.companyID, customerID)
    .pipe(
      map((payment: IPaymentSummary[])  => {
        this.groupByPaymentMethod(payment);
        this.customerPayments = payment;
        this.modalContentIsLoading = false;
      })
    ).subscribe();
    this.modalService.open(paymentContent, { centered: true,  size: 'lg', backdrop: 'static' });
  }
  printTable(){
    var divToPrint = document.getElementById('tableRecord');
    var htmlToPrint = '' +
        '<style type="text/css">' +
        'th, td {' +
        'padding:0.5em;' +
        'margin: none;'+
        'text-align: center;'+
        '}' +
        '}'+
          '#title {'+
          'font-size: 30px'+
          '}'+
        'table {'+
          'width: 100%;'+
          'border: 1px solid #000;'+
          '}'+
        '</style>';
    htmlToPrint = htmlToPrint+divToPrint.outerHTML;
    let newWin = window.open("");
    newWin.document.write(htmlToPrint);
    newWin.print();
    newWin.close();
  }

  formatdate(date){
    return formatDate(date,'d MMM yy','en'); 
  }

  //Payment methods
  paymentByCash: IPaymentSummary[] = [];
  paymentByCard: IPaymentSummary[] = [];
  paymentByTransfer: IPaymentSummary[] = [];
  paymentByCreditNote: IPaymentSummary[] = [];

  paymentSummary: IPaymentSummary[] = [];

  groupByPaymentMethod(paymentSummary: IPaymentSummary[]){
    paymentSummary.forEach(payment => {
      switch (payment.method){
        case 'cash':
          this.paymentByCash.push(payment)
          break;
        case 'card':
          this.paymentByCard.push(payment);
          break;
        case 'creditnote':
          this.paymentByCreditNote.push(payment);
          break;
        case 'transfer':
          this.paymentByTransfer.push(payment);
          break;
        default:
          console.log(payment);
          console.log("Not found");
      }
    })
  }

  getTotalPaymentsByPaymentMethod(paymentMethod: IPaymentSummary[]){
    let totalByPaymentMethod = 0;
    paymentMethod.forEach(payment => {
      totalByPaymentMethod = totalByPaymentMethod + payment.total;
    })

    return totalByPaymentMethod;
  }
  refreshPaymentMethods(){
    this.paymentByCard= [];
     this.paymentByCash= [];
     this.paymentByCreditNote=[];
      this.paymentByTransfer = [];
  }
  getGrandTotal(){
    let grandTotal = 0;
    this.customerPayments.forEach(payment => {
      grandTotal = grandTotal + payment.total;
    })
    return grandTotal;
  }

  downloadAsPDF(){
    this.format.saveAsPDF(this.user.company.name, 'customer-report', 'tableRecord')
  }
  exportASXLSX(){
    this.excelService.exportAsExcelFile(this.customerObj, `${this.user.company.name}-sales-report`);
  }

  
 
}
