import { Component, OnInit } from '@angular/core';
import { Salesregister } from '../models/salesregister';
import{Router} from '@angular/router';
import { SalesService } from '../services/sales.service';
import { MailingService } from '../services/mailing.service';
import * as numeral from 'numeral';
import { TaxService } from '../services/tax.service'
import {Formats} from '../Classes/Formats' ;

@Component({
  selector: 'app-recieptprint',
  templateUrl: './recieptprint.component.html',
  styleUrls: ['./recieptprint.component.scss']
})
export class RecieptprintComponent implements OnInit {
 printsale:Salesregister;
 print=false;
 salestotal:number=0;
  format = new Formats();
  cashierName;

  constructor(public router:Router,private messageservice:MailingService,private taxService:TaxService) { 
    this.printsale=<Salesregister> JSON.parse(localStorage.getItem("reciept"));
    this.calctotal();
    this.processTax()
    this.print=true;
   
  }

  ngOnInit() {
    
  }
  calctotal(){
 this.printsale.invoice.cart.items.map((i)=>{this.salestotal=this.salestotal+i.amount});
  }
  formatPrice(price, dropDecimals = false) {
    return numeral(price).format(dropDecimals ? '0,0' : '0,0.00');
  }
  onsend(){
 this.messageservice.sendreciept(this.printsale.id).subscribe(res=>{
   this.router.navigateByUrl('/layout/saleshistory');
 })
  }
  onprint(){

    var ua = navigator.userAgent.toLowerCase();
    var isAndroid = ua.indexOf("android") > -1 ;
    
    console.log(ua) ;
    
    if(isAndroid)
    {   
      //this.cart.forEach()
      var dataToPrint = this.prepareReceiptData();
      this.format.Print2RawBT(dataToPrint);
    }
    else
    {
      let printcontent=document.getElementById("reciept").innerHTML
      const win = window.open('');
      win.document.write(printcontent);
     win.focus();
      win.print();
      win.close();
     
    }

    this.router.navigateByUrl('/layout/saleshistory');
    
    
    
  }

  public companyTaxArray
  public salesTax
  public salesTaxExist
  public taxPayable
  public grandTotalAmount
  public amountpay
  public discountval
  public total
  public inclusiveText
  public taxName
  public userDetail = localStorage.getItem("CurrentUser")
  public companyID = JSON.parse(this.userDetail).company.id
  public amountWithTax
  public amountWithoutTax
  public taxIsInclusive
  public showTax = false;

  processTax(){  
    this.taxService.getAllCompanyTax(this.companyID).subscribe(data=>{
    this.companyTaxArray=data
    this.salesTax = this.companyTaxArray.find(tax=>tax.purpose=='Sales')

    this.salesTaxExist = this.salesTax == 'undefined'  ? true: false;
    
    if(this.salesTaxExist)  
    {
      this.showTax = true ;

      this.taxPayable = (this.salesTax.amount/100)*this.total

      if (this.salesTax.isInclusive==false){
       // this.grandTotalAmount = (this.amountpay) + this.taxPayable
       // this.amountpay = (this.amountpay-this.discountval) + this.taxPayable
       this.grandTotalAmount = (this.amountpay-this.discountval) + this.taxPayable
      this.amountWithTax = false
      this.amountWithoutTax = true
        this.inclusiveText = "excl"
      }
      else {
      
       // this.amountpay = this.amountpay-this.discountval
        this.inclusiveText = "incl"
        this.grandTotalAmount = this.amountpay-this.discountval
      this.amountWithTax = true
      this.amountWithoutTax = false
      }

    
   
    this.taxIsInclusive = this.printsale.taxInclusive ;
    

}
    
} );

this.cashierName=`${this.printsale.cashier.firstName} ${this.printsale.cashier.lastName}`
  
}

  

  public prepareReceiptData() : any
  {
    var LF = '%0A' ;
    var left = '%1b%61%0' ;
    var center = '%1b%61%1' ;
    var right = '%1b%61%2' ;
    var HT = '%09';
    var boldOn = '%1B%45%1'
    var boldOff = '%1B%45%0'

    var today = new Date().toLocaleString();
     
    var data ;
    data = boldOn + LF;
    data = data + '------------------------------------------------'+LF;

    // if(this.printsale.invoice.company.logo)
    // {
      
    //   var logo = this.printsale.invoice.company.logo

    //   data = data + logo + LF;

    // }

    data = data + this.printsale.invoice.company.name + LF;

    data = data + this.printsale.invoice.cashier.location.address.street + LF;
     data = data + this.printsale.invoice.company.phoneNo + LF;
    data = data + this.printsale.invoice.company.email + LF;
    //align left
   //data = data + left ;
    data = data +  '------------------------------------------------'+LF+LF;
    
    data = data + 'RECEIPT:' + this.printsale.invoice.invoiceNo  +LF;
    data = data  +'Customer: '+this.printsale.invoice.customer.name+  LF+LF;
    data = data  + 'Detail:  '+this.printsale.invoice.extData+ LF+LF;

    data = data+'Item         '+HT+'Price     '+HT+'QTY   '+'Amount'+LF;

     

    this.printsale.invoice.cart.items.forEach((item,index) =>
      {
        data = data + item.name + '         ';
        data = data + '' + this.formatPrice(item.price) + '      ';
        data = data + '' + item.quantity + '   '
       data = data + '' + this.formatPrice(item.amount) +LF;
       data = data +  '------------------------------------------------'+LF;
      }
    ); 
    data = data + '                Sub Total:    '+ this.formatPrice(this.printsale.totalAmount) +LF;

    if(this.salesTaxExist)
    {
      data = data + '                   TAX:   '+ this.formatPrice(this.printsale.tax) +LF;
    }
       
    data = data + '                Discount:    '+ this.formatPrice(this.printsale.discount) +LF;

    if(this.taxIsInclusive){
      data = data + '                Total:    '+ this.formatPrice((this.printsale.totalAmount-this.printsale.discount)) +LF;
    }
    else{
      data = data + '                Total:    '+ this.formatPrice((this.printsale.totalAmount-this.printsale.discount)+this.printsale.tax) +LF;
    }

    data = data + '                Total:    '+ this.formatPrice(this.grandTotalAmount - this.discountval) +LF;
   
    data = data +  '------------------------------------------------'+LF+LF;
   
    data = data+'PAYMENT    '+HT+'AMOUNT        '+'CHANGE'+LF;

    this.printsale.payments.forEach((payment,index) =>
    {
      data = data + payment.method + '        ';
      data = data+ payment.amount + '         ';
      data = data + payment.change + '    '+LF;
    
     data = data +  '------------------------------------------------'+LF;
    }
  ); 

  data = data + 'You were served by:' + this.cashierName  +LF;

  data = data + this.printsale.invoice.invoiceDate + LF;
  data = data + this.printsale.invoice.company.thankYouMessage +LF;
  data = data + this.printsale.invoice.company.refundPolicy +LF;

 data = data + LF + LF + LF;
    //data = data + today;

    console.log(data);
return data; 

}

}
