import { Injectable } from '@angular/core';
import{HttpClient, HttpHeaders} from '@angular/common/http';
import{AuthenticationService} from './authentication.service';
import { Location } from '../models/location';
import { Observable } from 'rxjs';
import { ILocationSummary } from '../models/LocationSummary';
import { IStaffSummary } from '../models/StaffSummary';
import { ITopEmployees } from '../models/topEmployees';
import { IProductSummary } from '../models/productSaleSummary';
import { ITopCustomer  } from '../models/topCustomers';
import { ICustomerSale, ICustomer } from '../models/customerSaleSummary';
import { Salesregister } from '../models/salesregister';
import { IBirthday } from '../models/stockControl';
import { IProfitLocation, IProfitCustomer, IProfitCashier } from '../models/Profit';
import { IProductSale } from '../models/ProductSummary';
import { IStockAdjust, IStock, IStockIssued } from '../models/Stocks';
import { IExpenseSummary } from '../models/expenseManagement';


@Injectable({
  providedIn: 'root'
})
export class ReportingService {

  constructor(private httpclient:HttpClient,private authservice:AuthenticationService) {
   }
   
   getdailysale(companyid:number){
    return this.httpclient.get(this.authservice.api+"Report/GetSaleSummary/"+companyid+"/0/0/0");   
  }
  getpopularproduct(cid:number,lid:number){
    return this.httpclient.get(this.authservice.api+"Report/GetProductSaleSummary/"+cid+"/"+lid);
  }

  getproductnumbers(companyid:number){
    return this.httpclient.get(this.authservice.api+"Report/GetStockSummary/"+companyid+"/0");
  }

  getcustomernumbers(companyid:number){
    return this.httpclient.get(this.authservice.api+"Report/GetCustomerSummary/"+companyid);
  }

getsalesummary(cid:number,lid:number,start:any,end:any){
  return this.httpclient.get(this.authservice.api+"Report/GetSaleSummary/"+cid+"/"+lid+"/"+start+"/"+end); 
}
getpaymentssummary(cid:number,lid:number,start:any,end:any){
  return this.httpclient.get(this.authservice.api+"Report/GetPaymentSummary/"+cid+"/"+lid+"/"+start+"/"+end);

}

//Newly added methods by Tosin

  getSalesSummaryByLocation(companyID:number, locationID: number, startDate: string, endDate: string):Observable<ILocationSummary[]> {
    return this.httpclient.get<ILocationSummary[]>(this.authservice.api+`Report/GetSalesSummaryByLocation/${companyID}/${locationID}/${startDate}/${endDate}`)
  }

  getSalesSummaryByStaff(companyID:number, userID: number, startDate: string, endDate: string): Observable<IStaffSummary[]> {
    return this.httpclient.get<IStaffSummary[]>(this.authservice.api+`Report/GetSalesSummaryByStaff/${companyID}/${userID}/${startDate}/${endDate}`)
  }

  getLocationsByCompanyID(companyID: number): Observable<Location[]>{
    return this.httpclient.get<Location[]>(this.authservice.api+`Location/GetByCompany/${companyID}`)
  }

  getTopEmployees(companyID: number, locationID: number, startDate: string, endDate: string):Observable<ITopEmployees[]>{
    return this.httpclient.get<ITopEmployees[]>(this.authservice.api+`Report/GetTopSellingStaff/${companyID}/${locationID}/${startDate}/${endDate}`)

  }
  getProductSaleSummary(companyID: number, locationID: number): Observable<IProductSummary[]>{
    return this.httpclient.get<IProductSummary[]>(this.authservice.api+`Report/GetProductSaleSummary/${companyID}/${locationID}`);

  }
  getTopCustomers(companyID: number, locationID: number, startDate: string, endDate: string): Observable<ITopCustomer[]>{
    return this.httpclient.get<ITopCustomer[]>(this.authservice.api+`Report/GetTopCustomers/${companyID}/${locationID}/${startDate}/${endDate}`);
  }

  getPaymentSummaryByCustomer(companyID: number, customerID, startDate: string, endDate){
    return this.httpclient.get(this.authservice.api+`Report/GetPaymentSummaryByCustomer/${companyID}/${customerID}/${startDate}/${endDate}`);
  }
  getPaymentSaleSummaryByCustomer(companyID: number, customerID: number, locationID: number, startDate: string, endDate: string): Observable<ICustomerSale[]>{
    return this.httpclient.get<ICustomerSale[]>(this.authservice.api+`Report/GetPaymentSaleSummaryByCustomer/${companyID}/${customerID}/${locationID}/${startDate}/${endDate}`);
  }
  getcustomers(id:number): Observable<ICustomer[]>{
    return this.httpclient.get<ICustomer[]>(this.authservice.api+"Customer/GetByCompany/"+id);
  }

  getSaleByCustomer(cid:number,cusid:number): Observable<Salesregister[]>{
    
    return this.httpclient.get<Salesregister[]>(this.authservice.api+`SaleRegister/GetByCompanyAndCustomer/${cid}/${cusid}`);
  }
  getPaymentByCustomer(companyID: number, customerID: number){
    return this.httpclient.get(this.authservice.api+`Report/GetPaymentSummaryByCustomer/${companyID}/${customerID}`);
  }

  //Getcustomer birthday
  getBirthday(companyID: number, type:string, value: number):Observable<IBirthday[]>{
    return this.httpclient.get<IBirthday[]>(this.authservice.api+`Report/GetCustomerBirthday/${companyID}/${type}/${value}`);
  }

  getAllCustomer(companyID: number): Observable<{name: string, id: number}[]>{
    return this.httpclient.get<{name: string, id: number}[]>(this.authservice.api+`Customer/GetByCompany/${companyID}`);
  }

  /**
   * Profit report APIs
   */

   getProfitByLocation(companyID: number, locationID: number, startDate: string, endDate: string): Observable<IProfitLocation[]>{
     return this.httpclient.get<IProfitLocation[]>(this.authservice.api+`Report/GetProfitSummaryByLocation/${companyID}/${locationID}/${startDate}/${endDate}`);
   }
   getProfitByCustomer(companyID: number, customerID: number, startDate: string, endDate: string): Observable<IProfitCustomer[]>{
    return this.httpclient.get<IProfitCustomer[]>(this.authservice.api+`Report/GetProfitSummaryByCustomer/${companyID}/${customerID}/${startDate}/${endDate}`);
  }

  getProfitByCashier(companyID: number, cashierID: number, startDate: string, endDate: string): Observable<IProfitCashier[]>{
    return this.httpclient.get<IProfitCashier[]>(this.authservice.api+`Report/GetProfitSummaryByStaff/${companyID}/${cashierID}/${startDate}/${endDate}`);
  }

  getProductSummary(companyID: number, locationID: number, startDate: string, endDate: string): Observable<IProductSale[]>{
    return this.httpclient.get<IProductSale[]>(this.authservice.api+`Report/GetAllProductSaleSummary/${companyID}/${locationID}/${startDate}/${endDate}`);
  }

  getAdjustSummary(companyID: number, locationID: number, startDate: string, endDate: string): Observable<IStockAdjust[]>{
    return this.httpclient.get<IStockAdjust[]>(this.authservice.api+`Report/GetStockAdjustmentReport/${companyID}/${locationID}/${startDate}/${endDate}`);
  }
  getIssueSummary(companyID: number, locationID: number, startDate: string, endDate: string): Observable<IStockIssued[]>{
    return this.httpclient.get<IStockIssued[]>(this.authservice.api+`Report/GetStockIssueReport/${companyID}/${locationID}/${startDate}/${endDate}`);
  }

  getExpenseSummary(companyID: number, locationID: number, startDate: string, endDate: string): Observable<IExpenseSummary[]>{
    return this.httpclient.get<IExpenseSummary[]>(this.authservice.api+`Report/GetExpenseSummaryByLocation/${companyID}/${locationID}/${startDate}/${endDate}`);
  }

}

