import { Component, OnInit, AfterViewChecked, ChangeDetectorRef } from '@angular/core';
import { NgbModal, ModalDismissReasons } from '@ng-bootstrap/ng-bootstrap';
import { Router, ActivatedRoute } from '@angular/router';
import { Vendor } from '../../../models/vendor';
import { VendorService } from '../../../services/vendor.service';
import { User } from '../../../models/user';

@Component({
  selector: 'app-vendors',
  templateUrl: './vendors.component.html',
  styleUrls: ['./vendors.component.scss',
  '../../../../vendor/styles/pages/contacts.scss']
})
export class VendorsComponent implements OnInit {
  vendors:Vendor[]
  currentuser:User
  choosenvendor:Vendor
  filterval=""
  filteredvendors:Vendor[]
  show=false

  constructor(private vendorservice:VendorService, public modalService: NgbModal, public router:Router, private route:ActivatedRoute, private cdr:ChangeDetectorRef) { 
    this.currentuser=<User> JSON.parse(localStorage.getItem("CurrentUser"))
  }

  ngOnInit() {
    this.loaddata()
  }

  viewMode = 'col'

  loaddata(){
    this.vendorservice.getvendors(this.currentuser.companyID).subscribe(data=>{
      this.vendors=<Vendor[]>data
      this.filteredvendors=<Vendor[]>JSON.parse(JSON.stringify(this.vendors))
    })
  }

  open(content, options = {}) {
    this.modalService.open(content, options).result.then((result)=>{
      console.log(`Closed with: ${result}`)
    }, (reason)=>{
      console.log(`Dismissed ${this.getDismissReason(reason)}`)
    })
  }

  private getDismissReason(reason: any): string {
    if (reason === ModalDismissReasons.ESC) {
      return 'by pressing ESC';
    } else if (reason === ModalDismissReasons.BACKDROP_CLICK) {
      return 'by clicking on a backdrop';
    } else {
      return  `with: ${reason}`;
    }
  }
  get modalservice(){
    return this.modalService;
  }

  filtervendors(){
    if(this.filterval.length===0){
      this.filteredvendors=<Vendor[]>JSON.parse(JSON.stringify(this.vendors))
    }
    this.filteredvendors=this.vendors.filter(ven=>ven.name.toLowerCase().includes(this.filterval.toLowerCase()))
  }

}
