import { Product } from "./product";
import { Location } from "./location";

export class Stock {
    productID:number;
    locationID:number;
    purchaseDate:string;
    reference:string;
    priceID:number;
    product:Product;
    location:Location;
    quantity:number;
    reason:string;
    id:number;
    userModified:number;
    companyID:number;
    constructor(pid:number,lid:number,prid:number,quan:number,companyid:number){
        this.productID=pid;
        this.locationID=lid;
        this.priceID=prid;
        this.quantity=quan;
        this.companyID=companyid;

    }
}
