import { Component, OnInit } from '@angular/core';
import * as numeral from 'numeral';
import{ActivatedRoute} from '@angular/router';
import{Location} from '@angular/common';
import{User} from '../../models/user';
import{Priviledge} from '../../models/priviledge';
import{UserService} from '../../services/user.service';


@Component({
  selector: 'app-viewstaff',
  templateUrl: './viewstaff.component.html',
  styleUrls: ['./viewstaff.component.scss','../../../vendor/styles/pages/users.scss']
})
export class ViewstaffComponent implements OnInit {

  response:object;
  user:User;

  
  constructor(private route:ActivatedRoute, private location:Location,private userservice:UserService) {
    this.user=new User();
    this.getuser();

   }

  ngOnInit() {
  
  }
  formatInt (v) {
    return numeral(v).format('0,0');
  }
getuser(){
  const id=+this.route.snapshot.paramMap.get('id');
   this.userservice.getUser(id).subscribe(userservice=>{
     this.response=userservice;
     this.user=<User> this.response;
   },error=>{

   })
}
}
