import { Component, OnInit } from '@angular/core';
import{FormBuilder,FormGroup,Validators} from '@angular/forms';
import{MailingService} from '../../services/mailing.service';
import { User } from '../../models/user';
@Component({
  selector: 'app-quickemail',
  templateUrl: './quickemail.component.html',
  styleUrls: ['./quickemail.component.scss']
})
export class QuickemailComponent implements OnInit {
 
  mailform:FormGroup;
  to=[];
  currentuser:User;
  constructor(private formbuilder:FormBuilder,private mailingservice:MailingService) {
    this.currentuser=<User> JSON.parse(localStorage.getItem("CurrentUser"));
    this.mailform=this.formbuilder.group({
      to:['',Validators.required],
      subject:['',Validators.required],
       message:['',Validators.required]
    })
   }

  ngOnInit() {
  }
  sendmail(){
    this.to.push(this.mailform.controls.to.value);
    var value= {"to":this.to,"subject":this.mailform.controls.subject.value,"message":this.mailform.controls.message.value,"from":this.currentuser.company.email,"senderName":this.currentuser.firstName}
    this.mailingservice.sendmail(value).subscribe(res=>{
      console.log(res);
      this.mailform.reset();
    })
  }


}
