import { Component, OnInit } from '@angular/core';
import{AuthenticationService} from '../services/authentication.service';
import{User} from '../models/user';
import{Address} from '../models/address';
import{Location} from '../models/location';
import{Company} from '../models/company';
import{FormBuilder,FormGroup,Validators,FormControl} from '@angular/forms';
import{CompanyService} from '../services/company.service';
import{Sector} from '../models/sector';
import { Roles } from '../models/roles';
import { Priviledge } from '../models/priviledge';
import{Response} from '../models/response';
import{UserService} from '../services/user.service';
import { Router } from '@angular/router';
import { Country } from '../models/country';
import { State } from '../models/state';
import { Lga } from '../models/lga';


@Component({
  selector: 'app-welcome',
  templateUrl: './welcome.component.html',
  styleUrls: ['./welcome.component.scss','../../vendor/libs/ng-select/ng-select.scss','../../vendor/libs/angular-2-dropdown-multiselect/angular-2-dropdown-multiselect.scss']
})
export class WelcomeComponent implements OnInit {
  companysetupform:FormGroup;
  companylocationform:FormGroup;
  currentuser:User;
  companyset=false;
  locationset=false;
  submit=true;
  response:object;
  invalid=false;
  address:Address;
  location:Location;
  sectors:Sector[];
  role:Roles;
  countries:Country[];
  states:State[];
  lga:Lga[];
  logo:File;

 
  module=[{"name":"Sales","code":"Sales","read":true,"write":true},{"name":"Inventory","code":"invent","read":true,"write":true},{"name":"Admin","code":"admin","read":true,"write":true}
  ,{"name":"Reports","code":"report","read":true,"write":true}];

  constructor(private formbuilder:FormBuilder,public router:Router, private authservice:AuthenticationService, private companyservice:CompanyService,private userservice:UserService) { 
    this.companysetupform=this.formbuilder.group({
      companyname:['',Validators.required],
      companyarea:[,Validators.required],
      thankyoumg:['Thanks for Shopping with us'],
      refundpolicy:['No refund for goods bought in good condition'],
      websiteurl:[''],
      phone:['',Validators.required],
      currency:['',Validators.required],
      eazyshop:[false],
      logo:['']

    })
    this.companylocationform=this.formbuilder.group({
     address1:['',Validators.required],
     address2:[''],
     city:[''],
     country:[,Validators.required],
     state:[,Validators.required],
     lga:[,Validators.required]
    })
  }

  ngOnInit() {
    this.currentuser= <User> JSON.parse(localStorage.getItem("CurrentUser"));
    this.loadsectors();
    this.loadcountries();
  }
  loadsectors(){
    this.companyservice.getcompanysectors().subscribe(data=>{
      this.sectors=<Sector[]> data;
    })
  }
  loadcountries(){
    this.companyservice.getcountries().subscribe(data=>{
      this.countries=<Country[]> data;
    })
  }
  onchange(){
   this.companyservice.getstates((<Country>this.companylocationform.controls.country.value).id).subscribe(data=>{
     this.states=<State[]> data;
   })
  }
  onchange2(){
    this.companyservice.getlga((<State>this.companylocationform.controls.state.value).id).subscribe(data=>{
      this.lga=<Lga[]> data;
    })
  }

  onfilechanged(event){
   this.logo = event.target.files[0];
   var reader= new FileReader();
    reader.readAsDataURL(this.logo)
  reader.onload=(event)=>{
    var result = reader.result;
    var data= {"id":this.currentuser.companyID,"logo":result}
    this.companyservice.updatecompany(<Company> data).subscribe(res=>{
      console.log(res)
    })
  }
  }
  onupdatecompany(){

    if(this.companysetupform.invalid){
     this.invalid=true;
     return;
    }else{
      this.currentuser.company.name=this.companysetupform.controls.companyname.value;
      this.currentuser.company.sectorID=(<Sector>this.companysetupform.controls.companyarea.value).id;
      this.currentuser.company.thankYouMessage=this.companysetupform.controls.thankyoumg.value;
      this.currentuser.company.url=this.companysetupform.controls.websiteurl.value;
      this.currentuser.company.phoneNo=this.companysetupform.controls.phone.value;
      this.currentuser.company.refundPolicy=this.companysetupform.controls.refundpolicy.value;
      this.currentuser.company.currency=this.companysetupform.controls.currency.value;
      this.currentuser.company.isEazyShoppingEnabled=this.companysetupform.controls.eazyshop.value;

    this.companyservice.updatecompany(this.currentuser.company).subscribe(companyservice=>{
     this.response=companyservice;
     console.log(this.response);
     this.companyset=true;
     if(this.locationset&&this.companyset){
      this.submit=false;
    }
      },error=>{
        console.log("failed");
      })


    }
  }

  oncreatelocation(){
    if(this.companylocationform.invalid){
      this.invalid=true;
      return;
     }else{
    this.address=new Address(this.companylocationform.controls.address1.value + this.companylocationform.controls.address2.value,this.companylocationform.controls.city.value,(<Lga>this.companylocationform.controls.lga.value).id,(<State>this.companylocationform.controls.state.value).id,(<Country>this.companylocationform.controls.country.value).id);
    this.location=new Location("HQ",this.currentuser.company.phoneNo,this.address,this.currentuser.companyID);
    console.log(this.location);
     this.companyservice.createcompanylocation(this.location).subscribe(companyservice=>{
       console.log(companyservice);
      this.currentuser.locationID=(<Location>(<Response> companyservice).message).id;
      this.locationset=true;
      if(this.locationset&&this.companyset){
        this.submit=false;
      }
     },error=>{
       console.log("failed");
     })
     }
  }
  onsubmit(){
 this.role= new Roles("Super Admin","superadmin",<Priviledge[]> this.module,this.currentuser.companyID);
 this.userservice.createuserrole(this.role).subscribe(res=>{
   this.currentuser.roleID=(<Roles>(<Response> res).message).id;
   this.userservice.updateuser(this.currentuser).subscribe(res=>{
     console.log(res)
     this.userservice.getUser(this.currentuser.id).subscribe(data=>{
       this.currentuser=<User> data;
       localStorage.setItem("CurrentUser",JSON.stringify(this.currentuser));
       this.router.navigateByUrl("/layout")
     },error=>{

     })
   },error=>{

   })
 },error=>{

 })
 

  }
}
 
