import { Component, OnInit } from '@angular/core';
import{ReportingService} from '../../services/reporting.service'
import { Report } from '../../models/report';
import { User } from '../../models/user';
import { formatDate } from '@angular/common';
import{CompanyService} from '../../services/company.service';
import { NumberCardComponent } from '@swimlane/ngx-charts';
import { Location } from '../../models/location';

@Component({
  selector: 'app-salescharts',
  templateUrl: './salescharts.component.html',
  styleUrls: ['./salescharts.component.scss']
})
export class SaleschartsComponent implements OnInit {
  today:Date=new Date()
  weeklysales:Report[];
  salesbystore:Report[];
  locations:Location[];
  currentuser:User;
  loaded=false;
  loaded2=false;
  dayweek:number;
  t:number;
  yt:number;

  constructor(private reportservice:ReportingService,private companyservice:CompanyService) {
    this.currentuser=<User> JSON.parse(localStorage.getItem("CurrentUser"))
  }
  colors = {
    domain: ['#d0a6e0','#89a3d8','#9bb3e5','#b29be5',
'#f0b27a',
'#ebf5fb']
  };

salesdata=[];
storedata=[];
  singleData = [{
    name: 'Mon',
    value: 0
  }
  ];
  ngOnInit() {
  this.getlocations();
   this.gettodaysale();


  }
  getlocations(){
    this.companyservice.getcompanylocations(this.currentuser.companyID).subscribe(data=>{
 this.locations=<Location[]>data;
 for(let l of this.locations){
this.storedata.push({name:l.name,value:0})
 }
    },error=>{

    })
  }
    gettodaysale(){
    this.reportservice.getdailysale(this.currentuser.companyID).subscribe( data=>{
      this.salesdata.push({name:"Today",value:this.calculatetotal(<Report[]>data)})
     
      this.dayweek=this.today.getDay()-1;
      if(this.dayweek<0){
        this.dayweek=6;
      }
       this.t=Date.now()-86400000;
      this.yt=Date.now();
       this.loadweeklysales();
      
    })
  }

   loadweeklysales(){

        if(this.dayweek===0){
          this.dayweek=7;
        }
       var day = this.getday(this.dayweek);
       this.reportservice.getsalesummary(this.currentuser.companyID,0,formatDate(this.t,'dd-MM-yyyy','en'),formatDate(this.t,'dd-MM-yyyy','en')).subscribe(async data=>{
         this.salesdata.push({name:day,value:this.calculatetotal(<Report[]>data)});
        if(this.salesdata.length===7){
          this.loaded=true;
          this.loadstoresales();
        }else{
          this.t=this.t-86400000;
          this.yt=this.yt-86400000;
          this.dayweek=this.dayweek-1;
          this.loadweeklysales()
        }
      },error=>{
      return
     }) 
  }

  loadstoresales(){
    this.yt=Date.now();
    console.log(formatDate(this.t,'dd-MM-yyyy','en'));
    console.log(formatDate(this.yt,'dd-MM-yyyy','en'));
    this.reportservice.getsalesummary(this.currentuser.companyID,0,formatDate(this.t,'dd-MM-yyyy','en'),0).subscribe(data=>{
      this.salesbystore=<Report[]>data;
      for(let item of this.storedata){
         var total=0;
         if((this.salesbystore.filter(sale=>sale.locationName===item.name).length>0)){
             total=this.salesbystore.filter(sale=>sale.locationName===item.name)[0].vol
             item.value=total;
         }
      }
      this.loaded2=true;

    })
    

  }

  
  getday(day:number):string{
  if(day===1){
    return "Mon"
  }else if(day===2){
    return "Tue"
  }else if(day===3){
    return "Wed"
  }else if(day===4){
    return "Thurs"
  }else if(day===5){
    return "Frid"
  }else if(day===6){
    return "Sat"
  }else if(day===7){
    return "Sun"
  }
  }
  calculatetotal(data:Report[]){
  var total=0;
  data.map((d)=>{total=total+d.val})
  return total;
  }

}
