import { KitchenItem } from "./kitchenItem";
import { Customer } from "./customer";
import { Cashier } from "./cashier";

export class KitchenCart {
    cartID:number;
    amount:number;
    isPaid:boolean;
    customerID:number;
    //items
    cart={items:[]};
    id:number;
    companyID:number;
    userCreated:number;
    customer:Customer;
    extData:string;
    status:string;
    priority:string;
    type:string;
    cashier={
        id:1,
        locationID:1,
        userCreated:1,
        companyID:1
    }
    company;
    tax:number;
    taxPercent:number;
    taxName:string;
    taxInclusive:boolean;
    discount:number;
    code:string;



constructor(
    kitchenItems:KitchenItem[],
    customerid:number,
    companyid:number,
    extData:string,
    status:string,
    priority:string,
    type:string,
    userCreated:number,
    id:number){
 //this.items=kitchenItems;
 this.customerID=customerid;
 this.companyID=companyid;
 this.extData=extData,
 this.status=status,
 this.priority=priority,
 this.type=type,
 this.userCreated=userCreated,
 this.id=id
 this.cart.items=kitchenItems
 
}

}



