import { Injectable } from '@angular/core';
import{HttpClient, HttpHeaders} from '@angular/common/http';
import{Company} from '../models/company';
import{Location} from '../models/location';
import{AuthenticationService} from './authentication.service'
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class CompanyService {
  constructor(private httpclient:HttpClient,private authservice:AuthenticationService) { }
 updatecompany(company:Company){
  return(this.httpclient.post(this.authservice.api+"Company/update",company));
 }

  createcompanylocation(location:Location){
    return(this.httpclient.post(this.authservice.api+"Location/save",location));

  }
  getcompany(id:number){
    return(this.httpclient.get(this.authservice.api+"Company/"+ id));
  }
  getcompanylocations(id:number){
    return(this.httpclient.get(this.authservice.api+"Location/GetByCompany/"+id));

  }
  getcompanylocation(id:number){
    return(this.httpclient.get(this.authservice.api+"Location/"+id));
  }
  getcountries(){
 return (this.httpclient.get(this.authservice.api+"Country/Get"));
  }
  getstates(id:number){
    return (this.httpclient.get(this.authservice.api+"State/Get/"+id));
  }
  getlga(id:number){
    return (this.httpclient.get(this.authservice.api+"LGA/Get/"+id));
  }
  getcompanysectors(){
    return (this.httpclient.get(this.authservice.api+"Sector/GetAll"));
  }


}
