import { Item } from "./item";
import { Customer } from "./customer";

export class Cart {
    code:string;
    customerID:number;
    items:Item[];
    id:number;
    companyID:number;
    userCreated:number;
    customer:Customer;


constructor(items:Item[],customerid:number,companyid:number){
 this.items=items;
 this.customerID=customerid;
 this.companyID=companyid;
}

}
