import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { IExpense, IExpenseCategory } from '../models/expenseManagement';
import { Observable } from 'rxjs';
import{AuthenticationService} from './authentication.service'

@Injectable({
  providedIn: 'root'
})
export class ExpenseManagementService {

  constructor(
    private httpClient: HttpClient,
    private authservice:AuthenticationService
  ) {

   }

   //To save an expense
   saveExpense(newExpense: IExpense): Observable<IExpense>{
    return this.httpClient.post<IExpense>(this.authservice.api+`Expenses/save`, newExpense);
   }

   //get expense by company

   getExpenseByCompany(companyID: number): Observable<IExpense[]>{
    return this.httpClient.get<IExpense[]>(this.authservice.api+`Expenses/GetByCompany/${companyID}`)
   }

   //To get expense
   getExpense(expenseID: number): Observable<IExpense>{
     return this.httpClient.get<IExpense>(this.authservice.api+`Expenses/${expenseID}`)
   }

   //To update an expense
   updateExpense(expense: IExpense):Observable<any>{
     return this.httpClient.post<any>(this.authservice.api+`Expenses/update`, expense)
   }

    //To delete an expense
    deleteExpense(companyID: number, id: number, userID: number): Observable<any>{
    return this.httpClient.post<any>(this.authservice.api+`Expenses/delete`, {companyID:companyID, id:id, userID:userID});
    }


    //Expense category
    saveExpenseCategory(newCategory: IExpenseCategory): Observable<IExpenseCategory>{
      return this.httpClient.post<IExpenseCategory>(this.authservice.api+`ExpenseCategory/save`, newCategory);
     }

     //getAll expense category by companyID
     getExpenseCategoryByCompanyID(companyID:number): Observable<IExpenseCategory[]>{
      return this.httpClient.get<IExpenseCategory[]>(this.authservice.api+`ExpenseCategory/GetByCompany/${companyID}`);
     }

     //delete an expense category
     deleteExpenseCategory(companyID: number, categoryID: number): Observable<IExpenseCategory>{
      return this.httpClient.post<IExpenseCategory>(this.authservice.api+`ExpenseCategory/delete`, {companyID: companyID, id: categoryID});
     }

     //Update an expense category
     updateExpenseCategory(expenseCategory): Observable<IExpenseCategory>{
       return this.httpClient.post<IExpenseCategory>(this.authservice.api+`ExpenseCategory/update`, expenseCategory);
     }



}
