import { Component, OnInit } from '@angular/core';
import { SalesService } from '../../services/sales.service'
import { User } from '../../models/user';
import { interval } from 'rxjs'

@Component({
  selector: 'app-livetickets',
  templateUrl: './livetickets.component.html',
  styleUrls: ['./livetickets.component.scss']
})
export class LiveticketsComponent implements OnInit {
  displayTickets=false
  displayPos=true
  liveTickets
  liveTickett
  liveTicket
  ticketloading=true
  selectToPrint=[[false]]
  graphicView=true
  tabularView=false
  currentuser

  constructor(private salesservice:SalesService) {
    this.currentuser=<User> JSON.parse(localStorage.getItem("CurrentUser"))
   }

  ngOnInit() {
    this.displayLiveTickets()
    //this.getLiveTickets
  }

  tabularTicket(){
    this.tabularView=true
    this.graphicView=false
  }
  graphicTicket(){
    this.tabularView=false
    this.graphicView=true
  }
  displayLiveTickets(){
    this.displayTickets=true
    this.displayPos=false
    this.salesservice.getktichencartbycompanyidanddate(this.currentuser.companyID).subscribe(data=>{
     this.liveTicket=data
     this.liveTickets=this.liveTicket.filter(x=>x.customer)
     this.ticketloading=false
   })
  }
  test(){
    console.log("yes")
  }
  
  //getLiveTickets=setInterval(()=>{this.displayLiveTickets()},60000)
}
