import { Component, OnInit } from '@angular/core';
import { Invoice } from '../models/invoice';
import { User } from '../models/user';
import { formatDate } from '@angular/common';
import {MailingService} from '../services/mailing.service';
import{Router} from '@angular/router';
import * as numeral from 'numeral';

@Component({
  selector: 'app-invoicing',
  templateUrl: './invoicing.component.html',
  styleUrls: ['./invoicing.component.scss']
})
export class InvoicingComponent implements OnInit {

  invoice:Invoice;
  currentuser:User
  date:any
  total=0;
  istax=false;

  constructor(private mailingservice:MailingService,private router:Router) {
    this.invoice=<Invoice>JSON.parse(localStorage.getItem("Invoice"));
    this.currentuser=<User>JSON.parse(localStorage.getItem("CurrentUser"));
    this.date=formatDate(this.invoice.invoiceDate,'d MMM yy HH:mm','en');
    if(this.invoice.tax===0){
      this.istax=false;
    }else{
      this.istax=true;
    }
  
   }

  ngOnInit() {
    this.calculatetotal()
  }

  calculatetotal(){
    for(let item of this.invoice.cart.items){
      this.total=this.total+item.amount;
    }
  }
  sendinvoice(){
    this.mailingservice.sendinvoice(this.invoice.id).subscribe(response=>{
      console.log(response);
      this.router.navigateByUrl('/layout')
    })

  }
   onprint(){
    let printcontent=document.getElementById("invoice").innerHTML
    const win = window.open('');
    win.document.write(printcontent);
   win.focus();
    win.print();
    win.close();
    this.router.navigateByUrl('/layout')
   }
   
   formatPrice(price, dropDecimals = false) {
    return numeral(price).format(dropDecimals ? '0,0' : '0,0.00');
  }

  backtopos(){
    if(this.currentuser.company.appSector==1){
      this.router.navigateByUrl('/layout/makesalerestaurant');
    }else{
      this.router.navigateByUrl('/layout/makesale');
    }
  }

}
