import { Component, OnInit } from '@angular/core';
import { Customer } from '../../models/customer';
import { CustomerService } from '../../services/customer.service';
import{CustomersComponent} from '../customers/customers.component'
import { Country } from '../../models/country';
import { State } from '../../models/state';
import { Lga } from '../../models/lga';
import { CompanyService } from '../../services/company.service'
import { FormGroup, FormBuilder, Validators } from '../../../../node_modules/@angular/forms';
import { User } from '../../models/user';

@Component({
  selector: 'app-editcustomer',
  templateUrl: './editcustomer.component.html',
  styleUrls: ['./editcustomer.component.scss',
  '../../../vendor/libs/ng-select/ng-select.scss','../../../vendor/libs/angular-2-dropdown-multiselect/angular-2-dropdown-multiselect.scss']
})
export class EditcustomerComponent implements OnInit {
 customer:Customer;
 loading=false;
 countries:Country[];
 states:State[];
 lga:Lga[];
 currentuser: User;
 addcustomerform:FormGroup
 public days=[]
 public months=[]
 public day
 public month
  
  constructor(private customerservice:CustomerService,private cuscomp:CustomersComponent, private companyservice:CompanyService, private formbuilder:FormBuilder) {
    this.customer=this.cuscomp.choosencustomer;
    this.currentuser=<User> JSON.parse(localStorage.getItem("CurrentUser"))

    this.addcustomerform= this.formbuilder.group({
      name:['',Validators.required],
      phoneNo:['',Validators.required],
      email:[''],
      companyID:[this.currentuser.companyID],
      birthdate:[''],
      birthmonth:[''],
      birthday:[],
      billingAddress:this.formbuilder.group({
        street:[''],
        city:[''],
        country:[''],
        state:[''],
        lga:[''],
        lgaid:[],
        stateID:[],
        countryID:[]
      }),
    })

    if (this.customer.birthday){
      let dob = this.customer.birthday.split("/")
      this.day = dob[0]
      this.month= dob[1]
      for (let i=1; i<32; i++){
        this.days.push(i)
      }
      for (let i=1; i<13; i++){
        this.months.push(i)
      }
    }
    else {
      for (let i=1; i<32; i++){
        this.days.push(i)
      }
      for (let i=1; i<13; i++){
        this.months.push(i)
      }
    }
    if(this.customer.billingAddress == null || this.customer.billingAddress.state == null || this.customer.billingAddress.country == null || this.customer.billingAddress.lga == null){
      this.customer.billingAddress={
        street:"",
        city:"",
        country:{name:""},
        state:{name:""},
        lga:{name:""}
      }
    }
    else{
      
    }
    

   }
   onclick(){
    console.log(this.cuscomp.modalService.dismissAll("click"));
    }
    onsave(){
      if(this.day && this.month){
      this.customer.birthday=`${this.day}/${this.month}`
      }
      else{
        this.customer.birthday=""
      }
      this.loading=true;
      this.customer.billingAddress.stateID=this.selectedStateID
      this.customer.billingAddress.countryID=this.selectedCountryID
      this.customer.billingAddress.lgaid=this.selectedlgaid
      console.log(this.customer)
    this.customerservice.updatecustomer(this.customer ).subscribe(res=>{
      this.loading=false;
      this.onclick();
      this.cuscomp.loaddata();
    })

    
    }
    selectedCountryID
    selectedStateID
    selectedlgaid

    loadcountries(){
      this.companyservice.getcountries().subscribe(data=>{
        this.countries=<Country[]> data;
      },error=>{
        return;
      })
    }
  
    onchangec(chosencountry){
      this.selectedCountryID=chosencountry.id
      this.companyservice.getstates(this.selectedCountryID).subscribe(data=>{
        this.states=<State[]> data;
      },error=>{
        return
      })
     }
     onchangest(chosenstate){
       this.selectedStateID=chosenstate.id
       this.companyservice.getlga(this.selectedStateID).subscribe(data=>{
         this.lga=<Lga[]> data;
       },error=>{
         return;
       })
     }
     onchangel(chosenlga){
      this.selectedlgaid=chosenlga.id
    }

  ngOnInit() {
    this.loadcountries()
  }

}
