import { Component, OnInit } from '@angular/core';
import{ActivatedRoute,Router} from '@angular/router';
import { Product } from '../../models/product';
import{FormBuilder,FormGroup,Validators, NgModel} from '@angular/forms';
import {ProductService} from '../../services/product.service';
import{StockService} from '../../services/stock.service';
import { User } from '../../models/user';
import { Stock } from '../../models/stock';
import{Location} from '../../models/location';
import{CompanyService} from '../../services/company.service'
import { Category } from '../../models/category';
import { Subcategory } from '../../models/subcategory';
import { Supplier } from '../../models/supplier';
import { map } from 'rxjs/operators';
import { UserService } from '../../services/user.service';

@Component({
  selector: 'app-viewproduct',
  templateUrl: './viewproduct.component.html',
  styleUrls: ['./viewproduct.component.scss','../../../vendor/libs/ng-select/ng-select.scss','../../../vendor/libs/angular-2-dropdown-multiselect/angular-2-dropdown-multiselect.scss']
})
export class ViewproductComponent implements OnInit {
  product:Product;
  currentuser:User;
  stock:Stock[];
  locations:Location[];
  quantity:number;
  location:Location;
  stockitem:Stock;
  Categories:Category[];
  subcategories:Subcategory[]=[];
  editproductform:FormGroup;
  suppliers:Supplier[];
  loading=false;
  showcode=true;
  showprice=true;
  //variables by tosin for the last tab in the view
  updatedQuantity: number = 0;
  productID: number;
  stockID: number;
  locationsByStocks: Location[]=[];
  stockLocation;
  stocks: Stock[];
  stockQty: number;
   operator = "+";
   reason: string = "";
   isLoading: boolean = false;
   staffs: User[];
   selectedObj: User | Location;
   issuedID: number;
   issueStockReason: string = "";
   issuedQuantity: number = 0;
   issueType: string = "user";
   issueBy: Location[] | User[];
   issueByLocation: boolean = false;
   

  constructor(private route:ActivatedRoute,private router:Router,private productservice:ProductService,private stockservice:StockService,private companyservice:CompanyService, private userService: UserService,private formbuilder:FormBuilder) {
    this.currentuser=<User> JSON.parse(localStorage.getItem("CurrentUser"));
    
   }
 
  ngOnInit() {
    this.getproduct();
    this.loadsuppliers();
    this.getStockByProductID();

   
  //  this.loadsubcategories();
    
   
  }

  getStockByProductID(){
    this.stockservice.getAllStockUnderProduct(this.productID).pipe(
      map(stocks => {
        this.stocks = stocks;
        stocks.forEach(stock => {
          this.locationsByStocks.push(stock.location);
        });
        this.issueBy = this.locationsByStocks;
        //this.getStaffs();
      })
    ).subscribe();
  }

  loadIssueByType(type){
    this.issueType = type;
    if(type == "user"){
      this.getStaffs();
      this.issueByLocation = false;
    }else if(type == "location"){
      this.issueBy = this.locationsByStocks;
      this.issueByLocation = true;
    }
   }

   changeIssueMethod(){
     if(this.issueType == "user"){
      this.issuedID = this.selectedObj.id;
      console.log(this.selectedObj);
     }else if(this.issueType == "location"){
      const stock = this.stock.filter(stock => this.selectedObj.id == stock.location.id);
      console.log(stock);
      this.issuedID = stock[0].id;
     }
   }

  getStockQuantity(){
    const stock = this.stocks.filter(stock => stock.location.id === this.stockLocation.id);
    this.stockID = stock[0].id;
    this.quantity = stock[0].quantity;
    console.log(this.stockID);
    this.getStaffs();
  }

  adjustStock(){
    this.isLoading = true;
    this.stockservice.adjustStock(this.stockID, this.operator, this.updatedQuantity, this.currentuser.id, this.reason)
    .pipe(
      map(data => {
        this.isLoading = false;
        this.getStockByProductID();
        this.getstock();
        this.locationsByStocks = [];
        this.updatedQuantity = 0;
        this.reason = "";
        
      })
    ).subscribe(data => {

    }
  );
  }

  getStaffs(){
    this.userService.getUsers(this.currentuser.companyID).pipe(
      map(users => {
        this.issueBy = <User[]> users;
      })
    ).subscribe();
  }

  issueStock(){
    console.log(this.stockID, this.issuedQuantity, this.issuedID, this.currentuser.id, this.issueStockReason,this.issueType);
    this.isLoading = true;
    this.stockservice.postIssueStock(this.stockID, this.issuedQuantity, this.issuedID, this.currentuser.id, this.issueStockReason,this.issueType)
    .subscribe(data => {
      console.log(data);
      this.isLoading = false;
      this.getStockByProductID();
      this.getstock();
      this.issuedQuantity = 0;
      this.issueStockReason = "";
      this.locationsByStocks = [];
      
    });
  }

  loadcategory(){
    this.productservice.getcategoriesbycompany(this.currentuser.companyID).subscribe(productservice=>{
      const response=productservice;
      this.Categories= <Category[]> response;
      this.loadsubcategories();
    },error=>{
 
    })
   }
   onchange(){
     if(this.product.category.subCategory[0]!=null){
     this.subcategories=this.product.category.subCategory;
     }else{
       this.subcategories=[];
     }
   }

   loadsuppliers(){
    this.productservice.getcomppanysuppliers(this.currentuser.companyID).subscribe(productservice=>{
      const response=productservice;
      this.suppliers= <Supplier[]> response;
    },error=>{
  
    })
   }
   newtag(){
     this.product.tags.push({"name":"","description":"","companyID":this.currentuser.companyID,"id":0});
     console.log(this.product.tags);
   }

   loadsubcategories(){
   // this.productservice.getsubcategory(this.product.category.id).subscribe(data=>{
     // this.subcategories=<Subcategory[]>data;

  //  })
   this.subcategories=this.Categories.filter(cat=>cat.id===this.product.categoryID)[0].subCategory
   }
   onupdate(){
     this.loading=true;
      this.product.categoryID=this.product.category.id;
      console.log(this.product);
     this.productservice.updateproduct(this.product).subscribe(res=>{
this.loading=false;
       console.log(res);
       this.router.navigateByUrl('/layout/products')
     })

   }

  getproduct(){
    const id=+this.route.snapshot.paramMap.get('id');
    this.productID = id;
     this.productservice.getproduct(id).subscribe(data=>{
      this.product=<Product> data
      if(this.product.tags[0]===null){
        this.product.tags.pop();
      }
      this.loadcategory();
      this.getstock();
     },error=>{
  
     })
  }
  getstock(){
    this.stockservice.getstock(this.currentuser.companyID).subscribe(data=>{
      this.stock=<Stock[]>data
    this.stock=this.stock.filter(stock=>stock.productID===this.product.id);
    this.getlocations();
    },error=>{
      
    })
  }
  getlocations(){
    this.companyservice.getcompanylocations(this.currentuser.companyID).subscribe(data=>{
       this.locations=<Location[]> data
    this.locations= this.locations.filter(location=>{ let val=false; for(let s of this.stock){
       if(s.locationID===location.id){
         val=true;
       }
     }
     if(val===false){
      return location
    }});
    })
  }
  createstock(){
   this.stockitem= new Stock(this.product.id,this.location.id,this.product.priceID,this.quantity,this.currentuser.companyID);
    this.stockservice.savestockitem(this.stockitem).subscribe(data=>{
      console.log(data);
     this.getstock();
     this.quantity=0;
this.location=null;
    },error=>{

    })
  }
  printbarcode(){
    let printcontent=document.getElementById("print").innerHTML
      const win = window.open('');
      win.document.write(printcontent);
     win.focus();
      win.print();
      win.close();
     // this.router.navigateByUrl('/layout/makesale');
  }


  public logo
  onfilechanged(event){
    this.logo = event.target.files[0];
    var reader= new FileReader();
     reader.readAsDataURL(this.logo)
   reader.onload=(event)=>{
    var result = reader.result;
    this.product.imgURL = reader.result.toString()
   }
   }
  
   resetPicture(){
     this.product.imgURL = ""
   }

   
 

}
