import { Component, OnInit } from '@angular/core';
import { NgbModal, ModalDismissReasons } from '@ng-bootstrap/ng-bootstrap';
import{FormBuilder,FormGroup,Validators} from '@angular/forms';
import { Router } from '@angular/router';
import{Category} from '../../models/category';
import{Subcategory} from '../../models/subcategory';
import{ProductService} from '../../services/product.service';
import{CompanyService} from '../../services/company.service';
import{StockService} from '../../services/stock.service'
import{User} from '../../models/user';
import { Product } from '../../models/product';
import {Stock} from '../../models/stock';
import { Location } from '../../models/location';
import{Price} from '../../models/price';
import{Address} from '../../models/address';
import{Supplier} from '../../models/supplier';
import{Response} from '../../models/response';
import{Tag} from '../../models/tag';
import { Country } from '../../models/country';
import { State } from '../../models/state';
import { Lga } from '../../models/lga';
import { Unit } from '../../models/unit';
import { Vendor } from '../../models/vendor';
import { Bank } from '../../models/bank';
import { VendorService } from '../../services/vendor.service';
@Component({
  selector: 'app-addproduct',
  templateUrl: './addproduct.component.html',
  styleUrls: ['./addproduct.component.scss','../../../vendor/libs/ng-select/ng-select.scss','../../../vendor/styles/pages/users.scss', '../../../vendor/libs/ngx-chips/ngx-chips.scss']
})
export class AddproductComponent implements OnInit {
  addproductform:FormGroup;
  currentuser:User;
  product:Product;
  stockitem:Stock;
  price:Price;
  supplier:Supplier;
  supplyaddress:Address;
  vendor:Vendor;
  vendoraddress:Address;
  Categories:Category[];
  locations:Location[];
  subcategories:Subcategory[]=[];
  suppliers:Supplier[];
  vendors:Vendor[];
  tags:Tag[]=[];
  countries:Country[];
  states:State[];
  units:Unit[];
  lga:Lga[];
  banks:Bank[];
  bankID:number;
   i=0;
   attributes:object[]
   newsupply=false;
   newvendor=false;
   loading=false;
   submitted=false;
   showextradetails=false;
  constructor(private modalService: NgbModal,private stockservice:StockService,private productservice:ProductService,private companyservice:CompanyService,private formbuilder:FormBuilder, private router:Router, private vendorservice:VendorService) {
    this.currentuser= <User> JSON.parse(localStorage.getItem("CurrentUser"));
    this.attributes=[]
    this.addproductform=this.formbuilder.group({
       name:['',Validators.required],
       code:[''],
       upc:[''],
       ean :[''],
       isbn:[''],
       mpn:[''],
       reOrderLevel:[''],
       category:[,Validators.required],
       subcategory:[null],
       brand:[],
       description:[''],
       quantity:[,Validators.required],
       costprice:[0.0],
       sellprice:[0.0,Validators.required],
       discountable:[true,Validators.required], 
       location:[,Validators.required],
       supplier:[null],
       supplyname:[''],
       street:[''],
       city:[''],
       lga:[''],
       state:[''],
       country:[''],
       //vendor
       vendor:[null],
       vendorname:[''],
       vendorstreet:[''],
       vendorcity:[''],
       vendorlga:[''],
       vendorstate:[''],
       vendorcountry:[''],
       vendoraccountname:[''],
       vendoraccountnumber:[''],
       vendorbankname:['']
       

    })
   }

  ngOnInit() {
    this.loadcategory();
    this.loadlocation();
    this.loadsuppliers();
    this.loadcountries();
    this.loadunits();
    this.loadvendors();
    this.loadbanks()
  }
  loadcategory(){
   this.productservice.getcategoriesbycompany(this.currentuser.companyID).subscribe(productservice=>{
     const response=productservice;
     this.Categories= <Category[]> response;
   },error=>{

   })
  }
 loadlocation(){
   this.companyservice.getcompanylocations(this.currentuser.companyID).subscribe(companyservice=>{
     const response = companyservice;
     this.locations=<Location[]> response;
   },error=>{

   })
 }

 loadunits(){
  this.productservice.getunits().subscribe(productservice=>{
    const response = productservice;
    this.units = <Unit[]> response;
    console.log(this.units);
  },error=>{
  })
}

 loadsuppliers(){
  this.productservice.getcomppanysuppliers(this.currentuser.companyID).subscribe(productservice=>{
    const response=productservice;
    this.suppliers= <Supplier[]> response;
  },error=>{

  })
 }

 loadvendors(){
  this.productservice.getcompanyvendors(this.currentuser.companyID).subscribe(data=>{
    const response=data;
    this.vendors = <Vendor[]> response;
  },error=>{

  })
 }

 loadcountries(){
  this.companyservice.getcountries().subscribe(data=>{
    this.countries=<Country[]> data;
  },error=>{
    return;
  })
}
onchange(){
  if((<Category> this.addproductform.controls.category.value).subCategory[0]!=null){
    this.subcategories=(<Category>this.addproductform.controls.category.value).subCategory;
  }else{
    this.subcategories=[];
  }
  
  
}
onchangec(){
  this.companyservice.getstates((<Country>this.addproductform.controls.country.value).id).subscribe(data=>{
    this.states=<State[]> data;
  },error=>{
    return
  })
 }

 onchangecvendor(){
  this.companyservice.getstates((<Country>this.addproductform.controls.vendorcountry.value).id).subscribe(data=>{
    this.states=<State[]> data;
  },error=>{
    return
  })
 }

 onchangest(){
   this.companyservice.getlga((<State>this.addproductform.controls.state.value).id).subscribe(data=>{
     this.lga=<Lga[]> data;
   },error=>{
     return;
   })
 }

 onchangestvendor(){
  this.companyservice.getlga((<State>this.addproductform.controls.vendorstate.value).id).subscribe(data=>{
    this.lga=<Lga[]> data;
  },error=>{
    return;
  })
}



  open(content, options = {}) {
    this.modalService.open(content, options).result.then((result) => {
      console.log(`Closed with: ${result}`);
    }, (reason) => {
      console.log(`Dismissed ${this.getDismissReason(reason)}`);
    });
  }

  private getDismissReason(reason: any): string {
    if (reason === ModalDismissReasons.ESC) {
      return 'by pressing ESC';
    } else if (reason === ModalDismissReasons.BACKDROP_CLICK) {
      return 'by clicking on a backdrop';
    } else {
      return  `with: ${reason}`;
    }
  }


newtag(){
  this.attributes.push({"name":"","description":"","companyID":this.currentuser.companyID});
}
  createsupplier(){
    this.newsupply=!this.newsupply;
  }
  createvendor(){
    this.newvendor=!this.newvendor
  }
  onsave(){
    this.submitted=true;
    if(this.addproductform.invalid){
      return
    }
 this.loading=true;
    this.price=new Price(this.addproductform.controls.costprice.value,this.addproductform.controls.sellprice.value,this.addproductform.controls.discountable.value,this.currentuser.companyID);
    
    if(this.newsupply){
    this.supplyaddress=new Address(this.addproductform.controls.street.value,this.addproductform.controls.city.value,(<Lga>this.addproductform.controls.lga.value).id,(<State>this.addproductform.controls.state.value).id,(<Country>this.addproductform.controls.country.value).id);
    this.supplier= new Supplier(this.addproductform.controls.supplyname.value,this.supplyaddress,this.currentuser.companyID);
      this.productservice.saveproductsupplier(this.supplier).subscribe(data=>{
        const response=data;
       this.supplier.id=(<Supplier> (<Response> data).message).id;
       //this.createproduct();
      },error=>{
        return;
      })
    }else{
     
      this.supplier=<Supplier> this.addproductform.controls.supplier.value;
     //this.createproduct();
    }

    //vendor
    if(this.newvendor){
      this.vendoraddress=new Address(this.addproductform.controls.vendorstreet.value,this.addproductform.controls.vendorcity.value,(<Lga>this.addproductform.controls.vendorlga.value).id,(<State>this.addproductform.controls.vendorstate.value).id,(<Country>this.addproductform.controls.vendorcountry.value).id);
      this.vendor = new Vendor(this.addproductform.value.vendorname)
      this.vendor.name = this.addproductform.value.vendorname
      this.vendor.accountName = this.addproductform.value.vendoraccountname
      this.vendor.accountNumber = this.addproductform.value.vendoraccountnumber
      this.vendor.companyID = this.currentuser.companyID
      this.vendor.bankID = this.addproductform.value.vendorbankID
      this.vendor.address = this.vendoraddress
      
        this.productservice.saveproductvendor(this.vendor).subscribe(data=>{
          const response=data;
         this.vendor.id=(<Vendor> (<Response> data).message).id;
         this.createproduct();
        },error=>{
          return;
        })
      }else{
       
        this.vendor=<Vendor> this.addproductform.controls.vendor.value;
       this.createproduct();
      }
    //vendor
   
  }

  loadbanks(){
    this.vendorservice.getBanks().subscribe(data=>{
      this.banks=<Bank[]>data},
   err=>{
     return
   })
  }

  createproduct(){
    this.product= new Product(this.addproductform.controls.name.value,this.addproductform.controls.code.value, this.addproductform.controls.upc.value, this.addproductform.controls.ean.value, this.addproductform.controls.isbn.value, this.addproductform.controls.mpn.value, this.addproductform.controls.reOrderLevel.value || 0, this.addproductform.controls.brand.value,this.addproductform.controls.quantity.value,
      this.addproductform.controls.description.value,(<Category> this.addproductform.controls.category.value).id,(<Location> this.addproductform.controls.location.value).id,this.price,this.currentuser.companyID,
    this.imageUrl);
     if(this.supplier!==null){
 this.product.supplierID=this.supplier.id;
     }

     if(this.vendor!==null){
      //this.product.vendorID=this.addproductform.value.vendor.id || this.vendor.id;
      this.product.vendorID=this.vendor.id || this.addproductform.value.vendor.id
          }

     if(this.attributes.length!==0){
       console.log("yes");
      this.product.tags=<Tag[]> this.attributes;
     }
     
 if(this.addproductform.controls.subcategory.value!==null){
   this.product.subCategoryID=(<Subcategory> this.addproductform.controls.subcategory.value).id;
 }
    this.productservice.saveproduct(this.product).subscribe(productservice=>{
       this.product.id=(<Product>(<Response> productservice).message).id;
       this.productservice.getproduct(this.product.id).subscribe(res=>{
  this.product.priceID=(<Product> res).priceID;
  this.createstockitem()
  this.router.navigateByUrl('/layout/products')

  console.log("product",this.product)
  console.log("vendor",this.vendor)
  console.log("supplier",this.supplier)
       },error=>{
         return
       })
  
    },error=>{

    })

  }
  createstockitem(){
    this.stockitem= new Stock(this.product.id,this.product.locationID,this.product.priceID,this.product.quantity,this.currentuser.companyID);
    this.stockservice.savestockitem(this.stockitem).subscribe(response=>{
      this.loading=false;
    },error=>{

    })
    
  }
public logo

public imageUrl=null;


onfilechanged(event){
  this.logo = event.target.files[0];
  var reader= new FileReader();
   reader.readAsDataURL(this.logo)
 reader.onload=(event)=>{
  var result = reader.result;
  this.imageUrl = reader.result
 }
 }

resetPicture(){
  this.imageUrl = ""
}

addProductDetails(){
  this.showextradetails=!this.showextradetails
}

changeBank(bank){
  this.bankID=bank.id
}

test(){
  // console.log("vendor",this.addproductform.value.vendor)
  // console.log("supplier",this.addproductform.value.supplier)
  // console.log("supplierid",this.addproductform.value.supplier.id)
  // console.log("vendorid",this.addproductform.value.vendor.id)
  // console.log("product",this.product)
}

}
