import { Injectable } from '@angular/core';
import { HttpClientModule, HttpClient } from "@angular/common/http";
import { TerminalSetting } from '../models/terminalSetting';
import { HttpHeaders } from "@angular/common/http";
import{AuthenticationService} from './authentication.service';

@Injectable({
  providedIn: 'root'
})
export class TerminalSetupService {

terminalSetting:TerminalSetting

 httpOptions = {
  headers: new HttpHeaders({
    'Content-Type':  'application/json-patch+json',
    'Authorization': 'my-auth-token'
  })
};

 terminalSetupURL = this.authservice.api+'TerminalSetup/save'

getTerminalsSetupURL = this.authservice.api+'TerminalSetup/GetByCompanyAndLocation'

get_single_terminalURL = this.authservice.api+'TerminalSetup/'

post_update_terminalURL = this.authservice.api+'TerminalSetup/update'


  constructor(private http: HttpClient, private  authservice:AuthenticationService) { }

  setTerminal(terminalSetting:TerminalSetting){
    return this.http.post(this.terminalSetupURL,terminalSetting,this.httpOptions);
  }
   
  getTerminalSetup(cID,lID){
    return this.http.get(`${this.getTerminalsSetupURL}/${cID}/${lID}`)
  }

  get_single_terminal(id){
    return this.http.get(`${this.get_single_terminalURL}${id}`)
  }

 post_update_terminal(terminalSetting:TerminalSetting){
  return this.http.post(this.post_update_terminalURL,terminalSetting,this.httpOptions);
}
 

}
