import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms'
import { User } from '../../models/user'
import { CompanyService } from '../../services/company.service'
import { Country } from '../../models/country';
import { State } from '../../models/state';
import { Lga } from '../../models/lga';
import { SuppliersService } from '../../services/suppliers.service'
import { Supplier } from '../../models/supplier'
import { AllsuppliersComponent } from '../allsuppliers/allsuppliers.component'


@Component({
  selector: 'app-suppliers',
  templateUrl: './suppliers.component.html',
  styleUrls: ['./suppliers.component.scss',
  '../../../vendor/libs/ng-select/ng-select.scss','../../../vendor/libs/angular-2-dropdown-multiselect/angular-2-dropdown-multiselect.scss']
})
export class SuppliersComponent implements OnInit {

  addsupplierform:FormGroup
  currentuser:User
  countries:Country[];
  states:State[];
  lga:Lga[];
  customerForm:FormGroup;
  loading = false
  constructor(private fb:FormBuilder, private companyservice:CompanyService, private supplierservice:SuppliersService, private supcomp:AllsuppliersComponent) {
    this.currentuser=<User> JSON.parse(localStorage.getItem("CurrentUser"));
    this.addsupplierform= this.fb.group({
      name:['',Validators.required],
      //phoneNo:['',Validators.required],
      //email:[''],
      companyID:[this.currentuser.companyID],
      address:this.fb.group({
        street:[''],
        city:[''],
        country:[''],
        state:[''],
        lga:[''],
        lgaid:[],
        stateID:[],
        countryID:[]
      }),
    })
   }

  ngOnInit() {
    this.loadcountries()
  }

  loadcountries(){
    this.companyservice.getcountries().subscribe(data=>{
      this.countries=<Country[]> data;
    },error=>{
      return;
    })
  }

  onchangec(){
    this.companyservice.getstates((<Country>this.addsupplierform.controls.address.value.country).id).subscribe(data=>{
      this.states=<State[]> data;
    },error=>{
      return
    })
   }
   onchangest(){
     this.companyservice.getlga((<State>this.addsupplierform.controls.address.value.state).id).subscribe(data=>{
       this.lga=<Lga[]> data;
     },error=>{
       return;
     })
   }

  onclick(){
    console.log(this.supcomp.modalservice.dismissAll(" click"))
  }

   onsave(){
    this.addsupplierform.value.address.lgaid=this.addsupplierform.value.address.lga.id
    this.addsupplierform.value.address.stateID=this.addsupplierform.value.address.state.id
    this.addsupplierform.value.address.countryID=(<Country>this.addsupplierform.controls.address.value.country).id
    this.loading=true;
    this.supplierservice.savesupplier(<Supplier> this.addsupplierform.value).subscribe(res=>{
      this.loading=false;
      this.onclick();
      this.supcomp.loaddata();
      console.log(res)
    },error=>{
      console.log(error)
    })
    
  }

}
