  public prepareReceiptData() : any
  {
    var LF = '%0A' ;
    var left = '%1b%61%0' ;
    var center = '%1b%61%1' ;
    var right = '%1b%61%2' ;
    var HT = '%09';
    var boldOn = '%1B%45%1'
    var boldOff = '%1B%45'

    var today = new Date().toLocaleString();

    var data ;
    data = boldOn + LF;
    data = data + '------------------------------------------------'+LF;

    // if(this.currentuser.company.logo)
    // {
      
    //   var logo = this.currentuser.company.logo

    //   data = data + logo + LF;

    // }

    data = data + this.currentuser.company.name + LF;

    data = data + this.currentuser.location.address.street + LF;
     data = data + this.currentuser.company.phoneNo + LF;
    data = data + this.currentuser.company.email + LF;
    //align left
   //data = data + left ;
    data = data +  '------------------------------------------------'+LF+LF;

    data = data + 'RECEIPT:' + this.invoiceno + '        ' + today +LF;
    data = data  +'Customer: '+this.customer.name+  LF+LF;
    data = data  + 'Detail:  '+this.extradet+ LF+LF;

    data = data+'QTY    '+HT+'ITEM                '+HT+'PRICE   '+'TOTAL'+LF;

     

    this.Cart.forEach((car,index) =>
      {
        data = data + car.quantity + '    ';
        data = data+ car.name + '    ';
        data = data + '' + this.formatPrice(car.price) + '    ';
       data = data + '' + this.formatPrice(car.amount) +LF;
       data = data +  '------------------------------------------------'+LF;
      }
    ); 
    data = data + '                Sub Total:    '+ this.formatPrice(this.amountpay) +LF;

    if(this.salesTaxExist)
    {
      data = data + '                   TAX:   '+ this.formatPrice(this.taxPayable) +LF;
    }
       
    data = data + '                Discount:    '+ this.formatPrice(this.discountval) +LF;

    data = data + '                Total:    '+ this.formatPrice(this.grandTotalAmount - this.discountval) +LF;
   
    data = data +  '------------------------------------------------'+LF+LF;
   
    data = data+'PAYMENT    '+HT+'AMOUNT        '+'CHANGE'+LF;

    this.payments.forEach((payment,index) =>
    {
      data = data + payment.method + '        ';
      data = data+ payment.amount + '         ';
      data = data + payment.change + '    '+LF;
    
     data = data +  '------------------------------------------------'+LF;
    }
  ); 
 data = data + LF + LF + LF;
    //data = data + today;
 data = data + 'You were served by:' + this.cashierName + LF;
 data = data + this.currentuser.company.thankYouMessage +LF;
 data = data + this.currentuser.company.refundPolicy +LF;
 data = data + 'Powered by Mercator (connect@mercator.ng) '+LF+LF;

console.log(data);
     return data;
      
  }


