  savesale(){ 
  this.sale = new Salesregister();
 this.sale.invoiceID=this.invoice.id
 this.sale.payments=this.payments
 this.sale.totalAmount=this.amountpay;
 this.sale.companyID=this.currentuser.companyID;
 if(this.selecteduser){
  this.sale.userCreated=this.selecteduser.id;
 }else{
 this.sale.userCreated=this.currentuser.id;
 }
 this.sale.discount=this.discountval;
 this.sale.customerID=this.customer.id;
 this.sale.locationID=this.currentuser.locationID;
 if(this.salesTaxExist){
 this.sale.tax=this.taxPayable;
 this.sale.taxPercent=this.salesTax.amount;
 this.sale.taxName=this.taxName;
 this.sale.taxInclusive=this.salesTax.isInclusive;
 }
 this.sale.salesDate=formatDate(this.today,'d MMM yy HH:mm','en'); 
 this.saleservice.savesale(this.sale).subscribe(response=>{
   console.log(response);
   this.loading=false;
   this.complete=false;
  this.sale.id=(<Salesregister>(<Response> response).message).id;
},error=>{
  this.errormsg=(<Response> error).statusText;
  this.displayerror();

})
 }

 
