import { Component, OnInit } from '@angular/core';
import { Salesregister } from '../../models/salesregister';
import { User } from '../../models/user';
import {SalesService} from '../../services/sales.service';
import { Invoice } from '../../models/invoice';
import{Router} from '@angular/router'
import { formatDate } from '@angular/common';
import {Location} from '../../models/location';
import {CompanyService} from '../../services/company.service'
import { CustomerService } from '../../services/customer.service';
import { Customer } from '../../models/customer';
import { UserService } from '../../services/user.service';
import {Response} from '../../models/response';
import * as numeral from 'numeral';

@Component({
  selector: 'app-saleshistory',
  templateUrl: './saleshistory.component.html',
  styleUrls: ['./saleshistory.component.scss','../../../vendor/libs/ngx-sweetalert2/ngx-sweetalert2.scss','../../../vendor/libs/ng-select/ng-select.scss','../../../vendor/libs/angular-2-dropdown-multiselect/angular-2-dropdown-multiselect.scss']
})
export class SaleshistoryComponent implements OnInit {
 sales:Salesregister[];
 currentuser:User;
 loading=true;
 locations:Location[];
 selectfrom:Object[]=[];
 searchbyterm:Object;
 cashiersearch=false;
 errormsg="";
 error=false;
 startDate;
   endDate;
 


  constructor(private salesservice:SalesService,private userservice:UserService,private router:Router,private companyservice:CompanyService,private customerservice:CustomerService) {
    this.currentuser=<User> JSON.parse(localStorage.getItem("CurrentUser"));
    this.loadsales();
   }

  ngOnInit() {
  }

    filterVerified = 'Any';
  filterRole = 'Any';
  filterStatus = 'Any';
  filterLatestActivity = [null, null];
  searchKeys = ['salesDate','invoiceNo', 'cashier', 'totalAmount', 'invoice','Status'];
  sortBy = 'InvoiceNo';
  sortDesc = true;
  perPage = 10;

  filterVal = '';
  currentPage = 1;
  totalItems = 0;

  usersData: Salesregister[] = [];
  originalUsersData: Salesregister[] = [];

  loadsales(){
    this.loading=true;
    this.salesservice.getsalebylocation(this.currentuser.companyID,this.currentuser.locationID,0,0).subscribe(sales=>{
     this.originalUsersData=<Salesregister[]> sales
     this.update();
     this.loading=false;
     },error=>{
     return
     })
  }
  displayerror(){
    this.error=true;
    setTimeout(()=>this.error=false,10000);
  }

  filterchange(){
    console.log(this.filterStatus);
  if(this.filterStatus==="Branch"){
    this.cashiersearch=false;
    this.companyservice.getcompanylocations(this.currentuser.companyID).subscribe(data=>{
      console.log(data);
      this.selectfrom = <Location[]> data;
    },error=>{

    })

      
  }else if(this.filterStatus==="Customer"){
    this.cashiersearch=false;
    this.customerservice.getcustomers(this.currentuser.companyID).subscribe(data=>{
      this.selectfrom=<Customer[]> data;
    },error=>{
      
    })
  }else if(this.filterStatus=="Cashier"){
    this.cashiersearch=true;
    this.userservice.getCashiers(this.currentuser.companyID).subscribe(data=>{
      console.log(data);
      this.selectfrom=<User[]> data;
    },error=>{

    })
  }

  }
  onfilter(){
    var startDate = this.startDate ===undefined  || this.startDate.trim() ==""?undefined: formatDate(this.startDate, 'dd-MM-yyyy', 'en');
    var endDate = this.endDate ===undefined  || this.endDate.trim() ==""?undefined: formatDate(this.endDate, 'dd-MM-yyyy', 'en');


    if((typeof startDate !== "undefined" || startDate !="") && (typeof endDate === "undefined" || endDate == "")){
      endDate = "0";
    }else if((typeof startDate === "undefined" || startDate == "") && (typeof endDate !== "undefined" || endDate != "")){
      startDate = "0";
    }else if((typeof startDate === "undefined" || startDate == "") &&  (typeof endDate === "undefined" || endDate == "")){
      startDate = "0", endDate = "0";
    }
    
 if (this.filterStatus==="Branch"){
   this.loading=true;
  this.salesservice.getsalebylocation(this.currentuser.companyID,(<Location>this.searchbyterm).id,startDate,endDate).subscribe(sales=>{
    this.originalUsersData=<Salesregister[]> sales
    this.update();
    this.loading=false;
  },error=>{

  })
}else if (this.filterStatus==="Customer"){
  this.loading=true;
  this.salesservice.getsalebycustomer(this.currentuser.companyID,(<Customer> this.searchbyterm).id,startDate,endDate).subscribe(sales=>{
    this.originalUsersData=<Salesregister[]> sales
    this.update();
    this.loading=false;
  },error=>{

  })
} else if(this.filterStatus==="Cashier"){
  this.loading=true;
  this.salesservice.getsalebycashier((<User> this.searchbyterm).id).subscribe(sales=>{
    this.originalUsersData=<Salesregister[]> sales
    this.update();
    this.loading=false;
  },error=>{

  })
}

  }

  
deletesale(code:any,saleid:number){
   this.salesservice.deletesale(saleid,code).subscribe(res=>{
     console.log(res);
   if((<Response> res).message){
     this.errormsg="Invalid Access Code";
     this.displayerror();
   
  }
  this.loadsales();
    
   },error=>{

   })
  
}

  

  get totalPages() {
    return Math.ceil(this.totalItems / this.perPage);
  }

  update() {
    const data = this.filter(this.originalUsersData);

    this.totalItems = data.length;

    this.sort(data);
    this.usersData = this.paginate(data);
  }

  filter(data) {
    const filter = this.filterVal.toLowerCase();
    return !filter ?
      data.slice(0) :
      data.filter(d => {
        return Object.keys(d)
          .filter(k => this.searchKeys.includes(k))
          .map(k => String(d[k]))
          .join('|')
          .toLowerCase()
          .indexOf(filter) !== -1 || !filter;
      });
  }

  sort(data) {
    data.sort((a: any, b: any) => {
      a = typeof(a[this.sortBy]) === 'string' ? a[this.sortBy].toUpperCase() : a[this.sortBy];
      b = typeof(b[this.sortBy]) === 'string' ? b[this.sortBy].toUpperCase() : b[this.sortBy];

      if (a < b) { return this.sortDesc ? 1 : -1; }
      if (a > b) { return this.sortDesc ? -1 : 1; }
      return 0;
    });
  }

  paginate(data) {
    const perPage = parseInt(String(this.perPage), 10);
    const offset = (this.currentPage - 1) * perPage;

    return data.slice(offset, offset + perPage);
  }

  setSort(key) {
    if (this.sortBy !== key) {
      this.sortBy = key;
      this.sortDesc = false;
    } else {
      this.sortDesc = !this.sortDesc;
    }

    this.currentPage = 1;
    this.update();
  }

  viewinvoice(invoice:Invoice){
 localStorage.setItem("Invoice",JSON.stringify(invoice));
 this.router.navigateByUrl('/layout/invoicing');
  }

  printreciept(sale:Salesregister){
  localStorage.setItem("reciept",JSON.stringify(sale));
  this.router.navigateByUrl('/reciept');

  }
  formatdate(date){
    return formatDate(date,'d MMM yy','en'); 
  }
  formatPrice(price, dropDecimals = false) {
    return numeral(price).format(dropDecimals ? '0,0' : '0,0.00');
  }
  addcustomerpayment(sale:Salesregister){
    localStorage.setItem("pendingsale",JSON.stringify(sale));
    this.router.navigateByUrl('layout/quickpay');
    }
  


}