const routes: Routes = [

  {path:'home',component:DcrhomeComponent, children: [
  {path:'',component:SigninComponent},{path:'register',component:RegisterComponent}]},
  
  {path:'welcome',component:WelcomeComponent},
  {path:'payinvoice',component:InvoicepayComponent},
  {path:'reciept',component:RecieptprintComponent}
  ,
  
  

{ path: 'layout', component: Layout1Component,children: [
  { path: '', component: MainComponent,canActivate:[Authguard],data:{"name":"Dashboard-Admin","read":true}},

{path:'staff',component:StaffComponent,canActivate:[Authguard],data:{"name":"STAFF","read":true},children:[{path:'',outlet:'addstaff',component:AddstaffComponent},{path:'',outlet:'editstaff',component:EditstaffComponent},{path:'',outlet:'movestaff',component:MovestaffComponent}]},{path:'viewstaff/:id',component:ViewstaffComponent},
{path:'store',component:StoreComponent,canActivate:[Authguard],data:{"name":"STORE","read":true}, children: [{path:'', outlet:'addstore',component:AddstoreComponent},{path:'',outlet:'editstore',component:EditstoreComponent}]},
{path:'viewstore/:id',component:ViewstoreComponent},{path:'products',component:ProductsComponent,canActivate:[Authguard],data:{"name":"PRODUCTS","read":true}},
{path:'allsuppliers',component:AllsuppliersComponent,canActivate:[Authguard],data:{"name":"Suppliers","read":true}, children:[{path:'', outlet:'suppliers',component:SuppliersComponent},{path:'',outlet:'editsupplier',component:EditsupplierComponent}]},
{path:'addproduct',component:AddproductComponent,canActivate:[Authguard],data:{"name":"PRODUCTS","read":true}},
{path:'tax',component:BrandsComponent,canActivate:[Authguard],data:{"name":"TAX","read":true}},
{path:'settings',component:SettingsComponent,canActivate:[Authguard],data:{"name":"SETTINGS","read":true}},
{path:'VAS',component:ValueaddedservicesComponent},{path:'quickpay',component:QuickpaymentComponent},
{path:'service',component:ServiceComponent,canActivate:[Authguard],data:{"name":"Services","read":true}},
{path:'checkout',component:CheckoutComponent,canActivate:[Authguard],data:{"name":"POS","read":true}},
{path:'roles',component:RolesComponent,canActivate:[Authguard],data:{"name":"ROLES","read":true}},
{path:'stockcontrol',component:StockcontrolComponent,canActivate:[Authguard],data:{"name":"Stock Control","read":true},children:[{path:'',component:StockadjustComponent}]},
{path:'categories',component:CategoriesComponent,canActivate:[Authguard],data:{"name":"CATEGORIES","read":true}}, 
{path:'duplicate/:id',component:DuplicateproductComponent,canActivate:[Authguard],data:{"name":"PRODUCTS","read":true}},
{path:'customers',component:CustomersComponent,canActivate:[Authguard],data:{"name":"Customers","read":true}, children:[{path:'', outlet:'addcustomer',component:AddcustomerComponent},{path:'',outlet:'editcustomer',component:EditcustomerComponent}]},
  {path:'vendors',component:VendorsComponent,canActivate:[Authguard],data:{"name":"Vendors","read":true}, children:[{path:'', outlet:'addvendor',component:AddvendorComponent},{path:'',outlet:'editvendor',component:EditvendorComponent}]},
  {path:'makesale',component:MakesaleComponent,canActivate:[Authguard],data:{"name":"POS","read":true}},
  {path:'makesalerestaurant',component:MakesalerestaurantComponent,canActivate:[Authguard],data:{"name":"POS","read":true}},
  {path:'livetickets',component:LiveticketsComponent,canActivate:[Authguard],data:{"name":"Live Tickets","read":true}},
  {path:'viewproduct/:id',component:ViewproductComponent},
  {path:'promotions',component:PromotionsComponent,canActivate:[Authguard],data:{"name":"PROMOTIONS","read":true}},{path:'invoicing',component:InvoicingComponent},{path:'saleshistory',component:SaleshistoryComponent,canActivate:[Authguard],data:{"name":"SALES HISTORY REPORT","read":true}
  
  
},
//Newly added paths by Tosin
{
  path: 'print', outlet: 'print', component: PrintLayoutComponent, children: [
    { path: 'payment-report', component: PaymentSummaryComponent},
    {
      path: 'sales-report', component: SalesReportComponent
    }
  ]
},
{
  path: 'inventory', component: InventoryReportComponent, canActivate:[Authguard], data: {"name":"Dashboard-Admin","read":true}
},
{
  path: 'product&service', component: ProductServiceReportComponent, canActivate:[Authguard], data: {"name":"PRODUCT HISTORY REPORT","read":true}
},
{
  path: 'sales-report', component: SalesReportComponent, canActivate:[Authguard], data: {"name":"SALES SUMMARY REPORT","read":true}
},
{
  path: 'payment-summary', component: PaymentSummaryComponent, canActivate: [Authguard], data: {"name":"PAYMENT HISTORY REPORT", "read": true}
},
{
  path: 'report-dashboard', component: ReportComponent, canActivate: [Authguard], data: {"name": "BI REPORT DASHBOARD", "read": true}
},
{
  path: 'cashier-overview', component: DailyBusinessOverviewComponent, canActivate: [Authguard], data: {"name": "BUSINESS OVERVIEW", "read": true}
},
{
  path: 'daily-overview', component: GlobalBusinessOverviewComponent, canActivate: [Authguard], data: {"name": "BUSINESS OVERVIEW", "read": true}
},
{
  path: 'customer-report', component: CustomerComponent, canActivate: [Authguard], data: {"name": "CUSTOMER HISTORY REPORT", "read": true}
},
{
  path: 'settings', component: SettingsComponent, canActivate: [Authguard], data: {"name": "SETTINGS", "read": true}
}, 
{
  path: 'expensereg', component: ExpenseRegisterComponent, canActivate: [Authguard], data: {"name": "EXPENSE MANAGEMENT", "read": true}
},
{
  path: 'expensereg', component: ExpenseCategoryComponent, canActivate: [Authguard], data: {"name": "EXPENSE MANAGEMENT", "read": true}
},
{
  path: 'product-report', component: ProductReportComponent, canActivate: [Authguard], data: {"name": "PRODUCT HISTORY REPORT", "read": true}
},
{
  path: 'stock-report', component: AdjustStockComponent, canActivate: [Authguard], data: {"name": "STOCK REPORT", "read": true}
},
{
  path: 'stock-report', component: IssueStockComponent, canActivate: [Authguard], data: {"name": "STOCK REPORT", "read": true}
},
{
  path: 'profit-report', component: ProfitReportComponent, canActivate: [Authguard], data: {"name": "PROFIT REPORT", "read": true}
},

{path: 'profile', component: ProfileComponent}
]},
//End of added path by Tosin

{ path: '', redirectTo: '/home', pathMatch: 'full' }
];


