import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { ReportingService } from '../../services/reporting.service';
import {formatDate} from '@angular/common';
import { SalesReportComponent } from '../sales-report/sales-report.component';
import {ExcelService} from '../../services/excel.service';
import * as numeral from 'numeral';
import { map } from 'rxjs/operators';
import { IPaymentSummary } from '../../models/PaymentSummary';
import { CustomerService } from '../../services/customer.service';
import {Formats} from '../../Classes/Formats'

@Component({
  selector: 'app-payment-summary',
  templateUrl: './payment-summary.component.html',
  styleUrls: ['./payment-summary.component.scss', '../../../vendor/libs/ngx-sweetalert2/ngx-sweetalert2.scss','../../../vendor/libs/ng-select/ng-select.scss','../../../vendor/libs/angular-2-dropdown-multiselect/angular-2-dropdown-multiselect.scss']
})
export class PaymentSummaryComponent implements OnInit {
  format = new Formats();
  user;
  currentDate = new Date();
  isSearchByCustomer: boolean = false;
  isSearchByBranch: boolean = true;
  startDate;
   endDate;


  selectFrom: any[]=[];
  searchByTerm: Object | any;
  filterStatus;
  loading = true;

  //Payment methods
  paymentByCash: IPaymentSummary[] = [];
  paymentByCard: IPaymentSummary[] = [];
  paymentByTransfer: IPaymentSummary[] = [];
  paymentByCreditNote: IPaymentSummary[] = [];

  paymentSummary: IPaymentSummary[] = [];
  constructor(
    private paymentService: ReportingService,
    private customerService: CustomerService,
    private excelService: ExcelService
  ) {
    this.user=JSON.parse(localStorage.getItem("CurrentUser"));
    this.paymentService.getpaymentssummary(this.user.companyID, 0, this.getCurrentDate(), this.getCurrentDate())
    .subscribe(paymentSummary => {
      this.paymentSummary =  <IPaymentSummary[]>paymentSummary
      this.groupByPaymentMethod(this.paymentSummary);
      this.loading = false;
    })
   }

  ngOnInit() {
  }

  groupByPaymentMethod(paymentSummary: IPaymentSummary[]){
    paymentSummary.forEach(payment => {
      switch (payment.method){
        case 'cash':
          this.paymentByCash.push(payment)
          break;
        case 'card':
          this.paymentByCard.push(payment);
          break;
        case 'creditnote':
          this.paymentByCreditNote.push(payment);
          break;
        case 'transfer':
          this.paymentByTransfer.push(payment);
          break;
        default:
          console.log(payment);
          console.log("Not found");
      }
    })
  }

    filterChange(){
      switch(this.filterStatus){
        case "Branch":
          this.selectFrom = [];
          this.isSearchByCustomer = false;
          this.refreshPaymentMethods();
          this.isSearchByBranch = true;
          this.paymentService.getLocationsByCompanyID(this.user.companyID).pipe(
            map((locations:any[]) => {
              
              locations.push({
                name: "All",
                id: 0
              })
              locations.reverse();
              this.selectFrom = locations;
            })
          ).subscribe();
        break;

        case "Customer":
        this.selectFrom = [];
        this.isSearchByBranch = false;
        this.refreshPaymentMethods();
        this.isSearchByCustomer = true;
        this.customerService.getcustomers(this.user.companyID).pipe(
        ).subscribe((customers: any[]) => {
          this.selectFrom = customers;
        })
          
      }
    }

    onfilter(){
      var startDate = this.startDate ===undefined  || this.startDate.trim() ==""?undefined: formatDate(this.startDate, 'dd-MM-yyyy', 'en');
      var endDate = this.endDate ===undefined  || this.endDate.trim() ==""?undefined: formatDate(this.endDate, 'dd-MM-yyyy', 'en');


      if((typeof startDate !== "undefined" || startDate !="") && (typeof endDate === "undefined" || endDate == "")){
        endDate = "0";
      }else if((typeof startDate === "undefined" || startDate == "") && (typeof endDate !== "undefined" || endDate != "")){
        startDate = "0";
      }else if((typeof startDate === "undefined" || startDate == "") &&  (typeof endDate === "undefined" || endDate == "")){
        startDate = "0", endDate = "0";
      }
    
      switch (this.filterStatus) {
        case "Branch":
            this.loading = true;
            this.paymentService.getpaymentssummary(this.user.companyID, this.searchByTerm.id, startDate, endDate)
          .subscribe(summaryByLocation => {
            this.refreshPaymentMethods();
            this.paymentSummary = <IPaymentSummary[]>summaryByLocation;
            this.groupByPaymentMethod(this.paymentSummary);
            this.loading = false;
          })
          break;
        
        case "Customer":
          this.loading = true;
          this.paymentService.getPaymentSummaryByCustomer(this.user.companyID, this.searchByTerm.id, startDate, endDate)
          .subscribe(paymentByCustomer => {
            this.refreshPaymentMethods();
            this.paymentSummary = <IPaymentSummary[]>paymentByCustomer;
            this.groupByPaymentMethod(this.paymentSummary);
            this.loading = false;
          })
          break;
      }

      console.log({startDate, endDate})
    
    }



  refreshPaymentMethods(){
    this.paymentByCard= [];
     this.paymentByCash= [];
     this.paymentByCreditNote=[];
      this.paymentByTransfer = [];
  }

  getCurrentDate(){
    return formatDate(this.currentDate,'dd-MM-yyyy','en').toString();
  }

  printTable(){
    var divToPrint = document.getElementById('tableRecord');
    var htmlToPrint = '' +
        '<style type="text/css">' +
        'th, td {' +
        'padding:0.5em;' +
        'margin: none;'+
        'text-align: center;'+
        '}' +
        'table {'+
          'width: 100%;'+
          'border: 1px solid #000;'+
          '}'+
        '</style>';
       
    htmlToPrint = htmlToPrint+ divToPrint.outerHTML;
    let newWin = window.open("");
    newWin.document.write(htmlToPrint);
    newWin.print();
    newWin.close();
      
  }

  getTotalPaymentsByPaymentMethod(paymentMethod: IPaymentSummary[]){
    let totalByPaymentMethod = 0;
    paymentMethod.forEach(payment => {
      totalByPaymentMethod = totalByPaymentMethod + payment.total;
    })

    return totalByPaymentMethod;
  }

  getGrandTotal(){
    let grandTotal = 0;
    this.paymentSummary.forEach(payment => {
      grandTotal = grandTotal + payment.total;
    })
    return grandTotal;
  }

formatdate(date){
  return formatDate(date,'d MMM yy','en'); 
}

exportAsXLSX(): void {
  const excelData: any[] = this.paymentByCard.concat(this.paymentByCash.concat(this.paymentByCreditNote.concat(this.paymentByTransfer)));
  this.excelService.exportAsExcelFile(excelData, `${this.user.company.name}-payment-summary`);
}

resetTable(){
  this.paymentService.getpaymentssummary(this.user.companyID, this.user.locationID, this.getCurrentDate(), this.getCurrentDate())
    .subscribe(paymentSummary => {
      this.paymentSummary =  <IPaymentSummary[]>paymentSummary
      this.groupByPaymentMethod(this.paymentSummary);
      this.loading = false;
    })
}

allBranch: {
  locationName: 'All branch',
  locationID: 0
}

formatPrice(price, dropDecimals = false) {
  return numeral(price).format(dropDecimals ? '0,0' : '0,0.00');
}

public downloadAsPDF()  
{  
  this.format.saveAsPDF(this.user.company.name, 'payment-summary', 'tableRecord');
} 
    
}






