import { Component, OnInit } from '@angular/core';
import { Vendor } from '../../../models/vendor';
import { VendorService } from '../../../services/vendor.service';
import { VendorsComponent } from '../vendors/vendors.component';
import { Country } from '../../../models/country';
import { State } from '../../../models/state';
import { Lga } from '../../../models/lga';
import { CompanyService } from '../../../services/company.service';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { User } from '../../../models/user';
import { Bank } from '../../../models/bank';


@Component({
  selector: 'app-editvendor',
  templateUrl: './editvendor.component.html',
  styleUrls: ['./editvendor.component.scss',
  '../../../../vendor/libs/ng-select/ng-select.scss','../../../../vendor/libs/angular-2-dropdown-multiselect/angular-2-dropdown-multiselect.scss']
})
export class EditvendorComponent implements OnInit {
  vendor:Vendor
  loading=false
  countries:Country[];
  states:State[];
  lga:Lga[];
  currentuser: User;
  addvendorform:FormGroup
  selectedCountryID
  selectedStateID
  selectedlgaid
  banks:Bank[]
  selectedbank
  bank:Bank
  currentBank

  chosenbankname:any

  constructor(private vendorservice:VendorService, private vencomp:VendorsComponent, private companyservice: CompanyService, private fb:FormBuilder) {
    this.vendor=this.vencomp.choosenvendor
    this.currentuser=<User> JSON.parse(localStorage.getItem("CurrentUser"))
    console.log(this.vendor)
    this.addvendorform=this.fb.group({
      name:['',Validators.required],
      accountname:[''],
      accountnumber:['',],
      bankname:[''],
      companyID:[this.currentuser.companyID],
      address:this.fb.group({
        street:[''],
        city:[''],
        country:[''],
        state:[''],
        lga:[''],
        lgaid:[],
        stateID:[],
        countryID:[]
      }),
    })

    if(this.vendor.address == null || this.vendor.address.state == null || this.vendor.address.country == null || this.vendor.address.lga == null){
      this.vendor.address={
        street:"",
        city:"",
        country:{name:""},
        state:{name:""},
        lga:{name:""}
      }
    }
    else{
      
    }
    this.loadbanks()
   }

   onclick(){
    console.log(this.vencomp.modalService.dismissAll("click"));
    }

    onsave(){
       this.loading=true
       this.vendor.address.stateID=this.selectedStateID
       this.vendor.address.countryID=this.selectedCountryID
       this.vendor.address.lgaid=this.selectedlgaid
       this.vendor.bankID=this.selectedbank
       //this.vendor.bank.fullName=this.chosenbankname.fullName
       this.vendor.bank.id=this.selectedbank
        this.vendorservice.updatevendor(this.vendor).subscribe(res=>{
          this.loading=false
          this.onclick()
          this.vencomp.loaddata()
        })
      console.log(this.vendor)
    }

    loadcountries(){
      this.companyservice.getcountries().subscribe(data=>{
        this.countries=<Country[]> data;
      },error=>{
        return;
      })
    }
  
    onchangec(chosencountry){
      this.selectedCountryID=chosencountry.id
      this.companyservice.getstates(this.selectedCountryID).subscribe(data=>{
        this.states=<State[]> data;
      },error=>{
        return
      })
     }
     onchangest(chosenstate){
       this.selectedStateID=chosenstate.id
       this.companyservice.getlga(this.selectedStateID).subscribe(data=>{
         this.lga=<Lga[]> data;
       },error=>{
         return;
       })
     }
     onchangel(chosenlga){
      this.selectedlgaid=chosenlga.id
    }

    onchangebank(chosenbank){
      this.chosenbankname=chosenbank
      this.selectedbank=chosenbank.id
      // console.log("selectedbank",this.selectedbank)
      // console.log("chosenbankname",this.chosenbankname,this.chosenbankname.fullName)
    }

    loadbanks(){
      this.vendorservice.getBanks().subscribe(data=>{
        this.banks=<Bank[]>data
        this.currentBank=this.banks[+this.vendor.bankID-1]
      },
     err=>{
       return
     })
    }
 


  ngOnInit() {
    this.loadcountries()
    this.loadbanks()
  }

}
