import { Injectable } from '@angular/core';
import{HttpClient, HttpHeaders, HttpRequest} from '@angular/common/http';
import {Customer} from '../models/customer';
import{AuthenticationService} from './authentication.service'
@Injectable({
  providedIn: 'root'
})
export class CustomerService {

  constructor(private httpclient:HttpClient,private authservice:AuthenticationService) {

   }
   getindividualcustomer(id:number){
     return this.httpclient.get(this.authservice.api+"Customer/"+id);
   }
   savecustomer(customer:Customer){
     return this.httpclient.post(this.authservice.api+"Customer/save",customer);
   }
   updatecustomer(customer:Customer){
     return this.httpclient.post(this.authservice.api+"Customer/update",customer);
   }
   getcustomer(id:number,identifier:string){
     return this.httpclient.get(this.authservice.api+"Customer/Search/"+id+"/"+identifier);
   }
   getcustomers(id:number){
     return this.httpclient.get(this.authservice.api+"Customer/GetByCompany/"+id);
   }
   deletecustomer(cid:number,cusid:number,userid:number){
     return this.httpclient.post(this.authservice.api+"Customer/delete",{"companyID":cid,"customerID":cusid,"userID":userid})
   }

   postBulkCustomer(bulkCustomer){
    return this.httpclient.post(this.authservice.api+"Customer/SaveBulk", bulkCustomer, {headers: {'Content-Type': 'application/json; charset=utf-8'}});
   }
}
