import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Vendor } from '../models/vendor'
import { AuthenticationService } from './authentication.service'

@Injectable({
  providedIn: 'root'
})
export class VendorService {

  constructor(private httpclient:HttpClient, private authservice:AuthenticationService) { }

  savevendor(vendor:Vendor){
    return this.httpclient.post(this.authservice.api+"Vendor/save", vendor)
  }

  getvendors(id:number){
    return this.httpclient.get(this.authservice.api+"Vendor/GetByCompany/"+id)
  }

  updatevendor(vendor:Vendor){
    return this.httpclient.post(this.authservice.api+"Vendor/update",vendor)
  }

  getBanks(){
    return this.httpclient.get(this.authservice.api+"Bank/GetAll")
  }
}
