import { Injectable } from '@angular/core';
import { HttpClientModule, HttpClient } from "@angular/common/http";
import { CompanyConfig } from '../models/companyConfig';
import{AuthenticationService} from './authentication.service';


@Injectable({
  providedIn: 'root'
})
export class CompanyConfigService {

  //compnayConfig:CompanyConfig;
  kompanydetail:CompanyConfig[]

  get_company_config_by_company_id_URL = this.authservice.api+'Company/'

post_companyConfig_update_URL = this.authservice.api+'CompanyConfig/update/'

  constructor(private http: HttpClient, private authservice:AuthenticationService) { }

  get_company_config_by_company_id(cID){
 return this.http.get(`${this.get_company_config_by_company_id_URL}${cID}`)
}

post_companyConfig_update(kompanydetail:CompanyConfig){
  //console.log(kompanydetail)
 return  this.http.post(this.post_companyConfig_update_URL,kompanydetail)
}


}
