import { Component, OnInit, AfterViewChecked,ChangeDetectorRef} from '@angular/core';
import { NgbModal, ModalDismissReasons } from '@ng-bootstrap/ng-bootstrap';
import { Router, ActivatedRoute } from '@angular/router';
import{Customer} from '../../models/customer';
import{CustomerService} from '../../services/customer.service';
import { User } from '../../models/user';

@Component({
  selector: 'app-customers',
  templateUrl: './customers.component.html',
  styleUrls: ['./customers.component.scss','../../../vendor/styles/pages/contacts.scss']
})
export class CustomersComponent implements OnInit{
 customers:Customer[]
 currentuser:User;
 choosencustomer:Customer;
 filterval="";
 filteredcustomers:Customer[];

 //bulkType: string = "customer"
 
  constructor(private customerservice:CustomerService,public modalService: NgbModal,public router:Router, private route:ActivatedRoute, private cdr:ChangeDetectorRef) { 
    this.currentuser=<User> JSON.parse(localStorage.getItem("CurrentUser"));
  }
 
  show=false;


  ngOnInit() {
    this.loaddata();
  }
  viewMode = 'col';


  loaddata(){
    this.customerservice.getcustomers(this.currentuser.companyID).subscribe(data=>{
      this.customers=<Customer[]>data;
      this.filteredcustomers=<Customer[]>JSON.parse(JSON.stringify(this.customers));
    })
  }
  open(content, options = {}) {
    this.modalService.open(content, options).result.then((result) => {
      console.log(`Closed with: ${result}`);
    }, (reason) => {
      console.log(`Dismissed ${this.getDismissReason(reason)}`);
    });
  }

  private getDismissReason(reason: any): string {
    if (reason === ModalDismissReasons.ESC) {
      return 'by pressing ESC';
    } else if (reason === ModalDismissReasons.BACKDROP_CLICK) {
      return 'by clicking on a backdrop';
    } else {
      return  `with: ${reason}`;
    }
  }
  get modalservice(){
    return this.modalService;
  }
  filtercustomers(){
    if(this.filterval.length===0){
      this.filteredcustomers=<Customer[]>JSON.parse(JSON.stringify(this.customers));
    }
    this.filteredcustomers=this.customers.filter(cus=>cus.name.toLowerCase().includes(this.filterval.toLowerCase())||cus.email.toLowerCase().includes(this.filterval.toLowerCase())||cus.phoneNo.toLowerCase().includes(this.filterval.toLowerCase()));
  }
  removecustomer(id:number){
this.customerservice.deletecustomer(this.currentuser.companyID,id,this.currentuser.id).subscribe(data=>{
  console.log(data);
  this.loaddata();
})
  }

}
