import { Component, OnInit } from '@angular/core';
import { NgbModal, ModalDismissReasons } from '@ng-bootstrap/ng-bootstrap';
import{FormBuilder,FormGroup,Validators} from '@angular/forms';
import{Roles} from '../../models/roles';
import{User} from '../../models/user';
import{UserService} from '../../services/user.service';
import { Priviledge } from '../../models/priviledge';

@Component({
  selector: 'app-roles',
  templateUrl: './roles.component.html',
  styleUrls: ['./roles.component.scss']
})
export class RolesComponent implements OnInit {
  roleform:FormGroup;
  role:Roles;
  privileges:Priviledge[];
  response:object;
  currentuser:User;
  roledata:Roles[];
  loading=true;
  appMenus;
  privilegeArray;
  selectedRole;
  

  constructor(public modalService: NgbModal,private formbuilder:FormBuilder, private userservice:UserService,) {
    this.roleform=this.formbuilder.group({
      name:['',Validators.required]
    })
    this.currentuser=<User> JSON.parse(localStorage.getItem("CurrentUser"));
    this.loaddata();
   
   }


  module=[{"name":"Sales","code":"Sales","read":false,"write":false},{"name":"Inventory","code":"invent","read":false,"write":false},{"name":"Admin","code":"admin","read":false,"write":false}
,{"name":"Reports","code":"report","read":false,"write":false}];

  open(content, options = {}) {
    this.modalService.open(content, options).result.then((result) => {
      console.log(`Closed with: ${result}`);
    }, (reason) => {
      console.log(`Dismissed ${this.getDismissReason(reason)}`);
    });
  }

  private getDismissReason(reason: any): string {
    if (reason === ModalDismissReasons.ESC) {
      return 'by pressing ESC';
    } else if (reason === ModalDismissReasons.BACKDROP_CLICK) {
      return 'by clicking on a backdrop';
    } else {
      return  `with: ${reason}`;
    }
  }

  ngOnInit() {
    this.loadAppMenu()
  }

  loaddata(){
   this.loading=true
 this.userservice.getRoles(this.currentuser.companyID).subscribe(userservice=>{
 this.response=userservice;
 this.roledata=<Roles[]> this.response;
 this.loading=false;
 console.log("roledata",this.roledata)
 },error=>{

 })
  }


  onsave(){
 if(this.roleform.invalid){
   return;
 }else{
  //  this.role=new Roles(this.roleform.controls.name.value,this.roleform.controls.name.value,<Priviledge[]> this.module,this.currentuser.companyID);
  this.role=new Roles(this.roleform.controls.name.value,this.roleform.controls.name.value,<Priviledge[]> this.privilegeArray,this.currentuser.companyID);
   this.userservice.createuserrole(this.role).subscribe(userservice=>{
     this.response=userservice;
     console.log(this.response);
     this.loaddata();
     this.modalService.dismissAll("click");
   },error=>{
     return;
   })
  console.log(this.role)
  
  }

}

loadAppMenu(){
  this.userservice.getAppMenu().subscribe(data=>{
    this.appMenus=data
    this.privilegeArray=new Array(this.appMenus.length)
    this.appMenus.forEach((appMenu,index)=>{
      this.privilegeArray[index]={
        appMenu,
        write:false,
        read:false,
        name:appMenu.name,
        companyID:this.currentuser.companyID
      }
    })
  },
  err=>{
    console.log(err)
  })
}

editrole(role){
  this.selectedRole=role
}

updaterole(){
  console.log(this.selectedRole)
  this.userservice.updateRole(this.selectedRole).subscribe(data=>{
    this.modalService.dismissAll("click")
    console.log(data)
  },
  err=>{console.log(err)})
}

test(){
  console.log("roles")
}

}
