import { Component, OnInit } from '@angular/core';
import {StaffComponent} from '../staff/staff.component'
import { User } from '../../models/user';
import{Location} from '../../models/location';
import {CompanyService} from '../../services/company.service';
import {UserService} from '../../services/user.service';

@Component({
  selector: 'app-movestaff',
  templateUrl: './movestaff.component.html',
  styleUrls: ['./movestaff.component.scss','../../../vendor/libs/ng-select/ng-select.scss','../../../vendor/libs/angular-2-dropdown-multiselect/angular-2-dropdown-multiselect.scss']
})
export class MovestaffComponent implements OnInit {
user:User;
locations:Location[];
newlocation:Location;
  constructor(private userservice:UserService,private companyservice:CompanyService,private staffcomp:StaffComponent) { }

  ngOnInit() {
this.user=this.staffcomp.currentstaff
 this.loadlocations();
  }
  loadlocations(){
    this.companyservice.getcompanylocations(this.staffcomp.currentuser.companyID).subscribe(data=>{
      this.locations=<Location[]> data;
      this.locations=this.locations.filter(loc=>loc.id!==this.user.locationID);
    })
  }
  onclick(){
    console.log(this.staffcomp.modalService.dismissAll("click"));
    }
onsave(){
  var updated={"id":this.user.id,"locationID":this.newlocation.id};
  this.userservice.updateuser(<User> updated).subscribe(res=>{
    console.log(res);
    this.staffcomp.loadData();
    this.onclick();
  })

}


}
