import { Component, OnInit } from '@angular/core';
import {StaffComponent} from '../staff/staff.component'
import{FormBuilder,FormGroup,Validators} from '@angular/forms';
import{Roles} from '../../models/roles';
import{User} from '../../models/user';
import{UserService} from '../../services/user.service';
import{CompanyService} from '../../services/company.service';
import{Company} from '../../models/company';
import{Location} from '../../models/location';

@Component({
  selector: 'app-addstaff',
  templateUrl: './addstaff.component.html',
  styleUrls: ['./addstaff.component.scss','../../../vendor/libs/ng-select/ng-select.scss','../../../vendor/libs/angular-2-dropdown-multiselect/angular-2-dropdown-multiselect.scss']
})
export class AddstaffComponent implements OnInit {
  addstaffform:FormGroup;
  roles:Roles[];
  currentuser:User;
  response:object;
  chosenrole:Roles;
  chosenlocation:Location;


  constructor(private staffcomp:StaffComponent,private formbuilder:FormBuilder,private userservice:UserService,private companyservice:CompanyService) {
    this.addstaffform=this.formbuilder.group({
      fname:['',Validators.required],
      lname:['',Validators.required],
      email:['',Validators.required],
      phoneno:['',Validators.required],
      username:['',Validators.required],
      role:['',Validators.required],
      location:['',Validators.required]

    })
    this.currentuser=<User> JSON.parse(localStorage.getItem("CurrentUser"));
    this.loadroledata();
    this.loadstoredata();
   }
 
  newrole=false;
  ngOnInit() {
  }
  
  loadroledata(){
    this.userservice.getRoles(this.currentuser.companyID).subscribe(userservice=>{
     this.response=userservice;
     this.roles=<Roles[]> this.response;
    },error=>{

    })

  }
  loadstoredata(){
    this.companyservice.getcompany(this.currentuser.companyID).subscribe(companyservice=>{
      this.currentuser.company=<Company> companyservice;
     },error=>{
 
     })
  }

  onclick(){
    this.staffcomp.modalService.dismissAll("click");
    this.staffcomp.loadData();
    }
    onsave(){
      this.chosenlocation=<Location> this.addstaffform.controls.location.value;
      this.chosenrole=<Roles> this.addstaffform.controls.role.value;
     this.userservice.saveUser(this.addstaffform.controls.fname.value,this.addstaffform.controls.lname.value,this.addstaffform.controls.username.value,this.addstaffform.controls.phoneno.value,this.addstaffform.controls.email.value,"1234",this.chosenrole.id,this.chosenlocation.id,"y",this.currentuser.companyID,this.currentuser.id).subscribe(userservice=>{
       this.response=userservice;
       this.onclick();
     },error=>{
       
     })
    }


}
