import { Component, OnInit } from '@angular/core';
import { NgbModal, ModalDismissReasons } from '@ng-bootstrap/ng-bootstrap';
import{FormBuilder,FormGroup,Validators} from '@angular/forms';
import{ProductService} from '../../services/product.service';
import{User} from '../../models/user';
import{Subcategory} from '../../models/subcategory';
import { Category } from '../../models/category';

@Component({
  selector: 'app-categories',
  templateUrl: './categories.component.html',
  styleUrls: ['./categories.component.scss']
})
export class CategoriesComponent implements OnInit {
 categoryform:FormGroup;
 currentuser:User;
 subcategories:Subcategory[]=[];
 response:object;
 id:number;
 categories:Category;
 loading=true;

  constructor(private modalService: NgbModal,private formbuilder:FormBuilder,private productservice:ProductService) { 
    this.categoryform=this.formbuilder.group({
      name:['',Validators.required]
    })
    this.currentuser=<User> JSON.parse(localStorage.getItem("CurrentUser"));
    this.loaddata();
  }

  ngOnInit() {
  }
  loaddata(){
    this.productservice.getcategoriesbycompany(this.currentuser.companyID).subscribe(productservice=>{
      this.response=productservice;
    this.categories=<Category> this.response;
    this.loading=false;
    })
  }
  open(content, options = {}) {
    this.modalService.open(content, options).result.then((result) => {
      console.log(`Closed with: ${result}`);
    }, (reason) => {
      console.log(`Dismissed ${this.getDismissReason(reason)}`);
    });
  }

  private getDismissReason(reason: any): string {
    if (reason === ModalDismissReasons.ESC) {
      return 'by pressing ESC';
    } else if (reason === ModalDismissReasons.BACKDROP_CLICK) {
      return 'by clicking on a backdrop';
    } else {
      return  `with: ${reason}`;
    }
  }
  onsave(){
  if(this.categoryform.invalid){
    return;
  }else{
    this.productservice.savecategory(this.categoryform.controls.name.value,this.currentuser.companyID).subscribe(productservice=>{
      this.response=productservice;
      this.onsavesub();
      this.modalService.dismissAll("click");
      this.loaddata();
    },error=>{

    })
  }
  }
  onsavesub(){
   for(let sub of this.subcategories){
     this.productservice.savesubcategory(sub).subscribe(productservice=>{
      this.response=productservice;
      console.log(this.response);
      this.subcategories=[];
      this.loaddata();
      this.modalService.dismissAll("click");
     },error=>{
      
     })
   }
  }
  newsub(){
    this.subcategories.push({"name":"","categoryID":this.id,"companyID":this.currentuser.companyID,id:0});
  }
sub(cate:Category){
  if(cate.subCategory[0]===null){
    return false;
  }
  return true

}
}
