import { Injectable } from '@angular/core';
import{HttpClient, HttpHeaders} from '@angular/common/http';
import{Subcategory} from '../models/subcategory';
import { Product } from '../models/product';
import {Supplier} from '../models/supplier';
import{AuthenticationService} from './authentication.service';
import { Vendor } from '../models/vendor';

@Injectable({
  providedIn: 'root'
})
export class ProductService {

  constructor(private httpclient:HttpClient,private authservice:AuthenticationService) { }

  savecategory(name:string,id:number){
    return this.httpclient.post(this.authservice.api+"Category/save",{"name":name, "companyID":id})
  }
  getcategoriesbycompany(id:number){
    return this.httpclient.get(this.authservice.api+"Category/GetByCompany/"+id);

  }
  savesubcategory(subcategory:Subcategory){
    return this.httpclient.post(this.authservice.api+"SubCategory/save",subcategory)
  }
  getsubcategory(id:number){
    return this.httpclient.get(this.authservice.api+"SubCategory/GetByCategory/"+id);
  }
  saveproduct(product:Product){
 return this.httpclient.post(this.authservice.api+"Product/save",product);
  }
  getproduct(id:number){
    return this.httpclient.get(this.authservice.api+"Product/"+id);
  }
  getproducts(id:number){
  return this.httpclient.get(this.authservice.api+"Product/GetByCompany/"+id);
  }

  getunits(){
    return this.httpclient.get(this.authservice.api+"UnitMeasure/GetAll");
    }

  updateproduct(product:Product){
    return this.httpclient.post(this.authservice.api+"Product/update",product);
  }
  searchproducts(companyid:number,locationid:number,searchterm:string){
    return this.httpclient.get(this.authservice.api+"Product/Search/"+companyid+"/"+locationid+"/"+searchterm)
  }

  saveproductsupplier(supplier:Supplier){
    return this.httpclient.post(this.authservice.api+"Supplier/save",supplier);

  }


  saveproductvendor(vendor:Vendor){
    return this.httpclient.post(this.authservice.api+"Vendor/save",vendor);

  }
  

  deleteProductByID(productID: number){
    return this.httpclient.post(this.authservice.api+`Product/delete?ID=${productID}`, null);
  }

getcomppanysuppliers(id:number){
  return this.httpclient.get(this.authservice.api+"Supplier/GetByCompany/"+id);
}
getcompanyvendors(id:number){
  return this.httpclient.get(this.authservice.api+"Vendor/GetByCompany/"+id)
}
validateqty(cid:number,lid:number,pid:number,qty:number){
  return this.httpclient.post(this.authservice.api+"Product/ValidateQty",{"companyID":cid,"locationID":lid,"productID":pid,"qty":qty})
}
postBulkProduct(bulkData){
  return this.httpclient.post(this.authservice.api+"Product/saveBulk",bulkData)
}
}
