import { Component, OnInit } from '@angular/core';
import { map, tap } from 'rxjs/operators';
import { ReportingService } from '../../services/reporting.service';
import { ProductService } from '../../services/product.service';
import { ILocationSummary } from '../../models/LocationSummary';
import { IProductSale } from '../../models/ProductSummary';
import { formatDate } from '@angular/common';
import { ExcelService } from '../../services/excel.service';

@Component({
  selector: 'app-product-report',
  templateUrl: './product-report.component.html',
  styleUrls: ['./product-report.component.scss', '../../../vendor/libs/ngx-sweetalert2/ngx-sweetalert2.scss','../../../vendor/libs/ng-select/ng-select.scss','../../../vendor/libs/angular-2-dropdown-multiselect/angular-2-dropdown-multiselect.scss']
})
export class ProductReportComponent implements OnInit {
  user;
  filterStatus = 'Any';
  selectfrom: any[] = [];
  summaryData: IProductSale[]
  searchbyterm: any;
  filterVal = '';
  currentPage = 1;
  totalItems = 0;
  loading: boolean = true;
  startDate;
   endDate;
  constructor(
    private reportService: ReportingService, 
    private productService: ProductService,
    private exportService: ExcelService
  ) { 
    this.user = JSON.parse(localStorage.getItem("CurrentUser"));
        this.reportService.getProductSummary(this.user.companyID, this.user.locationID, "0", "0").pipe(
          tap(summaryByLocation => {
            this.summaryData = summaryByLocation;
            this.loading = false;
          })
        )
      .subscribe()
  }

  ngOnInit() {
  }


  filterchange(){
    
    if(this.filterStatus==="Branch"){
     this.reportService.getLocationsByCompanyID(this.user.companyID).pipe(
       map((locations: any[]) => {
         locations.push({
           name: "All",
           id: 0
         })
         locations.reverse();
         this.selectfrom = locations;
       })
     ).subscribe()
     }else if(this.filterStatus==="Product"){
       this.productService.getproducts(this.user.companyID).pipe(
         tap(products => {

         })
       )
     
     }//Not available yet
 
 }
 onfilter(){
  var startDate = this.startDate ===undefined  || this.startDate.trim() ==""?undefined: formatDate(this.startDate, 'dd-MM-yyyy', 'en');
      var endDate = this.endDate ===undefined  || this.endDate.trim() ==""?undefined: formatDate(this.endDate, 'dd-MM-yyyy', 'en');


      if((typeof startDate !== "undefined" || startDate !="") && (typeof endDate === "undefined" || endDate == "")){
        endDate = "0";
      }else if((typeof startDate === "undefined" || startDate == "") && (typeof endDate !== "undefined" || endDate != "")){
        startDate = "0";
      }else if((typeof startDate === "undefined" || startDate == "") &&  (typeof endDate === "undefined" || endDate == "")){
        startDate = "0", endDate = "0";
      }

  switch (this.filterStatus) {
    case "Branch":
        this.loading = true;
        this.reportService.getProductSummary(this.user.companyID, this.searchbyterm.id, startDate, endDate).pipe(
          map(summaryByLocation => {
            this.summaryData = summaryByLocation;
            this.loading = false;
          })
        )
      .subscribe()
        break;
    default:
      break;
  }

}
 
printTable(){
  var divToPrint = document.getElementById('tableRecord');
  var htmlToPrint = '' +
      '<style type="text/css">' +
      'table th, table td {' +
      'padding:0.5em;' +
      'text-align: center;'+
      'border: 1px solid #000;'+
      '}' +
      'table {'+
        'width: 100%;'+
        'border: 1px solid #000;'+
        '}'+
      '</style>';
  htmlToPrint = htmlToPrint+ divToPrint.outerHTML;
  let newWin = window.open("");
  newWin.document.write(htmlToPrint);
  newWin.print();
  newWin.close();
  
}

exportASXLSX(){
  this.exportService.exportAsExcelFile(this.summaryData, `${this.user.company.name}-productsales-report`);
}



}
