import { Component, OnInit, ChangeDetectionStrategy, ViewChild, TemplateRef } from '@angular/core';
import {
  startOfDay, endOfDay, subDays, addDays, endOfMonth, isSameDay, isSameMonth, addHours
} from 'date-fns';
import { Subject } from 'rxjs';
import { NgbModal} from '@ng-bootstrap/ng-bootstrap';
import {
  CalendarEvent, CalendarEventAction, CalendarEventTimesChangedEvent, CalendarView
} from 'angular-calendar';
import { ReportingService } from '../../services/reporting.service';
import { User } from '../../models/user';
import { JsonPipe } from '@angular/common';
import { map } from 'rxjs/operators';

const colors: any = {
  red: {
    primary: '#ad2121',
    secondary: '#FAE3E3'
  },
  blue: {
    primary: '#1e90ff',
    secondary: '#D1E8FF'
  },
  yellow: {
    primary: '#e3bc08',
    secondary: '#FDF1BA'
  }
};

@Component({
  selector: 'app-birthdays',
  templateUrl: './birthdays.component.html',
  styleUrls: ['./birthdays.component.scss']
})
export class BirthdaysComponent implements OnInit {
  loading = true;
  user: User;
  @ViewChild('modalContent', {  }) modalContent: TemplateRef<any>;

  view: string = 'month';

  CalendarView = CalendarView;

  viewDate: Date = new Date();
  

  modalData: {
    action: string;
    event: CalendarEvent;
  };

  refresh: Subject<any> = new Subject();
   birthdays: CalendarEvent[]=[];
  
    
  

  activeDayIsOpen: boolean = true;
  actions: CalendarEventAction[] = [
    {
      label: '<i class="fa fa-fw fa-pencil"></i>',
      onClick: ({ event }: { event: CalendarEvent }): void => {
        this.handleEvent('Edited', event);
      }
    },
    {
      label: '<i class="fa fa-fw fa-times"></i>',
      onClick: ({ event }: { event: CalendarEvent }): void => {
        this.birthdays = this.birthdays.filter(iEvent => iEvent !== event);
        this.handleEvent('Deleted', event);
      }
    }
  ];

  

  constructor(private modal: NgbModal, private birthdayService: ReportingService) { 
    this.user = JSON.parse(localStorage.getItem("CurrentUser"));
    this.birthdayService.getBirthday(this.user.companyID, "M", this.viewDate.getMonth()+1).pipe(
      map(birthdays => {
        birthdays.forEach(birthday => {
          let birthDay = birthday.birthDay.split("/", 2);
          console.log(parseInt(birthDay[0]));
          this.birthdays.push({
            start: subDays(startOfDay(new Date(this.viewDate.getFullYear(), parseInt(birthDay[1])-1, parseInt(birthDay[0]))), 0),
            end: addDays(new Date(this.viewDate.getFullYear(),  parseInt(birthDay[1])-1, parseInt(birthDay[0])), 0),
            title: birthday.name,
            color: colors.red,
            actions: this.actions,
            allDay: true,
            resizable: {
              beforeStart: true,
              afterEnd: true
            },
            draggable: true
     })
          });
        
      })
    ).subscribe();
  }

  dayClicked({ date, events }: { date: Date; events: CalendarEvent[] }): void {
    
    if (isSameMonth(date, this.viewDate)) {
      console.log(true);
      if (
        (isSameDay(this.viewDate, date) && this.activeDayIsOpen === true) ||
        events.length === 0
      ) {
        console.log("Cannot open");
        this.activeDayIsOpen = false;
      } else {
        this.activeDayIsOpen = true;
        console.log("Opened");
      }
      this.viewDate = date;
    }
  }

  // eventTimesChanged({
  //   event,
  //   newStart,
  //   newEnd
  // }: CalendarEventTimesChangedEvent): void {
  //   this.birthdays = this.birthday.map(iEvent => {
  //     if (iEvent === event) {
  //       return {
  //         ...event,
  //         start: newStart,
  //         end: newEnd
  //       };
  //     }
  //     return iEvent;
  //   });
  //   this.handleEvent('Dropped or resized', event);
  // }

  handleEvent(action: string, event: CalendarEvent): void {
    this.modalData = { event, action };
    this.modal.open(this.modalContent, { size: 'lg' });
  }

  // addEvent(): void {
  //   this.birthday = [
  //     ...this.birthday,
  //     {
  //       title: 'New event',
  //       start: startOfDay(new Date()),
  //       end: endOfDay(new Date()),
  //       color: colors.red,
  //       draggable: true,
  //       resizable: {
  //         beforeStart: true,
  //         afterEnd: true
  //       }
  //     }
  //   ];
  // }

  // deleteEvent(eventToDelete: CalendarEvent) {
  //   this.birthday = this.birthday.filter(event => event !== eventToDelete);
  // }

  setView(view: CalendarView) {
    this.view = view;
  }

  closeOpenMonthViewDay() {
    this.activeDayIsOpen = false;
  }

  ngOnInit() {
  }

}
