import { Component, OnInit } from '@angular/core';
import {StaffComponent} from '../staff/staff.component'
import{FormBuilder,FormGroup,Validators} from '@angular/forms';
import{Roles} from '../../models/roles';
import{User} from '../../models/user';
import{UserService} from '../../services/user.service';
import{CompanyService} from '../../services/company.service';
import{Location} from '../../models/location';


@Component({
  selector: 'app-editstaff',
  templateUrl: './editstaff.component.html',
  styleUrls: ['./editstaff.component.scss','../../../vendor/libs/ng-select/ng-select.scss','../../../vendor/libs/angular-2-dropdown-multiselect/angular-2-dropdown-multiselect.scss']
})
export class EditstaffComponent implements OnInit {
  editstaffform:FormGroup;
  user:User;
  roles:Roles[];
  locations:Location[]
  response:object;
  chosenrole:Roles;
  chosenlocation:Location;

  constructor(private staffcomp:StaffComponent,private formbuilder:FormBuilder,private userservice:UserService,private companyservice:CompanyService) {
    this.user=this.staffcomp.currentstaff;

    this.editstaffform=this.formbuilder.group({
      fname:[this.user.firstName,Validators.required],
      lname:[this.user.lastName,Validators.required],
      email:[this.user.email,Validators.required],
      phoneno:[this.user.phoneNo,Validators.required],
      username:[this.user.userName,Validators.required],
      role:[this.user.role,Validators.required],
      location:[this.user.location,Validators.required]

    })
   }

  ngOnInit() {
    this.loadroledata();
    this.loadstoredata();
  }
  onclick(){
    console.log(this.staffcomp.modalService.dismissAll("click"));
    }
loadroledata(){
    this.userservice.getRoles(this.staffcomp.currentuser.companyID).subscribe(userservice=>{
     this.response=userservice;
     this.roles=<Roles[]> this.response;
    },error=>{

    })

  }
  loadstoredata(){
    this.companyservice.getcompanylocations(this.staffcomp.currentuser.companyID).subscribe(companyservice=>{
      this.locations=<Location[]>companyservice;
     },error=>{
 
     })
  }

  onsave(){
    var updated = {"firstName":this.editstaffform.controls.fname.value,"lastName":this.editstaffform.controls.lname.value,"email":this.editstaffform.controls.email.value,
  "phoneNo":this.editstaffform.controls.phoneno.value,"roleID":(<Roles>this.editstaffform.controls.role.value).id,"locationID":(<Location>this.editstaffform.controls.location.value).id,"id":this.user.id}
  this.userservice.updateuser(<User> updated ).subscribe(res=>{
    console.log(res);
    this.onclick();
    this.staffcomp.loadData();
  })
  }
}
