import { Component, OnInit } from '@angular/core';
import { ReportingService } from '../../services/reporting.service';
import { formatDate } from '@angular/common';
import { IStaffSummary } from '../../models/StaffSummary';
import { switchMap, tap, map } from 'rxjs/operators';
import { IPaymentSummary } from '../../models/PaymentSummary';
import * as numeral from 'numeral';
import { SalesService } from '../../services/sales.service';
import { Salesregister } from '../../models/salesregister';
import { Observable } from 'rxjs';
import { Payment } from '../../models/payment';
import { ExcelService } from '../../services/excel.service';
import {Formats} from '../../Classes/Formats'

@Component({
  selector: 'app-daily-business-overview',
  templateUrl: './daily-business-overview.component.html',
  styleUrls: ['./daily-business-overview.component.scss']
})
export class DailyBusinessOverviewComponent implements OnInit {

  totalChange = 0;
  format = new Formats();
  isSummarized: boolean = true;
  toggleText ="Generate Detailed Report";
  dataIsLoaded: boolean = false;
  dailySaleObj = {
    staffName: "",
    currentDate: Date.now(),
    locationName: "",
    totalSale: 0,
    revenue: 0,
    totalCash: 0,
    totalCard: 0,
    totalTransfer: 0,
    totalCreditNote: 0,
  }

  detailedReportData: Observable<Salesregister[]>;

  user;
  staff: IStaffSummary[];
  constructor(
    private saleService: SalesService,
    private reportService: ReportingService,
    private excelService: ExcelService
  ) { 
    this.user = JSON.parse(localStorage.getItem("CurrentUser"));
    
    const staffSale = this.reportService.getSalesSummaryByStaff(this.user.companyID, this.user.id, "0", "0");
    const paymentType = this.reportService.getpaymentssummary(this.user.companyID, this.user.locationID, "0", "0");

    const combined = staffSale.pipe(
      switchMap(staffSummarySale => {
        return paymentType
        .pipe(
          tap((paymentType: IPaymentSummary[]) => {
            staffSummarySale.forEach(sale => {
              this.dailySaleObj.totalSale = this.dailySaleObj.totalSale + sale.volume;
            })
            this.dailySaleObj.locationName = this.user.location.name;
            this.dailySaleObj.staffName = `${this.user.firstName} ${this.user.lastName}`;
            staffSummarySale.forEach(staffSummary => {
              this.dailySaleObj.revenue = this.dailySaleObj.revenue + staffSummary.value;

            })
            this.groupByPaymentMethod(paymentType, `${this.user.firstName} ${this.user.lastName}`)
          })
        )
      })
    );
    combined.subscribe();

    this.detailedReportData = this.saleService.getSaleByCashierByDate(this.user.id, "0", "0");
    this.dataIsLoaded = true;
    
  }

  ngOnInit() {
  }

  toggle() {
     this.isSummarized = !this.isSummarized;
      this.isSummarized? this.toggleText="Generate Detailed Report": this.toggleText ="Generate Summarized Report";
  }

   //To group the payment methods total value to display on payment outlet chart
   groupByPaymentMethod(payments: IPaymentSummary[], staffName: string){
     let index = 0;
     while(index < payments.length){
      if(staffName === payments[index].staff){
        let payment = payments[index];
         switch (payment.method){
           case 'cash':
             this.dailySaleObj.totalCash = this.dailySaleObj.totalCash + payment.total;
             break;
           case 'card':
           this.dailySaleObj.totalCard = this.dailySaleObj.totalCard + payment.total;
             break;
           case 'transfer':
             //this.paymentByInvoice.push(payment);
             this.dailySaleObj.totalTransfer = this.dailySaleObj.totalTransfer + payment.total;
             break;
           case 'creditnote':
           this.dailySaleObj.totalCreditNote = this.dailySaleObj.totalCreditNote + payment.total;
             break;
           default:  
         }
      }
      index = index+1;
     }
     
  }

 

  returnSortedPayment(payments: Payment[]){
    let paymentObj = {
      cash: 0,
      card: 0,
      transfer: 0,
      creditnote: 0
    }
    payments.forEach(payment => {
      switch(payment.method){
        case 'cash':
          paymentObj.cash = paymentObj.cash+ payment.amount;
          break;
        case 'card':
          paymentObj.card = paymentObj.card+  payment.amount;
          break;
        case 'transfer':
          paymentObj.transfer = paymentObj.transfer+ payment.amount;
          break;
        case 'creditnote':
          paymentObj.creditnote = paymentObj.creditnote+ payment.amount;
          break;
        default:
          return 0;
      }
    })
    return paymentObj;
  }

  returnSumOfChange(payments: Payment[]){
    let sum = 0;
    payments.forEach(payment => {
      sum = sum+payment.change;
    })
    this.totalChange = this.totalChange+sum;
    return this.formatPrice(sum);
  }
  formatPrice(price, dropDecimals = false) {
    return numeral(price).format(dropDecimals ? '0,0' : '0,0.00');
  }



  printTable(){
    var divToPrint = document.getElementById('tableRecord');
    var sec = document.getElementById("title");
    var htmlToPrint = '' +
        '<style type="text/css">' +
        'th, td {' +
        'padding:0.5em;' +
        'margin: none;'+
        'text-align: center;'+
        '}' +
        '}'+
          '#title {'+
          'font-size: 30px'+
          '}'+
        'table {'+
          'width: 100%;'+
          'border: 1px solid #000;'+
          '}'+
        '</style>';
    htmlToPrint = htmlToPrint+sec.outerHTML+divToPrint.outerHTML;
    let newWin = window.open("");
    newWin.document.write(htmlToPrint);
    newWin.print();
    newWin.close();
  }

  downloadAsPDF(){
    this.format.saveAsPDF(this.user.company.name, `${this.user.company.name}-${this.user.userName}-daily`, 'tableRecord')
  }
  exportASXLSX(){
    let reportData =[];
     this.detailedReportData.subscribe(
      data=> {
        console.log(data);
        reportData = data;
      }
    )
    console.log(reportData)
    this.excelService.exportAsExcelFile(reportData, `${this.user.company.name}-${this.user.userName}-daily`);
  }

}
