export class Price {
    costPrice:number;
    sellPrice:number;
    isDiscountable:boolean;
    discountType:string;
    discount:number;
    companyID:number;
    userCreated:number;
    id:number;

    constructor(cost:number,sell:number,discount:boolean,companyid:number){
   this.costPrice=cost;
   this.sellPrice=sell;
   this.isDiscountable=discount;
   this.companyID=companyid;

    }
}
