import { Injectable } from '@angular/core';
import { HttpHeaders } from "@angular/common/http";
import { AccountSetup } from "../models/accountSetup";
import { HttpClient } from "@angular/common/http";
import{AuthenticationService} from './authentication.service';

@Injectable({
  providedIn: 'root'
})
export class AccountsetupService {
  accountSetup: AccountSetup
  
  httpOptions = {
    headers: new HttpHeaders({
      'Content-Type':  'application/json-patch+json',
      'Authorization': 'my-auth-token'
    })
  };

  constructor(private http:HttpClient,private authservice:AuthenticationService) { }

setUp_acctURl = this.authservice.api+'AccountSetup/save'

setUp_acct(accountSetup: AccountSetup){
  return this.http.post(this.setUp_acctURl,accountSetup,this.httpOptions)
}

get_accstURL = this.authservice.api+'AccountSetup/GetByCompanyAndLocation'

get_single_acctURL = this.authservice.api+'AccountSetup/'

post_update_accountURL = this.authservice.api+'AccountSetup/update'

get_accts(cID,lID){
  return this.http.get(`${this.get_accstURL}/${cID}/${lID}`)
}

get_single_acct(id){
  return this.http.get(`${this.get_single_acctURL}${id}`)
}

post_update_account(accountSetup: AccountSetup){
  console.log(accountSetup)
  return this.http.post(this.post_update_accountURL,accountSetup,this.httpOptions);

}


}
