import { Component, OnInit } from '@angular/core';
import * as numeral from 'numeral';
import{Product} from '../../models/product';
import{User} from '../../models/user';
import{ProductService} from '../../services/product.service';
import { Category } from '../../models/category';
import { NgbModal, ModalDismissReasons } from '@ng-bootstrap/ng-bootstrap';
import { CommonService } from '../../services/common.service';
@Component({
  selector: 'app-products',
  templateUrl: './products.component.html',
  styleUrls: ['./products.component.scss','../../../vendor/styles/pages/products.scss',
  '../../../vendor/libs/ng2-nouislider/ng2-nouislider.scss']
})
export class ProductsComponent implements OnInit {
  products:Product[];
  currentuser:User;
  categories:Category[];
  loading=true;

  bulkType: string = "product";
  uploadSuccess: boolean = false;


  constructor(private productservice:ProductService, private modalService: NgbModal, private commonService: CommonService) { 
    this.currentuser=<User> JSON.parse(localStorage.getItem("CurrentUser"));
    this.loadcategories();
      this.loadData()}
   
  filterStatus = 'Any';

  // Options
  searchKeys = ['name', 'id'];
  sortBy = 'id';
  sortDesc = true;
  perPage = 10;

  filterVal = '';
  currentPage = 1;
  totalItems = 0;
  templateLink = "https://mercatorstorage.blob.core.windows.net/blobcontainer/EmailTemplates/Mercator Product template.xlsx";

  productsData: Product[] = [];
  originalProductsData: Product[] = [];

  

  formatPrice(price, dropDecimals = false) {
    return numeral(price).format(dropDecimals ? '0,0' : '0,0.00');
  }

  formatNumber(value) {
    return numeral(value).format('0,0');
  }
 loadcategories(){
  this.productservice.getcategoriesbycompany(this.currentuser.companyID).subscribe(data=>{
    const response=data;
    this.categories=<Category[]> data;
  })
 }
  loadData() {
    this.productservice.getproducts(this.currentuser.companyID).subscribe(data=>{
      const response=data;
      this.originalProductsData=<Product[]> data;
      this.productsData=this.originalProductsData;
      //this.productsData=  this.productsData.filter(product=>product.locationID===this.currentuser.locationID);
      this.update();
      this.loading=false;
    })
  }

  removeProduct(productID: number){
    this.productservice.deleteProductByID(productID).subscribe(data => {
      this.loadData();
    });
  }

  get totalPages() {
    return Math.ceil(this.totalItems / this.perPage);
  }
  update() {
    const data = this.filter(this.originalProductsData);

    this.totalItems = data.length;

    this.sort(data);
    this.productsData = this.paginate(data);
  }

  filter(data) {
    const filter = this.filterVal.toLowerCase();
    return !filter ?
      data.slice(0) :
      data.filter(d => {
        return Object.keys(d)
          .filter(k => this.searchKeys.includes(k))
          .map(k => String(d[k]))
          .join('|')
          .toLowerCase()
          .indexOf(filter) !== -1 || !filter;
      });
  }

  sort(data) {
    data.sort((a: any, b: any) => {
      a = typeof(a[this.sortBy]) === 'string' ? a[this.sortBy].toUpperCase() : a[this.sortBy];
      b = typeof(b[this.sortBy]) === 'string' ? b[this.sortBy].toUpperCase() : b[this.sortBy];

      if (a < b) { return this.sortDesc ? 1 : -1; }
      if (a > b) { return this.sortDesc ? -1 : 1; }
      return 0;
    });
  }

  paginate(data) {
    const perPage = parseInt(String(this.perPage), 10);
    const offset = (this.currentPage - 1) * perPage;

    return data.slice(offset, offset + perPage);
  }

  setSort(key) {
    if (this.sortBy !== key) {
      this.sortBy = key;
      this.sortDesc = false;
    } else {
      this.sortDesc = !this.sortDesc;
    }

    this.currentPage = 1;
    this.update();
  }


  ngOnInit() {
    this.commonService.productObs.subscribe((response) =>{
      this.uploadSuccess = true;
      setTimeout(() => {
        this.uploadSuccess = false;
      }, 3000);
      this.loadData()
    } );
  }
  

  open(content, options = {}) {
    this.modalService.open(content, options).result.then((result) => {
      console.log(`Closed with: ${result}`);
    }, (reason) => {
      console.log(`Dismissed ${this.getDismissReason(reason)}`);
    });
  }

  private getDismissReason(reason: any): string {
    if (reason === ModalDismissReasons.ESC) {
      return 'by pressing ESC';
    } else if (reason === ModalDismissReasons.BACKDROP_CLICK) {
      return 'by clicking on a backdrop';
    } else {
      return  `with: ${reason}`;
    }
  }

}
