import { Component, OnInit, AfterViewChecked, ChangeDetectorRef } from '@angular/core';
import { NgbModal, ModalDismissReasons } from '@ng-bootstrap/ng-bootstrap';
import { Router, ActivatedRoute } from '@angular/router';
import { Supplier } from '../../models/supplier';
import { SuppliersService } from '../../services/suppliers.service';
import { User } from '../../models/user'

@Component({
  selector: 'app-allsuppliers',
  templateUrl: './allsuppliers.component.html',
  styleUrls: ['./allsuppliers.component.scss',
  '../../../vendor/styles/pages/contacts.scss']
})
export class AllsuppliersComponent implements OnInit {
  suppliers:Supplier[]
  currentuser:User
  choosensupplier:Supplier
  filterval=""
  filteredsuppliers:Supplier[]
  show=false

  constructor(private supplierservice:SuppliersService, public modalService: NgbModal, public router:Router, private route:ActivatedRoute, private cdr:ChangeDetectorRef) { 
    this.currentuser=<User> JSON.parse(localStorage.getItem("CurrentUser"))
  }

  ngOnInit() {
    this.loaddata()
  }

  viewMode = 'col'

  loaddata(){
    this.supplierservice.getsuppliers(this.currentuser.companyID).subscribe(data=>{
      this.suppliers=<Supplier[]>data
      this.filteredsuppliers=<Supplier[]>JSON.parse(JSON.stringify(this.suppliers))
    })
  }

  open(content, options = {}) {
    this.modalService.open(content, options).result.then((result)=>{
      console.log(`Closed with: ${result}`)
    }, (reason)=>{
      console.log(`Dismissed ${this.getDismissReason(reason)}`)
    })
  }

  private getDismissReason(reason: any): string {
    if (reason === ModalDismissReasons.ESC) {
      return 'by pressing ESC';
    } else if (reason === ModalDismissReasons.BACKDROP_CLICK) {
      return 'by clicking on a backdrop';
    } else {
      return  `with: ${reason}`;
    }
  }
  get modalservice(){
    return this.modalService;
  }

  filtersuppliers(){
    if(this.filterval.length===0){
      this.filteredsuppliers=<Supplier[]>JSON.parse(JSON.stringify(this.suppliers))
    }
    this.filteredsuppliers=this.suppliers.filter(sup=>sup.name.toLowerCase().includes(this.filterval.toLowerCase()))
  }

}
