import { Component, OnInit } from "@angular/core";
import { UserService } from "../services/user.service";
import { User } from "../models/user";
import { AuthenticationService } from "../services/authentication.service";
import {
  FormBuilder,
  FormGroup,
  Validators,
  FormControl
} from "@angular/forms";
import { error } from "@angular/compiler/src/util";
import { Router } from "@angular/router";
import { Response } from "../models/response";

@Component({
  selector: "app-register",
  templateUrl: "./register.component.html",
  styleUrls: ["./register.component.scss"]
})
export class RegisterComponent implements OnInit {
  registerform: FormGroup;
  currentuser: object;
  submitted = false;
  mismatch = false;
  useid: number;
  loading: boolean = false;

  constructor(
    private formbuilder: FormBuilder,
    public router: Router,
    private userservice: UserService,
    private authservice: AuthenticationService
  ) {
    this.registerform = formbuilder.group({
      firstname: ["", Validators.required],
      lastname: ["", Validators.required],
      email: ["", [Validators.required, Validators.email]],
      phoneno: ["", [Validators.required]],
      username: ["", Validators.required],
      password: ["", [Validators.required, Validators.minLength(6)]],
      repassword: [""]
    });
  }

  ngOnInit() {}

  customValidator() {
    return (
      this.registerform.controls.password.value ===
      this.registerform.controls.repassword.value
    );
  }

  onregister() {
    this.loading = true;
    this.submitted = true;
    if (this.registerform.invalid) {
      return;
    }
    this.userservice
      .registerUser(
        this.registerform.controls.firstname.value,
        this.registerform.controls.lastname.value,
        this.registerform.controls.email.value,
        this.registerform.controls.phoneno.value,
        this.registerform.controls.username.value,
        this.registerform.controls.password.value
      )
      .subscribe(
        userservice => {
          this.useid = (<User>(<Response>userservice).message).id;
          this.userservice.getUser(this.useid).subscribe(
            userservice => {
              this.currentuser = userservice;
              console.log(this.currentuser);
              localStorage.setItem(
                "Name",
                (<User>this.currentuser).firstName +
                  " " +
                  (<User>this.currentuser).lastName
              );
              localStorage.setItem(
                "CurrentUser",
                JSON.stringify(this.currentuser)
              );
              this.router.navigateByUrl("/welcome");
              this.loading = false;
            },
            error => {
              return;
            }
          );
        },
        error => {
          this.loading = false;
          return;
        }
      );
  }
}
