import { Injectable } from '@angular/core';
import{HttpClient, HttpHeaders} from '@angular/common/http';
import{Cart} from '../models/cart';
import{Invoice} from '../models/invoice';
import { Salesregister } from '../models/salesregister';
import{AuthenticationService} from './authentication.service';
import { Payment } from '../models/payment';
import { Observable } from 'rxjs';
import { KitchenCart } from '../models/kitchencart';

@Injectable({
  providedIn: 'root'
})
export class SalesService {

  constructor(private httpclient:HttpClient,private authservice:AuthenticationService) { }

  savecart(cart:Cart){
    return this.httpclient.post(this.authservice.api+"Cart/save",cart);
  }
  saveinvoice(invoice:Invoice){
    return this.httpclient.post(this.authservice.api+"Invoice/save",invoice);

  }
  updateinvoice(invoice:Invoice){
    return this.httpclient.post(this.authservice.api+"Invoice/update",invoice);
  }
  savepayment(payment:Payment){
    return this.httpclient.post(this.authservice.api+"Payment/save",payment);
    
  }
  updatepayment(payment:Payment){
    return this.httpclient.post(this.authservice.api+"Payment/update",payment);
  }
  getinvoice(id:number){
    return this.httpclient.get(this.authservice.api+"Invoice/"+id);
  }
  savesale(sale:Salesregister){

     return this.httpclient.post(this.authservice.api+"SaleRegister/save",sale);
  }
  getsalebycompany(id:number,start:any,end:any){
    return this.httpclient.get(this.authservice.api+"SaleRegister/GetByCompany/"+id+"/"+start+"/"+end);
  }
  deletesale(saleid:number,authcode:any){
    return this.httpclient.post(this.authservice.api+"SaleRegister/delete?ID="+saleid+"&authCode="+authcode,{})
  }
  getsalebylocation(cid:number,lid:number,start:any,end:any){
    return this.httpclient.get(this.authservice.api+"SaleRegister/GetByCompanyAndLocation/"+cid+"/"+lid+"/"+start+"/"+end);
  }
  getsalebycustomer(cid:number,cusid:number,start:any,end:any){
    
    return this.httpclient.get(this.authservice.api+"SaleRegister/GetByCompanyAndCustomer/"+cid+"/"+cusid+"/"+start+"/"+end);
  }
  getsalebycashier(cashid:number){
    return this.httpclient.get(this.authservice.api+"SaleRegister/GetByUser/"+cashid);
  }
  //service by Tosin
  getSaleByCashierByDate(cashierID: number, startDate: string, endDate: string): Observable<Salesregister[]>{
    return this.httpclient.get<Salesregister[]>(this.authservice.api+`SaleRegister/GetByUser/${cashierID}/${startDate}/${endDate}`)
  }
  //end of service by Tosin
  getsavedcart(cid:number,lid:number,code:string){
    return this.httpclient.get(this.authservice.api+"Cart/GetByCode/"+cid+"/"+lid+"/"+code);
  }
  updatecart(cart:Cart){
    return this.httpclient.post(this.authservice.api+"Cart/update",cart);
  }
  deletecart(cartid:number){
    return this.httpclient.post(this.authservice.api+"Cart/delete?ID="+cartid,{});
  }
  
  //kitchen order
  savekitchencart(kitchenCart){
    return this.httpclient.post(this.authservice.api+"KitchenOrder/save",kitchenCart)
  }
  updatekitchencart(kitchenCart:KitchenCart){
    return this.httpclient.post(this.authservice.api+"KitchenOrder/update",kitchenCart)
  }
  getktichencartbycompanyid(id){
    return this.httpclient.get(this.authservice.api+"KitchenOrder/GetByCompany/"+id)
  }

  getktichencartbycompanyidanddate(id){
    return this.httpclient.get(this.authservice.api+"KitchenOrder/GetByCompany/"+id+"/"+0+"/"+0)
  }

  //kitchen cart
  savekitchencartt(kitchenCart){
    return this.httpclient.post(this.authservice.api+"KitchenCart/save",kitchenCart)
  }
  getktichencarttbycompanyid(cid,lid,code){
    return this.httpclient.get(this.authservice.api+"KitchenCart/GetByCode/"+cid+"/"+lid+"/"+code)
  }
  updatekitchencartt(kitchenCart:KitchenCart){
    return this.httpclient.post(this.authservice.api+"KitchenCart/update",kitchenCart)
  }

  //stock
  getProductStockById(id){
    return this.httpclient.get(this.authservice.api+"Stock/GetByProduct/"+id)
  }

}
