import { Category } from "./category";
import{Subcategory} from "./subcategory";
import { Location } from "./location";
import { Price } from "./price";
import {Tag} from "./tag";
import { Supplier } from "./supplier";
import { Tax } from "./tax";
import { Unit } from "./unit";
import { Vendor } from "./vendor";

export class Product {
    name:string;
    brand:string;
    type:string;
    code:string;
    upc:string;
    ean:string;
    isbn:string;
    mpn:string;
    reOrderLevel:string;
    units:Unit[];
    sku:string;
    handle:string;
    purchaseDate:string;
    categoryID:number;
    subCategoryID:number;
    locationID:number;
    supplierID:number;
    vendorID:number;
    priceID:number;
    taxID:number;
    price:Price;
    description:string;
    quantity:number;
    tags:Tag[];
    category:Category;
    subCategory:Subcategory;
    location:Location;
    supplier:Supplier;
    vendor:Vendor[];
    tax:Tax;
    companyID:number;
    id:number;
    unitOfMeasure:string;
    imgURL:string
   

constructor(name:string,code:string, upc:string, ean:string, isbn:string, mpn:string, reOrderLevel:string, brand:string,quantity:number,description:string,category:number,location:number,price:Price,id:number,img:string){
    this.name=name;
    this.code=code;
    this.upc=upc;
    this.ean=ean;
    this.isbn=isbn;
    this.mpn=mpn;
    this.reOrderLevel=reOrderLevel;
    this.brand=brand;
    this.quantity=quantity;
    this.description=description;
    this.categoryID=category;
    this.locationID=location;
    this.price=price;
    this.companyID=id;
    this.imgURL=img

}
}
