import { Component, OnInit } from '@angular/core';
import {StockService} from '../../services/stock.service';
import { Stock } from '../../models/stock';
import { User } from '../../models/user';
import { map } from 'rxjs/operators'
@Component({
  selector: 'app-stockadjust',
  templateUrl: './stockadjust.component.html',
  styleUrls: ['./stockadjust.component.scss','../../../vendor/libs/ngx-sweetalert2/ngx-sweetalert2.scss']
})
export class StockadjustComponent implements OnInit {
  
  disabled=true;
  cantupdate=true;
  stock:Stock[];
  currentuser:User;
  revisedstock:Stock[];
  loading=true;
  errormsg=""
  error=false;
  constructor(private stockservice:StockService) { 
    this.currentuser=<User> JSON.parse(localStorage.getItem("CurrentUser"));
   this.loadstock();
  }

 

  ngOnInit() {
  }
  displayerror(){
    this.error=true;
    setTimeout(()=>this.error=false,20000);
  }
  oncount(){
  this.disabled=false;
  this.stock=this.stock.filter(stock=>stock.locationID===this.currentuser.locationID);
  this.revisedstock=<Stock[]> JSON.parse(JSON.stringify(this.stock))
  }
  showupdates(){
    this.stock=this.stock.filter(stock=>this.revisedstock[this.stock.indexOf(stock)].quantity!==stock.quantity);
    this.revisedstock=this.revisedstock.filter(stock=>{for(let s of this.stock){
      if(s.id==stock.id){
        return stock;
      }
    }});
    this.cantupdate=false;
  }
 updatestock(){
    for(let stock of this.stock){
     
      if(this.revisedstock[this.stock.indexOf(stock)].quantity>stock.quantity){
        var operator ="+"
        var difference = this.revisedstock[this.stock.indexOf(stock)].quantity-stock.quantity
      }else{
        var operator="-"
        var difference =stock.quantity- this.revisedstock[this.stock.indexOf(stock)].quantity
      }
    this.stockservice.adjuststock(stock.id,difference.valueOf(),operator).subscribe(response=>{
       if(this.stock.indexOf(stock)===this.stock.length-1){
          this.disabled=true;
           this.loadstock();
           this.cantupdate=true;

       }
     },error=>{
      this.errormsg=(<Response> error).statusText;
      this.displayerror();
     })
    }
   
  }
  loadstock(){
    this.loading=true;
    this.cantupdate=true;
  this.stockservice.getstock(this.currentuser.companyID).subscribe(data=>{
    this.stock=<Stock[]> data;
    this.revisedstock=<Stock[]> JSON.parse(JSON.stringify(this.stock))
    this.loading=false;
  },error=>{
    this.loading=false;
    this.errormsg=(<Response> error).statusText;
    this.displayerror();
  })
  }
  


    
}