import { Component, OnInit } from '@angular/core';
import { NgbModal, NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { ExpenseManagementService } from '../../services/expense-management.service';
import { NgForm, FormControl, NgModel, FormBuilder, Validators } from '@angular/forms';
import { Observable } from 'rxjs';
import { IExpenseCategory } from '../../models/expenseManagement';
import { animate, trigger, transition, query, stagger, style } from '@angular/animations';


@Component({
  selector: 'app-expense-category',
  templateUrl: './expense-category.component.html',
  animations: [
    trigger('listAnimation', [
      transition('* => *', [ // each time the binding value changes
        query(':leave', [
          stagger(100, [
            animate('0.5s', style({ opacity: 0 }))
          ])
        ], { optional: true }),
        query(':enter', [
          style({ opacity: 0 }),
          stagger(100, [
            animate('0.5s', style({ opacity: 1 }))
          ])
        ], { optional: true })
      ])
    ])
  ],
  styleUrls: ['./expense-category.component.scss']
})
export class ExpenseCategoryComponent implements OnInit {
  user;
  expenseCategories: IExpenseCategory[]=[];
  category;
  loading: boolean = true;

  constructor(
    private modalService: NgbModal,
    private expenseService: ExpenseManagementService,
    private fb: FormBuilder
  ) { 
    this.user = JSON.parse(localStorage.getItem("CurrentUser"));
    this.loadExpenseCategories();
   }

  expenseCategory  = this.fb.group({
    description: ['', [Validators.required]],
    status: [true],
  })

  ngOnInit() {
  }

  loadExpenseCategories(){
    this.loading = true;
    this.expenseService.getExpenseCategoryByCompanyID(this.user.companyID)
    .subscribe(data=> {this.expenseCategories = data; this.loading = false;});
  }

  saveCategory(){
    this.expenseCategory.addControl('companyID', this.fb.control(this.user.companyID, Validators.required));
 
     this.expenseService.saveExpenseCategory(this.expenseCategory.value).subscribe(data => {
      this.modalService.dismissAll('click');
     this.loadExpenseCategories();
   });
  }

  confirmDeleteCategory(name: string, category){
     this.modalService.open(name, {centered:true});
     this.category = category;
     
    
  }
  deleteCategory(){
    this.expenseService.deleteExpenseCategory(this.user.companyID, this.category.id)
    .subscribe(data => {
      this.loadExpenseCategories();
    })
    this.modalService.dismissAll('click');
  }

  updateForm(updateForm, category){
    this.category = category;
    this.modalService.open(updateForm);
    this.expenseCategory.patchValue({description: category.description, status: category.status});
  }

  updateCategory(){
    this.expenseCategory.addControl('id', this.fb.control(this.category.id));
    this.expenseCategory.addControl('companyID', this.fb.control(this.user.companyID));
    // console.log(this.expenseCategory.value);
    this.expenseService.updateExpenseCategory(this.expenseCategory.value)
    .subscribe(data =>{
      this.loadExpenseCategories();
    this.modalService.dismissAll('click');
    } )
  }

  

  open(content, options = {}) {
    this.expenseCategory.get('description').reset();
    this.modalService.open(content, options).result.then((result) => {
    this.expenseCategory.get('description').reset();
      console.log(`Closed with: ${result}`);
    }, (reason) => {

    });
  }

}
