import { Component, OnInit } from '@angular/core';
import { ReportingService} from '../../services/reporting.service';
import { User } from '../../models/user';
import { Location} from '../../models/location';
import { formatDate } from '@angular/common';
import { UserService } from '../../services/user.service';
import { ILocationSummary } from '../../models/LocationSummary';
import { IStaffSummary } from '../../models/StaffSummary';
import { map } from 'rxjs/operators';
import { ExcelService } from '../../services/excel.service';
import { Formats } from '../../Classes/Formats';


@Component({
  selector: 'app-sales-report',
  templateUrl: './sales-report.component.html',
  styleUrls: ['./sales-report.component.scss',  '../../../vendor/libs/ngx-sweetalert2/ngx-sweetalert2.scss','../../../vendor/libs/ng-select/ng-select.scss','../../../vendor/libs/angular-2-dropdown-multiselect/angular-2-dropdown-multiselect.scss']
})
export class SalesReportComponent implements OnInit {
  format = new Formats();
  loading = true;
  isLocation = true;
  isCashier = false;
  isCustomer = false;
  user:User;
  summaryData: ILocationSummary[] | IStaffSummary[];
  currentDate = new Date();

  locations: ILocationSummary[];
  staffSales: IStaffSummary[];
  selectfrom: any[] = [];
  cashiersearch=false;
  searchbyterm:  Object | any;

  startDate;
   endDate;

  constructor(
    private reportService: ReportingService,
    private userservice:UserService,
    private exportService: ExcelService
  ) {
    this.user=<User> JSON.parse(localStorage.getItem("CurrentUser"));
    this.reportService.getSalesSummaryByLocation(this.user.companyID, 0, this.getCurrentDate(), this.getCurrentDate())
      .subscribe(summaryByLocation => {
        this.summaryData = summaryByLocation;
        this.loading = false;
      })
    
   }

  ngOnInit() {
    
  }

  getCurrentDate(){
    return formatDate(this.currentDate,'dd-MM-yyyy','en').toString();
  }


  filterStatus = 'Any';
  searchKeys = ['saleDate','volume', 'value', 'locationName, soldBy'];


  filterVal = '';
  currentPage = 1;
  totalItems = 0;



  filterchange(){
    
   if(this.filterStatus==="Branch"){
    this.cashiersearch=false;
    this.reportService.getLocationsByCompanyID(this.user.companyID).pipe(
      map((locations: any[]) => {
        locations.push({
          name: "All",
          id: 0
        })
        locations.reverse();
        this.selectfrom = locations;
      })
    ).subscribe()
    }else if(this.filterStatus==="Customer"){

    
    }else if(this.filterStatus=="Cashier"){
    this.cashiersearch=true;
    this.userservice.getCashiers(this.user.companyID).subscribe(data=>{
     
      this.selectfrom=<User[]> data;
    })
    this.userservice.getUsers(this.user.companyID).pipe(
      map((users: any[]) => {
        users.push({
          firstName: "All",
          id: 0
        });
        users.reverse();
        this.selectfrom=users;
      })
    ).
    subscribe();
  }

}

  //return sum of each column
  getTotal(data: any){
    let totals = {
      totalVol: 0,
      totalVal: 0
    }
    data.forEach(element => {
      totals.totalVol = totals.totalVol+ element.volume;
      totals.totalVal = totals.totalVal+ element.value;
    });
    return totals;
  }

  logIt(event){
    console.log(event);
  }

onfilter(){
  var startDate = this.startDate ===undefined  || this.startDate.trim() ==""?undefined: formatDate(this.startDate, 'dd-MM-yyyy', 'en');
      var endDate = this.endDate ===undefined  || this.endDate.trim() ==""?undefined: formatDate(this.endDate, 'dd-MM-yyyy', 'en');


      if((typeof startDate !== "undefined" || startDate !="") && (typeof endDate === "undefined" || endDate == "")){
        endDate = "0";
      }else if((typeof startDate === "undefined" || startDate == "") && (typeof endDate !== "undefined" || endDate != "")){
        startDate = "0";
      }else if((typeof startDate === "undefined" || startDate == "") &&  (typeof endDate === "undefined" || endDate == "")){
        startDate = "0", endDate = "0";
      }

  switch (this.filterStatus) {
    case "Branch":
        this.isLocation = true;
        this.isCashier = false;
        this.loading = true;
        this.reportService.getSalesSummaryByLocation(this.user.companyID, this.searchbyterm.id, startDate, endDate)
      .subscribe(summaryByLocation => {
        this.summaryData = summaryByLocation;
        this.loading = false;
      })
        break;
    
    case "Cashier":
      this.isLocation = false;
      this.isCashier = true;
      this.loading = true;
      this.reportService.getSalesSummaryByStaff(this.user.companyID, this.searchbyterm.id, startDate, endDate)
      .subscribe(summaryByStaff => {
        this.summaryData = summaryByStaff;
        console.log(this.summaryData);
        this.loading = false;
      });
      break;

    case "Customer":
      this.loading = true;
      //Awaiting API for getSalesSummary by Customer
  
    default:
      break;
  }

}

  getLocations(){
    this.reportService.getLocationsByCompanyID(this.user.companyID).subscribe(
      locations => {
        this.selectfrom = locations;
      } 
    )
  }

    

  getSalesSummaryByStaff(){
    this.reportService.getSalesSummaryByStaff(this.user.companyID, 0, "04-01-2019", "20-05-2019")
    .subscribe(summary => console.log(summary))
  }

formatdate(date){
    return formatDate(date,'d MMM yy','en'); 
  }


  printTable(){
    var divToPrint = document.getElementById('tableRecord');
    var htmlToPrint = '' +
        '<style type="text/css">' +
        'table th, table td {' +
        'padding:0.5em;' +
        'text-align: center;'+
        'border: 1px solid #000;'+
        '}' +
        'table {'+
          'width: 100%;'+
          'border: 1px solid #000;'+
          '}'+
        '</style>';
    htmlToPrint = htmlToPrint+ divToPrint.outerHTML;
    let newWin = window.open("");
    newWin.document.write(htmlToPrint);
    newWin.print();
    newWin.close();
    
  }

  exportASXLSX(){
    this.exportService.exportAsExcelFile(this.summaryData, `${this.user.company.name}-sales-report`);
  }
  downloadAsPDF(){
    this.format.saveAsPDF(this.user.company.name, 'sale-report', 'tableRecord');
    
  }
}
