import { Component, OnInit } from '@angular/core';
import { NgForm } from '@angular/forms';
import { ServiceDataService } from '../../services/service-data.service';
import * as numeral from 'numeral';
import {ServiceObj} from '../../models/service-data';
import { HttpErrorResponse } from '@angular/common/http';
import { NgbModal, ModalDismissReasons } from '@ng-bootstrap/ng-bootstrap';
import { User } from '../../models/user';
import { CommonService } from '../../services/common.service';

@Component({
  selector: 'app-service',
  templateUrl: './service.component.html',
  styleUrls: ['./service.component.scss']
})
export class ServiceComponent implements OnInit {

  service: ServiceObj;
    serviceDetails: ServiceObj;
    services: ServiceObj[] = [];
    currentuser:User;
    templateLink: string = "https://mercatorstorage.blob.core.windows.net/blobcontainer/EmailTemplates/Simplify service template.xlsx"
  
  filterVerified = 'Any';
  filterRole = 'Any';
  filterStatus = 'Any';
  filterLatestActivity = [null, null];

  bulkType: string = 'service';
  uploadSuccess: boolean = false;
  

  constructor(
    private dataService: ServiceDataService,private modalService: NgbModal,private commonService: CommonService
  ) { 
    this.currentuser=<User> JSON.parse(localStorage.getItem("CurrentUser"));
  }
  ngOnInit() {
   this.loaddata();
   this.commonService.serviceObs.subscribe(response => {
     this.uploadSuccess = true;
     this.loaddata();
     setTimeout(() => {
       this.uploadSuccess = false;
     }, 3000);
   });

  }
  searchKeys = ['id', 'name'];
  sortBy = 'id';
  sortDesc = true;
  perPage = 10;

  filterVal = '';
  currentPage = 1;
  totalItems = 0;

  usersData: ServiceObj[] = [];
  originalUsersData: ServiceObj[] = [];
  
 loaddata(){
  this.dataService.getAllServicesByCompanyID(this.currentuser.companyID).subscribe(data=>{
    const response=data;
    this.originalUsersData=<ServiceObj[]>response;
    this.update();
  },error=>{

  })
 }


 formatPrice(price, dropDecimals = false) {
  return numeral(price).format(dropDecimals ? '0,0' : '0,0.00');
  }

  open(content, options = {}) {
    this.modalService.open(content, options).result.then((result) => {
      console.log(`Closed with: ${result}`);
    }, (reason) => {
      console.log(`Dismissed ${this.getDismissReason(reason)}`);
    });
  }

  private getDismissReason(reason: any): string {
    if (reason === ModalDismissReasons.ESC) {
      return 'by pressing ESC';
    } else if (reason === ModalDismissReasons.BACKDROP_CLICK) {
      return 'by clicking on a backdrop';
    } else {
      return  `with: ${reason}`;
    }
  }
  

  addService(newServiceForm: NgForm){
    this.service =new ServiceObj(newServiceForm.value);
    this.service.companyID=this.currentuser.companyID;
    this.dataService.postService(this.service)
    .subscribe(res => {
      this.services.push(this.service);
     this.loaddata();
      newServiceForm.reset(); 
      this.modalService.dismissAll('click');
    }, (error: HttpErrorResponse) => {
      console.log(error)
    })
  }

  deleteService(serviceID: number){
    console.log(serviceID);
    this.dataService.deleteServiceByID(serviceID).subscribe(
      data=> {
        this.loaddata();
        console.log(data);
    console.log(serviceID);

      }
    )
  }

  displayEditServiceFormByID(serviceID: number){
    this.dataService.getServiceByID(serviceID)
    .subscribe(data => this.service = data);
  }

  showServiceDetailsByID(serviceID: number){
    this.dataService.getServiceByID(serviceID)
    .subscribe(serviceDetails => {
      this.serviceDetails = serviceDetails;
    })
  }

  updateServiceFormByID(serviceUpdate, id: number){
    this.service = new ServiceObj(serviceUpdate);
    this.service.id = id;
    this.dataService.updateService(this.service).subscribe(() => {
    this.loaddata();
      this.modalService.dismissAll('click');
    },((error: HttpErrorResponse) => {
      console.log(error);
    }));
    
  }
  get totalPages() {
    return Math.ceil(this.totalItems / this.perPage);
  }

  update() {
    const data = this.filter(this.originalUsersData);

    this.totalItems = data.length;

    this.sort(data);
    this.usersData = this.paginate(data);
  }

  filter(data) {
    const filter = this.filterVal.toLowerCase();
    return !filter ?
      data.slice(0) :
      data.filter(d => {
        return Object.keys(d)
          .filter(k => this.searchKeys.includes(k))
          .map(k => String(d[k]))
          .join('|')
          .toLowerCase()
          .indexOf(filter) !== -1 || !filter;
      });
  }

  sort(data) {
    data.sort((a: any, b: any) => {
      a = typeof(a[this.sortBy]) === 'string' ? a[this.sortBy].toUpperCase() : a[this.sortBy];
      b = typeof(b[this.sortBy]) === 'string' ? b[this.sortBy].toUpperCase() : b[this.sortBy];

      if (a < b) { return this.sortDesc ? 1 : -1; }
      if (a > b) { return this.sortDesc ? -1 : 1; }
      return 0;
    });
  }

  paginate(data) {
    const perPage = parseInt(String(this.perPage), 10);
    const offset = (this.currentPage - 1) * perPage;

    return data.slice(offset, offset + perPage);
  }

  setSort(key) {
    if (this.sortBy !== key) {
      this.sortBy = key;
      this.sortDesc = false;
    } else {
      this.sortDesc = !this.sortDesc;
    }

    this.currentPage = 1;
    this.update();
  }

}
