import {Injectable} from '@angular/core';
import { Router, CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot } from '@angular/router';
import {AuthenticationService} from './services/authentication.service';
import { User } from './models/user';
//import {Roles} from './roles';
import { Priviledge } from './models/priviledge';

@Injectable({ providedIn: 'root' })
export class Authguard implements CanActivate {
    currentuser:User;
    privilege
    constructor(private router:Router,private authservice:AuthenticationService){

    }
    canActivate(route:ActivatedRouteSnapshot,state:RouterStateSnapshot){
        
        //return true
        if(this.authservice.checkstatus()){
            this.currentuser=<User> JSON.parse(localStorage.getItem("CurrentUser"))
            this.privilege = this.currentuser.role.privileges.find(x=>{return x.name==route.data.name})
            if(this.currentuser.role.name=="Super Admin"){
                return true
            }
            else if(this.privilege.read==true){
                return true
            }
            else{
                return false
            }
        }
          
    }
   
    
}





