import { Component, OnInit } from '@angular/core';
import {StoreComponent} from '../store/store.component';
import{FormBuilder,FormGroup,Validators} from '@angular/forms';
import{Location} from '../../models/location';
import{Address} from '../../models/address';
import{User} from '../../models/user';
import{CompanyService} from '../../services/company.service';
import { Country } from '../../models/country';
import { State } from '../../models/state';
import { Lga } from '../../models/lga';
@Component({
  selector: 'app-addstore',
  templateUrl: './addstore.component.html',
  styleUrls: ['./addstore.component.scss','../../../vendor/libs/ng-select/ng-select.scss','../../../vendor/libs/angular-2-dropdown-multiselect/angular-2-dropdown-multiselect.scss']
})
export class AddstoreComponent implements OnInit {
 addstoreform:FormGroup;
 address:Address;
 location:Location;
 currentuser:User;
 countries:Country[];
  states:State[];
  lga:Lga[];

  constructor(private storecomp:StoreComponent,private formbuilder:FormBuilder,private companyservice:CompanyService) {
    this.currentuser= <User> JSON.parse(localStorage.getItem("CurrentUser"));
   this.addstoreform=this.formbuilder.group({
     bname:['',Validators.required],
     address1:['',Validators.required],
     address2:[''],
     city:['',Validators.required],
     country:[,Validators.required],
     lga:[,Validators.required],
     state:[,Validators.required],
     bnumber:['',Validators.required]
  

   }

   )
   
   }

  ngOnInit() {
  this.loadcountries();
    
  }

  loadcountries(){
    this.companyservice.getcountries().subscribe(data=>{
      this.countries=<Country[]> data;
    })
  }
  onchange(){
    this.companyservice.getstates((<Country>this.addstoreform.controls.country.value).id).subscribe(data=>{
      this.states=<State[]> data;
    })
   }
   onchange2(){
    this.companyservice.getlga((<State>this.addstoreform.controls.state.value).id).subscribe(data=>{
      this.lga=<Lga[]> data;
    })
  }
  onclick(){
    console.log(this.storecomp.modalService.dismissAll("click"));
    }
    onsave(){
      if(this.addstoreform.invalid){
        return;
       }else{
      this.address=new Address(this.addstoreform.controls.address1.value+this.addstoreform.controls.address2.value,this.addstoreform.controls.city.value,(<Lga>this.addstoreform.controls.lga.value).id,(<State>this.addstoreform.controls.state.value).id,(<Country>this.addstoreform.controls.country.value).id);
      this.location=new Location(this.addstoreform.controls.bname.value,this.addstoreform.controls.bnumber.value,this.address,this.currentuser.companyID);
       this.companyservice.createcompanylocation(this.location).subscribe(companyservice=>{
        const response=companyservice;
        this.onclick();
        this.storecomp.loadstores();
       },error=>{
         console.log("failed");
       })
       }
    }
    }


