export class Payment {
    id:number;
    customerID:number;
    companyID:number;
    amount:number;
    method:string;
    reference:string;
    datePaid:string;
    invoiceNo:string;
    change:number;
    userCreated:number;
    saleRegisterID:number;
    accountID: number
    constructor(customerid:number,companyid:number,amount:number,method:string,change:number,date:string){
        this.customerID=customerid;
        this.companyID=companyid;
        this.amount=amount;
        this.method=method;
        this.change=change;
        this.datePaid=date;
    }
}
