import { Component, OnInit } from '@angular/core';
import{ActivatedRoute} from '@angular/router';
import{CompanyService} from '../../services/company.service';
import { NgbModal, ModalDismissReasons } from '@ng-bootstrap/ng-bootstrap';
import * as numeral from 'numeral';
import { Location } from '../../models/location';
@Component({
  selector: 'app-viewstore',
  templateUrl: './viewstore.component.html',
  styleUrls: ['./viewstore.component.scss','../../../vendor/styles/pages/users.scss']
})
export class ViewstoreComponent implements OnInit {
  store:Location;

  constructor(public modalService: NgbModal,private route:ActivatedRoute,private companyservice:CompanyService) { 
    this.store= new Location("","",null,0);
    this.getstore();
  }
 
  ngOnInit() {
  }
  getstore(){
    const id=+this.route.snapshot.paramMap.get('id');
   this.companyservice.getcompanylocation(id).subscribe(companyservice=>{
     const response=companyservice;
     this.store=<Location> response;
   },error=>{

   })

  }

  formatInt (v) {
    return numeral(v).format('0,0');
  }

  open(content, options = {}) {
    this.modalService.open(content, options).result.then((result) => {
      console.log(`Closed with: ${result}`);
    }, (reason) => {
      console.log(`Dismissed ${this.getDismissReason(reason)}`);
    });
  }

  private getDismissReason(reason: any): string {
    if (reason === ModalDismissReasons.ESC) {
      return 'by pressing ESC';
    } else if (reason === ModalDismissReasons.BACKDROP_CLICK) {
      return 'by clicking on a backdrop';
    } else {
      return  `with: ${reason}`;
    }
  }

}
