import { Component, OnInit } from '@angular/core';
import { NgbModal, ModalDismissReasons } from '@ng-bootstrap/ng-bootstrap';
import { Tax } from '../../models/tax'
import { TaxService } from '../../services/tax.service'
import { Observable } from 'rxjs'

@Component({
  selector: 'app-brands',
  templateUrl: './brands.component.html',
  styleUrls: ['./brands.component.scss']
})
export class BrandsComponent implements OnInit {

  constructor(private modalService: NgbModal, private taxService: TaxService) { }

  ngOnInit() {
    this.getAllCompanyTax()
  }

  open(content, options = {}) {
    this.modalService.open(content, options).result.then((result) => {
      console.log(`Closed with: ${result}`);
    }, (reason) => {
      console.log(`Dismissed ${this.getDismissReason(reason)}`);
    });
  }

  private getDismissReason(reason: any): string {
    if (reason === ModalDismissReasons.ESC) {
      return 'by pressing ESC';
    } else if (reason === ModalDismissReasons.BACKDROP_CLICK) {
      return 'by clicking on a backdrop';
    } else {
      return  `with: ${reason}`;
    }
  }
  public taxIsInclusive:boolean
  public taxData
  public userDetail = localStorage.getItem("CurrentUser")
  public companyID = JSON.parse(this.userDetail).company.id
  public userID = JSON.parse(this.userDetail).id
  public taxModel = new Tax("","",5,"p",this.taxIsInclusive,"",0,this.userID,this.companyID)
  public companyTaxArray
  public salesTax
  public salesTaxExist
  

  submitTax(){
    this.taxService.createTax(this.taxModel).subscribe(data=>{console.log(data)
    this.taxService.getAllCompanyTax(this.companyID).subscribe(data1=>{
        this.companyTaxArray=data1})},
    err=>{console.log(err)})
    
  }
  getAllCompanyTax(){
    this.taxService.getAllCompanyTax(this.companyID).subscribe(data=>{
    this.companyTaxArray=data
    let arr = []
    for (let i=0;i<this.companyTaxArray.length;i++){
      //console.log(this.companyTaxArray[i].purpose)
      arr.push(this.companyTaxArray[i].purpose)
      
    }
    this.salesTax = arr.indexOf("Sales")
    console.log(this.salesTax)
    if (this.salesTax == -1){
      this.salesTaxExist = true
    }
    else{
      this.salesTaxExist = false
    }
    
    })
  }

  editTax(id){
    console.log(id)
    this.taxService.getATaxById(id).subscribe(data=>{//console.log(data)
    this.taxData=data
    this.taxModel = new Tax(this.taxData.name,this.taxData.code,this.taxData.amount,this.taxData.type,this.taxData.isInclusive,this.taxData.purpose,id,this.userID,this.companyID)},
    err=>{console.log(err)})
  }
  
  updateTax(){
    this.taxService.updateATax(this.taxModel).subscribe(data=>{console.log(data)
    console.log(this.taxModel)
    this.taxService.getAllCompanyTax(this.companyID).subscribe(data1=>{
    this.companyTaxArray=data1})},
    err=>{console.log(err)})
    //this.getAllCompanyTax()
    
  }
  
  resetFields(){
    this.taxModel = new Tax("","",0,"p",true || false,"",0,this.userID,this.companyID)
  }
}
