    if(item.type==="p"){
      this.productservice.validateqty(this.currentuser.companyID,this.currentuser.locationID,item.psid,1).subscribe(res=>{
      this.validationqty[this.tabNo].push((<Response> res).status)
      this.cart[this.tabNo].push(new KitchenItem(1,item.name,item.price,item.psid,"p"));
      this.calculatetotal()
      },
    error=>{

    })
    }else{
      this.cart[this.tabNo].push(new KitchenItem(1,item.name,item.price,item.psid,"s"));
      this.validationqty[this.tabNo].push(true)
      this.calculatetotal()
    }
    //console.log(this.cart)
    this.quantity[this.tabNo].push(1)
    this.selectToPrint[this.tabNo].push(false)
    this.saveToLocalStorage()
