  calculateamount(car:Item,i,j){
    car.amount=this.quantity[this.tabNo][j]*car.price
    this.calculatetotal()
    if(car.type==="p"){
      var index = this.cart[this.tabNo].indexOf(car,0)
      this.validateproductadjust(index,car.psid,this.quantity[this.tabNo][j])
    }
  }
  

