onsavecart(){
  if(this.cart[this.tabNo]==undefined || this.cart[this.tabNo].length!=0){
  if(this.customer){
  this.savedcart= new KitchenCartt(this.cart[this.tabNo],this.customer.id,this.currentuser.companyID,this.extData,this.status,this.priority,this.type,this.userCreated,1)
  }else{
    this.savedcart= new KitchenCartt(this.cart[this.tabNo],1,this.currentuser.companyID,this.extData,this.status,this.priority,this.type,this.userCreated,1)
  }
  this.savedcart.code="p";
  this.savedcart.userCreated=this.currentuser.id;
  
  if(!this.savedcartid){
  this.salesservice.savekitchencartt(this.savedcart).subscribe(data=>{
   const response=data;
   console.log(response);
   this.loadcarts();
   this.deletecurrentcart();
  })
  console.log(this.savedcart)
}else{
  console.log("yes");
  this.savedcart.id=this.savedcartid;
  this.salesservice.updatekitchencartt(this.savedcart).subscribe(res=>{
    console.log(res);
    this.loadcarts();
    //this.resetsale();
    this.deletecurrentcart()
  })
}
  }

}


  async selectcart(){
    this.modalService.dismissAll("click")
    await this.checkstocks()
  }

  checkstocks(){
    for(let item of this.cart[this.tabNo]){
      this.productservice.validateqty(this.currentuser.companyID,this.currentuser.locationID,item.psid,item.quantity).subscribe(res=>{
        this.validationqty[this.tabNo].push((<Response> res).status)
      })
    }
  }

