import { Component, OnInit } from '@angular/core';
import { formatDate } from '@angular/common';
import { User } from '../../models/user';
import { ReportingService } from '../../services/reporting.service';
import { IPaymentSummary } from '../../models/PaymentSummary';
import { IStaffSummary } from '../../models/StaffSummary';
import { switchMap, tap, map, mapTo } from 'rxjs/operators';
import { Observable, merge } from 'rxjs';
import { ILocationSummary } from '../../models/LocationSummary';
import { IExpenseSummary } from '../../models/expenseManagement';

@Component({
  selector: 'app-global-business-overview',
  templateUrl: './global-business-overview.component.html',
  styleUrls: ['./global-business-overview.component.scss']
})
export class GlobalBusinessOverviewComponent implements OnInit {
  user: User;
  companyName;
  paymentSummary: IPaymentSummary[]
  staffSummary: IStaffSummary[];
  locationSummary: ILocationSummary[];
  expenseSummary: IExpenseSummary[];
  
  globalSaleObj = {
    totalSaleCount: 0,
    totalSaleValue: 0,
    totalExpenses: 0,
    estimatedProfit: 0,
  }

  constructor(
    private reportService: ReportingService
  ) {
    this.user = JSON.parse(localStorage.getItem("CurrentUser"));
    this.companyName = this.user.company.name;
    this.reportService.getSalesSummaryByLocation(this.user.companyID, 0, "0", "0")
    .pipe(
      map(locations => {
        this.locationSummary = locations;
        locations.forEach(location => {
          this.globalSaleObj.totalSaleCount += location.volume;
          this.globalSaleObj.totalSaleValue += location.value;
        })
      })
    ).subscribe();
    
    
    const staffSale = this.reportService.getSalesSummaryByStaff(this.user.companyID, 0, "0", "0");
    const paymentType = this.reportService.getpaymentssummary(this.user.companyID, 0, "0", "0");
    this.reportService.getExpenseSummary(this.user.companyID, this.user.locationID, "0", "0")
    .subscribe(data => {
      data.forEach(data => {
        this.globalSaleObj.totalExpenses += data.amount;
      })
      this.expenseSummary = data;
    });

    const combined = staffSale.pipe(
      switchMap(staffSummarySale => {
        return paymentType
        .pipe(
          tap((paymentType: IPaymentSummary[]) => {
            this.staffSummary = staffSummarySale;
            this.paymentSummary = paymentType;
            })
            
          )
      })
    );
    combined.subscribe();
    }

  ngOnInit() {
  }

  printOverview(){
    var divToPrint = document.getElementById('overview');
    var htmlToPrint = '' +
        '<style type="text/css">' +
        'table tr {' +
        'padding:0.5em;' +
        'text-align: center;'+
        'border: 1px solid #000;'+
        '}' +
        'table {'+
          'width: 100%;'+
          'border: 1px solid #000;'+
          '}'+
        '</style>';
    htmlToPrint = htmlToPrint+ divToPrint.outerHTML;
    let newWin = window.open("");
    newWin.document.write(htmlToPrint);
    newWin.print();
    newWin.close();
    
  }

  formatDate(date){
    return formatDate(date,'dd MMM yyy','en'); 
  }


}
