import { Component, OnInit } from '@angular/core';
import { User } from '../../models/user';
import { ReportingService } from '../../services/reporting.service';
import { IProfitLocation, IProfitCustomer, IProfitCashier } from '../../models/Profit';
import { map } from 'rxjs/operators';
import { formatDate } from '@angular/common';
import { UserService } from '../../services/user.service';
import { ExcelService } from '../../services/excel.service';
import { Formats } from '../../Classes/Formats';

@Component({
  selector: 'app-profit-report',
  templateUrl: './profit-report.component.html',
  styleUrls: ['./profit-report.component.scss', '../../../vendor/libs/ngx-sweetalert2/ngx-sweetalert2.scss','../../../vendor/libs/ng-select/ng-select.scss','../../../vendor/libs/angular-2-dropdown-multiselect/angular-2-dropdown-multiselect.scss']
})
export class ProfitReportComponent implements OnInit {
  format = new Formats();
  loading = true;
  isLocation = true;
  isCashier = false;
  isCustomer = false;
  user:User;
  summaryData: IProfitLocation[] | IProfitCustomer[] | IProfitCashier[];
  currentDate = new Date();

  // locations: ILocationSummary[];
  // staffSales: IStaffSummary[];
  selectfrom: any[] = [];
  cashierSearch=false;
  searchbyterm:  Object | any;
  startDate;
   endDate;

  constructor(
    private reportService: ReportingService,
    private userservice:UserService,
    private exportService: ExcelService
  ) {
    this.user=<User> JSON.parse(localStorage.getItem("CurrentUser"));
    this.reportService.getProfitByLocation(this.user.companyID, 0, this.getCurrentDate(), this.getCurrentDate())
      .subscribe(profitLocations => {
        this.summaryData = profitLocations;
        this.loading = false;
      })
    
   }

  ngOnInit() {
    
  }

  getCurrentDate(){
    return formatDate(this.currentDate,'dd-MM-yyyy','en').toString();
  }


  filterStatus = 'Any';

  filterVal = '';
  currentPage = 1;
  totalItems = 0;



  filterchange(){
   if(this.filterStatus==="Branch"){
    this.isLocation = true;
    this.isCashier = false;
    this.isCustomer = false;    
    this.cashierSearch=false;
    this.reportService.getLocationsByCompanyID(this.user.companyID).pipe(
      map((locations: any[]) => {
        locations.push({
          name: "All",
          id: 0
        })
        locations.reverse();
        this.selectfrom = locations;
      })
      
    ).subscribe()
    }else if(this.filterStatus==="Customer"){
      this.isLocation = false;this.isCashier = false;this.isCustomer = true;
      this.reportService.getAllCustomer(this.user.companyID).pipe(
       map(customers => {
         customers.push({
           name: "All",
           id: 0
         });
         customers.reverse();
         this.selectfrom = customers;
       })
      ).subscribe();
    
    }else if(this.filterStatus==="Cashier"){
      this.cashierSearch=true;
      this.isLocation = false;this.isCashier = true;this.isCustomer = false;
    
    this.userservice.getCashiers(this.user.companyID)
    .pipe(
      map(data => {
        this.selectfrom=<User[]> data;
      })
    ).subscribe();
  }

}

  //return sum of each column
  getTotal(data: any){
    let totals = {
      totalRevenue: 0,
      totalProfit: 0
    }
    data.forEach(element => {
      totals.totalRevenue = totals.totalRevenue+ element.revenue;
      totals.totalProfit = totals.totalProfit+ element.profit;
    });
    return totals;
  }

onfilter(){
  var startDate = this.startDate ===undefined  || this.startDate.trim() ==""?undefined: formatDate(this.startDate, 'dd-MM-yyyy', 'en');
      var endDate = this.endDate ===undefined  || this.endDate.trim() ==""?undefined: formatDate(this.endDate, 'dd-MM-yyyy', 'en');


      if((typeof startDate !== "undefined" || startDate !="") && (typeof endDate === "undefined" || endDate == "")){
        endDate = "0";
      }else if((typeof startDate === "undefined" || startDate == "") && (typeof endDate !== "undefined" || endDate != "")){
        startDate = "0";
      }else if((typeof startDate === "undefined" || startDate == "") &&  (typeof endDate === "undefined" || endDate == "")){
        startDate = "0", endDate = "0";
      }

  switch (this.filterStatus) {
    case "Branch":
        this.loading = true;
        this.reportService.getProfitByLocation(this.user.companyID, this.searchbyterm.id, startDate, endDate)
      .subscribe(summaryByProfit => {
        this.summaryData = summaryByProfit;
        this.loading = false;
      })
        break;
    
    case "Cashier":
      this.loading = true;
      this.reportService.getProfitByCashier(this.user.companyID, this.searchbyterm.id, startDate, endDate)
      .subscribe(summaryByStaff => {
        this.summaryData = summaryByStaff;
        this.loading = false;
      });
      break;

    case "Customer":
      this.loading = true;
      this.reportService.getProfitByCustomer(this.user.companyID, this.searchbyterm.id, startDate, endDate)
      .subscribe(profits => {
        this.summaryData = profits;
        this.loading = false;
      });
      console.log(this.searchbyterm.id);
  
    default:
      break;
  }

}

  getLocations(){
    this.reportService.getLocationsByCompanyID(this.user.companyID).subscribe(
      locations => {
        this.selectfrom = locations;
      } 
    )
  }


formatdate(date){
    return formatDate(date,'d MMM yy','en'); 
  }


  printTable(){
    var divToPrint = document.getElementById('tableRecord');
    var htmlToPrint = '' +
        '<style type="text/css">' +
        'table th {' +
        'padding:0.5em;' +
        'text-align: center;'+
        'border: 1px solid #000;'+
        '}' +
        'table {'+
          'width: 100%;'+
          'border: 1px solid #000;'+
          '}'+
        '</style>';
    htmlToPrint = htmlToPrint+ divToPrint.outerHTML;
    let newWin = window.open("");
    newWin.document.write(htmlToPrint);
    newWin.print();
    newWin.close();
    
  }

  exportASXLSX(){
    this.exportService.exportAsExcelFile(this.summaryData, `${this.user.company.name}-profit-report`);
  }
  downloadAsPDF(){
   this.format.saveAsPDF(this.user.company.name, 'profit-report', 'tableRecord');
    
  }

}
