import { Component, OnInit } from '@angular/core';
import { NgbModal, ModalDismissReasons } from '@ng-bootstrap/ng-bootstrap';
import{Router,ActivatedRoute} from '@angular/router';
import{ProductService} from '../../services/product.service';
import { Product } from '../../models/product';
import { Stock } from '../../models/stock';
import{StockService} from '../../services/stock.service';
import{Response} from '../../models/response';
@Component({
  selector: 'app-duplicateproduct',
  templateUrl: './duplicateproduct.component.html',
  styleUrls: ['./duplicateproduct.component.scss']
})
export class DuplicateproductComponent implements OnInit {
 product:Product;
 stockitem:Stock;
  constructor(private stockservice:StockService,private router:Router,private modalService: NgbModal,private route:ActivatedRoute,private productservice:ProductService) { }

  ngOnInit() {
 this.getproduct();
  }

  getproduct(){
    const id=+this.route.snapshot.paramMap.get('id');
     this.productservice.getproduct(id).subscribe(data=>{
      this.product=<Product> data
     },error=>{
  
     })
  }
  
  onsave(){
   this.product.name=this.product.name+"/variant";
    this.productservice.saveproduct(this.product).subscribe(response=>{
      this.product.id =(<Product>(<Response> response).message).id
      this.createstockitem();
      this.router.navigateByUrl('/layout/products');
    },error=>{
      console.log("something is wrong");
    })
  }
  createstockitem(){
    this.stockitem= new Stock(this.product.id,this.product.locationID,this.product.priceID,this.product.quantity,this.product.companyID);
    this.stockservice.savestockitem(this.stockitem).subscribe(response=>{
    })
    
  }

}
