import { Injectable } from '@angular/core';
import{HttpClient, HttpHeaders} from '@angular/common/http';
import { Stock } from '../models/stock';
import{AuthenticationService} from './authentication.service';
import { Observable } from 'rxjs';
import { IStock } from '../models/Stocks';

@Injectable({
  providedIn: 'root'
})
export class StockService {
   apiurl=this.authservice.api+""
  constructor(private httpclient:HttpClient,private authservice:AuthenticationService) { 
   
  }
  savestockitem(stock:Stock){
      return this.httpclient.post(this.authservice.api+"Stock/save",stock);
  }
  getstock(id:number){
    return this.httpclient.get(this.authservice.api+"Stock/GetByCompany/"+id);
  }
  adjuststock(id:number,qauntity:number,operator:string){
    return this.httpclient.post(this.apiurl+"Stock/adjust",{"stockID":id,"qty":qauntity,"operator":operator});

  }

  //Methods below by Tosin

  getAllStocksOfLocation(locationID: number): Observable<Stock[]>{
    return this.httpclient.get<Stock[]>(`${this.apiurl}Stock/GetByLocation/${locationID}`);
  }

  //get stocks under a particular product
  getAllStockUnderProduct(productID: number): Observable<Stock[]>{
    return this.httpclient.get<Stock[]>(`${this.apiurl}Stock/GetByProduct/${productID}`);
  }
  //getAll stocks by companyID
  getAllStocksByCompanyID(companyID: number): Observable<IStock[]>{
    return this.httpclient.get<IStock[]>(`${this.authservice.api}/Stock/GetByCompany/${companyID}`);
  }
  adjustStock(stockID: number, operator: string, qty: number, userCreated: number, reason: string): Observable<any>{
    return this.httpclient.post<any>(`${this.authservice.api}/Stock/adjust`, {stockID, operator, qty, userCreated, reason});
  }

  postIssueStock(stockID: number, qty: number, issuedTo: number, userCreated: number, reason: string, issuedToType: string): Observable<any>{
    return this.httpclient.post<any>(`${this.authservice.api}Stock/issue`, {
      stockID, qty, issuedTo, userCreated, reason, issuedToType
    })
  }
}
