import { Injectable } from '@angular/core';
import{HttpClient, HttpHeaders} from '@angular/common/http';
import{AuthenticationService} from './authentication.service'

@Injectable({
  providedIn: 'root'
})
export class MailingService {
  apiurl=this.authservice.api+"Message/"
  constructor(private httpclient:HttpClient,private authservice:AuthenticationService) { }

  sendinvoice(id:number){
   return this.httpclient.post(this.apiurl+"SendInvoiceByEmail?InvoiceID="+id,{})
  }
  sendreciept(id:number){
    return this.httpclient.post(this.apiurl+"SendReceiptByEmail?salesRegisterID="+id,{})
  }
  sendmail(content:any){
  return this.httpclient.post(this.apiurl+"Send",content);
    
  }
  sendpaymentreciept(id:number){
    return this.httpclient.post(this.authservice.api+"Message/SendPaymentReceipt?paymentID="+id,{})
  }
}
