import { Component, OnInit } from '@angular/core';
import{UserService} from '../services/user.service';
import{User} from '../models/user';
import{FormBuilder,FormGroup,Validators} from '@angular/forms';
import { error } from '@angular/compiler/src/util';
import { Router } from '@angular/router';
import{Response} from '../models/response';
@Component({
  selector: 'app-signin',
  templateUrl: './signin.component.html',
  styleUrls: ['./signin.component.scss','../../vendor/libs/ngx-sweetalert2/ngx-sweetalert2.scss']
})
export class SigninComponent implements OnInit {
  
  signinform:FormGroup;
  response:object;
  loginresponse:Response;
  currentuser:User;
   notsuccess=false;
   submitted=false;
   //loader
  loading: boolean = false

  constructor(private userservice:UserService, private formbuilder:FormBuilder, public router:Router) {
    this.signinform=this.formbuilder.group({
      username:['',Validators.required],
      password:['',Validators.required]

    })
   }
 
  ngOnInit() {
  }
  
  onsignin(){
    //show loader
    this.loading = true;
    this.submitted=true;
    if (this.signinform.invalid){
      return;
    }

    //send data to Api
    this.userservice.login(this.signinform.controls.username.value,this.signinform.controls.password.value).subscribe(userservice=>{
      this.response=userservice;
     this.loginresponse=<Response> this.response;
     this.currentuser=<User> this.loginresponse.message;
      localStorage.setItem("Name",this.currentuser.firstName +" "+ this.currentuser.lastName);
      localStorage.setItem("CurrentUser",JSON.stringify(this.currentuser));
      this.router.navigateByUrl('/layout');
      this.loading = false;
      console.log(this.currentuser)
      if(this.currentuser.role.name=="Super Admin"){
        this.router.navigateByUrl('/layout')
      }
      else if(this.currentuser.role.name=="Cashier"){
      //this.router.navigateByUrl('/layout/makesale')
        if(this.currentuser.company.appSector===1){
                this.router.navigateByUrl('/layout/makesalerestaurant'); 
               }else{
               this.router.navigateByUrl('/layout/makesale');
               }
      }
      else{
        this.router.navigateByUrl('/layout/profile')
      }
    }, error=>{
    this.notsuccess=true;
    this.loading = false;
      return;
    })
   


  }

}
