import { Component, OnInit} from '@angular/core';
import { Supplier } from '../../models/supplier';
import { SuppliersService } from '../../services/suppliers.service';
import { AllsuppliersComponent } from '../allsuppliers/allsuppliers.component';
import { Country } from '../../models/country';
import { State } from '../../models/state';
import { Lga } from '../../models/lga';
import { CompanyService } from '../../services/company.service';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { User } from '../../models/user';

@Component({
  selector: 'app-editsupplier',
  templateUrl: './editsupplier.component.html',
  styleUrls: ['./editsupplier.component.scss',
  '../../../vendor/libs/ng-select/ng-select.scss','../../../vendor/libs/angular-2-dropdown-multiselect/angular-2-dropdown-multiselect.scss']
})
export class EditsupplierComponent implements OnInit {
  supplier:Supplier;
  loading=false;
  countries:Country[];
  states:State[];
  lga:Lga[];
  currentuser: User;
  addsupplierform:FormGroup
  selectedCountryID
  selectedStateID
  selectedlgaid

  constructor(private supplierservice:SuppliersService, private supcomp:AllsuppliersComponent, private companyservice:CompanyService, private fb:FormBuilder) {
    this.supplier=this.supcomp.choosensupplier
    this.currentuser=<User> JSON.parse(localStorage.getItem("CurrentUser"))

    this.addsupplierform=this.fb.group({
      name:['',Validators.required],
      //phoneNo:['',Validators.required],
      //email:[''],
      companyID:[this.currentuser.companyID],
      address:this.fb.group({
        street:[''],
        city:[''],
        country:[''],
        state:[''],
        lga:[''],
        lgaid:[],
        stateID:[],
        countryID:[]
      }),
    })

    if(this.supplier.address == null || this.supplier.address.state == null || this.supplier.address.country == null || this.supplier.address.lga == null){
      this.supplier.address={
        street:"",
        city:"",
        country:{name:""},
        state:{name:""},
        lga:{name:""}
      }
    }
    else{
      
    }
    //console.log(this.supplier)
   }

   onclick(){
    console.log(this.supcomp.modalService.dismissAll("click"));
    }

  onsave(){
    this.loading=true
    this.supplier.address.stateID=this.selectedStateID
      this.supplier.address.countryID=this.selectedCountryID
      this.supplier.address.lgaid=this.selectedlgaid
      this.supplierservice.updatesupplier(this.supplier).subscribe(res=>{
        this.loading=false
        this.onclick()
        //this.cuscomp.loaddata()
      })
  }

  loadcountries(){
    this.companyservice.getcountries().subscribe(data=>{
      this.countries=<Country[]> data;
    },error=>{
      return;
    })
  }

  onchangec(chosencountry){
    this.selectedCountryID=chosencountry.id
    this.companyservice.getstates(this.selectedCountryID).subscribe(data=>{
      this.states=<State[]> data;
    },error=>{
      return
    })
   }
   onchangest(chosenstate){
     this.selectedStateID=chosenstate.id
     this.companyservice.getlga(this.selectedStateID).subscribe(data=>{
       this.lga=<Lga[]> data;
     },error=>{
       return;
     })
   }
   onchangel(chosenlga){
    this.selectedlgaid=chosenlga.id
  }

  ngOnInit() {
    this.loadcountries()
  }

}
