import { Component, OnInit } from '@angular/core';
import { ReportingService } from '../../services/reporting.service';
import { UserService } from '../../services/user.service';
import {formatDate} from '@angular/common'
import { map, switchMap, tap, groupBy, mergeMap, toArray } from 'rxjs/operators';
import { IPaymentSummary } from '../../models/PaymentSummary';
import { ILocationSummary } from '../../models/LocationSummary';
import { Observable, from } from 'rxjs';
import { ITopEmployees } from '../../models/topEmployees';
import { CompanyService } from '../../services/company.service';
import { IProductSummary } from '../../models/productSaleSummary';
import * as numeral from 'numeral';
import { ITopCustomer } from '../../models/topCustomers';

@Component({
  selector: 'app-report',
  templateUrl: './report.component.html',
  styleUrls: ['./report.component.scss']
})
export class ReportComponent implements OnInit {
  saleCount:number = 0;
  basketSize: number = 0;
  dataIsLoaded: boolean = false;
  companyName: string = "";
  topEmployees: Observable<ITopEmployees[]>;
  topCustomers: Observable<ITopCustomer[]>;
  filterDate = [null, null];
  locations: any[] = [];
  locationID = 0;
  currentDate = new Date();
  saleRevenueLocations = [];
  view = undefined;

  productDataIsLoaded: boolean = false;
  revenueDataIsLoaded: boolean = false;
  topEmployeeDataIsLoaded: boolean = false;
  paymentOutletDataIsLoaded: boolean = false;

  
  colors = {
    domain: ['#d0a6e0','#89a3d8','#9bb3e5','#b29be5',
'#f0b27a',
'#ebf5fb']
  };
  payments= [
    {name: "CARD", value: 0},
    {name: "CASH", value: 0},
    {name: "TRANSFER", value: 0},
    {name: "CREDIT NOTE", value: 0}
  ]

  
  showXAxis = true;
  showYAxis = true;
  showLegend = true;
  showXAxisLabel = false;
  showYAxisLabel = false;
  gradient = false;
  xAxisLabel = "Products";
  yAxisLabel = "Revenue";
  yScaleMax;
  yScaleMin;
  autoScale = true;

  lineChartData = [];

  user;

  products = [
    
  ]

  constructor(
    private reportService: ReportingService,
    private userService: UserService,
    private companyService: CompanyService
  ) {
    
    this.user = JSON.parse(localStorage.getItem("CurrentUser"));
    this.loadLocations();
    this.companyName = this.user.company.name;
    this.reportService.getpaymentssummary(this.user.companyID, this.locationID, this.getCurrentDate(), this.getCurrentDate()).subscribe(
      (payments: IPaymentSummary[])=>{
        this.groupByPaymentMethod(payments); 
        this.paymentOutletDataIsLoaded = true;
      }
    )
    this.reportService.getSalesSummaryByLocation(this.user.companyID, this.locationID, this.getCurrentDate(), this.getCurrentDate())
    .subscribe((sales: ILocationSummary[]) => {
      this.getTotalSalesCount(sales);
    })
    this.topEmployees = this.reportService.getTopEmployees(this.user.companyID, this.locationID, this.getCurrentDate(), this.getCurrentDate());
    this.reportService.getProductSaleSummary(this.user.companyID, this.locationID)
    .subscribe(salesPaymentSummary => {
      this.mapProductSummaryToBarChart(salesPaymentSummary);
      this.productDataIsLoaded = true;
    })

    this.reportService.getSalesSummaryByLocation(this.user.companyID, this.locationID, this.getCurrentDate(), this.getCurrentDate())
    .subscribe(saleSummary=> {
      const saleObservable = from(saleSummary)
      const sales = saleObservable.pipe(
        groupBy((location:any) => location.locationID),
        mergeMap((group:any) => group.pipe(toArray()))
      );
      sales.subscribe(val => {
        this.saleRevenueLocations.push(val);
      });
      this.setSaleRevenueOnChartByLocation(this.saleRevenueLocations);
      this.revenueDataIsLoaded = true;
    });
    this.topCustomers = this.reportService.getTopCustomers(this.user.companyID, this.user.locationID, this.getCurrentDate(), this.getCurrentDate());
    
   }

  ngOnInit() {
  }


  setSaleRevenueOnChartByLocation(saleRevenueLocation){
    console.log(saleRevenueLocation);
    saleRevenueLocation.forEach((locations: any[]) => {
      let revenueLocation = {
        name: "",
        series: [
          
        ]
      }
      
      locations.forEach(location => {
        revenueLocation.name = location.locationName;
        revenueLocation.series.push({
          value: location.value,
          name: formatDate(location.saleDate,'dd-MM-yyyy','en')
        })
        
      })
      this.lineChartData.push(revenueLocation);
    })
  }

  //To get salesCount, Basket Size
  getTotalSalesCount(payment: ILocationSummary[]){
    this.saleCount = 0;
    payment.forEach(payment => {
      this.saleCount = this.saleCount+payment.volume
    })
  }

  //Load locations to dropdown
  loadLocations(){
    this.companyService.getcompanylocations(this.user.companyID)
    .subscribe((locations: any[]) => {
      this.locations = locations;
    })
  }

  setLocationID(selectedLocationID){
    this.locationID = selectedLocationID;
  }

  //To group the payment methods total value to display on payment outlet chart
  groupByPaymentMethod(payments: IPaymentSummary[]){
    payments.forEach(payment => {
      switch (payment.method){
        case 'card':
          this.payments[0].value = this.payments[0].value + payment.total;
          break;
        case 'cash':
        this.payments[1].value = this.payments[1].value + payment.total;
          break;
        case 'transfer':
          //this.paymentByInvoice.push(payment);
          this.payments[2].value = this.payments[2].value + payment.total;
          break;
        case 'creditnote':
        this.payments[3].value = this.payments[3].value + payment.total;
          break;
        default:  
      }
    })
  }


  mapProductSummaryToBarChart(productSummary: IProductSummary[]){
    
    productSummary.forEach(productSummary => {
      let newProductSummary = {
      name: "",
      value: 0
    };
      newProductSummary.value = productSummary.price*productSummary.qtySold;
      newProductSummary.name = productSummary.name;
      this.products.push(newProductSummary);
    })
  }

  load(){
    
    this.paymentOutletDataIsLoaded = false;
    this.revenueDataIsLoaded = false;
    var startDate:any;
      var endDate:any;
      if(this.filterDate[0]===null){
        startDate=this.getCurrentDate();
        endDate=this.getCurrentDate();
     }else{
     startDate= formatDate(this.filterDate[0],'dd-MM-yyyy','en');
     endDate= formatDate(this.filterDate[1],'dd-MM-yyyy','en');
     }
      const paymentSummary = this.reportService.getpaymentssummary(this.user.companyID, this.locationID, startDate, endDate);
      const salesSummary = this.reportService.getSalesSummaryByLocation(this.user.companyID, this.locationID, startDate, endDate);
     this.payments.forEach(payment => {
       payment.value = 0;
     })
      const combinedSummary = paymentSummary
      .pipe(
        switchMap((payments: any[]) => {
          return salesSummary
          .pipe(
            tap(sales => {
              this.groupByPaymentMethod(payments);
              this.getTotalSalesCount(sales);
             this.paymentOutletDataIsLoaded = true;
            })
          )
        })
      );
      combinedSummary.subscribe();
      this.reportService.getProductSaleSummary(this.user.companyID, this.locationID)
      .subscribe();


    this.reportService.getSalesSummaryByLocation(this.user.companyID, this.locationID, startDate, endDate)
    .subscribe((sales: ILocationSummary[]) => {
      this.getTotalSalesCount(sales);
    })

      this.saleRevenueLocations = [];
      this.lineChartData = [];

      this.reportService.getSalesSummaryByLocation(this.user.companyID, this.locationID, startDate, endDate)
    .subscribe(saleSummary=> {
      const saleObservable = from(saleSummary)
      const sales = saleObservable.pipe(
        groupBy((location:any) => location.locationID),
        mergeMap((group:any) => group.pipe(toArray()))
      );
      sales.subscribe(val => {
        this.saleRevenueLocations.push(val);
      });
      this.setSaleRevenueOnChartByLocation(this.saleRevenueLocations);
      this.revenueDataIsLoaded = true;
    });
    this.topEmployees = this.reportService.getTopEmployees(this.user.companyID, this.locationID, startDate, endDate);
    this.topCustomers = this.reportService.getTopCustomers(this.user.companyID, this.locationID, startDate, endDate);

  }

  //To get top employees
  getTopEmployees(){

  }

  getSumOfTotalPaymentValue(payment: IPaymentSummary[]){
    payment.forEach(payment => {
      
    })
  }
  getCurrentDate(){
    return formatDate(this.currentDate,'dd-MM-yyyy','en').toString();
  }

  getAllUsers(){
    this.userService.getUsers(this.user.companyID)
  }
  getProductsByCompanyID(){

  }
  getPaymentSummary(){
    //this.reportService.getpaymentssummary(this.user.companyID, )
  }

  formatPrice(price, dropDecimals = false) {
  return numeral(price).format(dropDecimals ? '0,0' : '0,0.00');
}

}
