import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { User } from '../../../models/user';
import { CompanyService } from '../../../services/company.service';
import { Country } from '../../../models/country';
import { State } from '../../../models/state';
import { Lga } from '../../../models/lga';
import { Bank } from '../../../models/bank'
import { VendorService } from '../../../services/vendor.service';
import { Vendor } from '../../../models/vendor';
import { VendorsComponent } from '../../vendor/vendors/vendors.component'


@Component({
  selector: 'app-addvendor',
  templateUrl: './addvendor.component.html',
  styleUrls: ['./addvendor.component.scss',
  '../../../../vendor/libs/ng-select/ng-select.scss','../../../../vendor/libs/angular-2-dropdown-multiselect/angular-2-dropdown-multiselect.scss']
})
export class AddvendorComponent implements OnInit {
  addvendorform:FormGroup
  currentuser:User
  countries:Country[]
  states:State[]
  lga:Lga[]
  loading = false
  banks:Bank[]
  bankID:number

  constructor(private fb:FormBuilder, private companyservice:CompanyService, private vendorservice:VendorService, private vencomp:VendorsComponent){
    this.currentuser=<User> JSON.parse(localStorage.getItem("CurrentUser"))
    this.addvendorform = this.fb.group({
      name:['',Validators.required],
      accountname:['',Validators.required],
      accountnumber:['',Validators.required],
      bankname:['',Validators.required],
      bankID:[],
      companyID:[this.currentuser.companyID],
      address:this.fb.group({
        street:[''],
        city:[''],
        country:[''],
        state:[''],
        lga:[''],
        lgaid:[],
        stateID:[],
        countryID:[]
      }),
    })
  }

  ngOnInit() {
    this.loadcountries()
    this.loadbanks()
  }

  loadcountries(){
    this.companyservice.getcountries().subscribe(data=>{
      this.countries=<Country[]> data
    },error=>{
      return
    })
  }

  onchangec(){
    this.companyservice.getstates((<Country>this.addvendorform.controls.address.value.country).id).subscribe(data=>{
      this.states=<State[]> data;
    },error=>{
      return
    })
   }
   onchangest(){
     this.companyservice.getlga((<State>this.addvendorform.controls.address.value.state).id).subscribe(data=>{
       this.lga=<Lga[]> data;
     },error=>{
       return;
     })
   }

   loadbanks(){
     this.vendorservice.getBanks().subscribe(data=>{
       this.banks=<Bank[]>data},
    err=>{
      return
    })
   }

   onclick(){
     console.log(this.vencomp.modalservice.dismissAll("click"))
   }

   onsave(){
    this.addvendorform.value.bankID=this.addvendorform.value.bankname.id
    this.addvendorform.value.address.lgaid=this.addvendorform.value.address.lga.id
    this.addvendorform.value.address.stateID=this.addvendorform.value.address.state.id
    this.addvendorform.value.address.countryID=(<Country>this.addvendorform.controls.address.value.country).id
    this.loading=true;
    this.vendorservice.savevendor(<Vendor> this.addvendorform.value).subscribe(res=>{
      this.loading=false;
      this.onclick();
      this.vencomp.loaddata();
      console.log(res)
    },error=>{
      console.log(error)
    })
    console.log(this.addvendorform.value)
    
  }

}
