import { Injectable } from '@angular/core';
import {} from '../models/service-data';
import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { Observable, of } from 'rxjs';
import {ServiceObj} from '../models/service-data';
import{AuthenticationService} from './authentication.service';




@Injectable({
  providedIn: 'root'
})
export class ServiceDataService {
  
  services: ServiceObj[];

  constructor(
    private httpClient: HttpClient,private authservice:AuthenticationService
  ) {

   }

  postService(serviceForm): Observable<any>{
   return  this.httpClient.post<any>(this.authservice.api+`Service/save`, serviceForm)
    
  }

  getServiceByID(serviceID: number): Observable<ServiceObj>{
    return this.httpClient.get<ServiceObj>(this.authservice.api+`Service/${serviceID}`);
  }

  updateService(serviceForm: ServiceObj): Observable<any>{
  return  this.httpClient.post<any>(this.authservice.api+`Service/update`, serviceForm);
  
}
  deleteServiceByID(serviceID: number){
    return this.httpClient.post(this.authservice.api+`Service/delete?=${serviceID}`, null);
  }
  getAllServicesByCompanyID(companyId: number){
   return  this.httpClient.get<ServiceObj[]>(this.authservice.api+`Service/GetByCompany/${companyId}`);
   
  }

  postBulkService(bulkData){
    return this.httpClient.post(this.authservice.api+`Service/saveBulk`, bulkData);
  }

  
}
