import { Customer } from "./customer";
import { Cart } from "./cart";
import{User} from "./user";
import {Company} from "./company";

export class Invoice {
  company:Company;  
  customer:Customer;
    customerID:number;
    invoiceDate:string;
    invoiceNo:string;
    cart:Cart;
    amount:number;
    isPaid:boolean;
    cashier:User;
    id:number;
    companyID:number;
    userCreated:number;
    extData:string;
    tax:number;
    taxPercent:number;
    taxName:string;
    taxInclusive:boolean;
    discount:number;
    constructor(cid:number,cart:Cart,amount:number,cashier:User,companyid:number,ispaid:boolean,discount:number){
this.customerID=cid;
  this.cart=cart;
  this.amount=amount;
  this.cashier=cashier;
  this.companyID=companyid;
  this.isPaid=ispaid;
  this.discount=discount;
    }
    

}
