import { Component, OnInit } from '@angular/core';
import{Salesregister} from '../../models/salesregister'
import{SalesService} from '../../services/sales.service';
import { User } from '../../models/user';
import { ReportingService } from '../../services/reporting.service';
import { Report } from '../../models/report';
import { formatDate } from '@angular/common';
import * as numeral from 'numeral';
@Component({
  selector: 'app-recentsalespayments',
  templateUrl: './recentsalespayments.component.html',
  styleUrls: ['./recentsalespayments.component.scss']
})
export class RecentsalespaymentsComponent implements OnInit {
  recentsales:Salesregister[];
  currentuser:User;
  reports:Report[];
  cash=50;
  card=0;
  transfer=0;
  creditnote=0;
  loaded=false;
  loading=false;
  constructor(private salesservice:SalesService,private reportservice:ReportingService) { 
    this.currentuser=<User> JSON.parse(localStorage.getItem("CurrentUser"));

  }

  ngOnInit() {
this.loaddata();
this.loadpaymentsummary();
  }
  loaddata(){
    this.salesservice.getsalebycompany(this.currentuser.companyID,"01-"+formatDate(Date.now(),"MM-yyyy","en"),formatDate(Date.now(),"dd-MM-yyyy","en")).subscribe(data=>{
      this.recentsales=<Salesregister[]>data;
      if(this.recentsales.length>=10){
       this.recentsales= this.recentsales.slice(0,9);
      }
      this.loading=true;
    })
  }

  loadpaymentsummary(){
    this.reportservice.getpaymentssummary(this.currentuser.companyID,0,"01-"+formatDate(Date.now(),"MM-yyyy","en"),0).subscribe(data=>{
      this.reports=<Report[]>data;
      this.reports.filter(r=>r.method==="cash").map((c)=>{this.cash=this.cash+c.total})
      this.reports.filter(r=>r.method==="card").map((c)=>{this.card=this.card+c.total})
      this.reports.filter(r=>r.method==="transfer").map((c)=>{this.transfer=this.transfer+c.total})
      this.reports.filter(r=>r.method==="creditnote").map((c)=>{this.creditnote=this.creditnote+c.total})
     
      var total = this.cash+this.card+this.transfer+this.creditnote;
      this.cash=Math.round((this.cash/total)*100);
      this.card=Math.round((this.card/total)*100);
      this.transfer=Math.round((this.transfer/total)*100);
      this.creditnote=Math.round((this.creditnote/total)*100);
      this.loaded=true;
    })
  }
  formatdate(date){
    return formatDate(date,'d MMM yy HH:mm','en'); 
  }
  formatPrice(price, dropDecimals = false) {
    return numeral(price).format(dropDecimals ? '0,0' : '0,0.00');
  }

}
