import { Component, OnInit } from '@angular/core';
import{FormBuilder,FormGroup,Validators} from '@angular/forms';
import { User } from '../../models/user';
import { Customer } from '../../models/customer';
import { CustomerService } from '../../services/customer.service';
import{Payment} from '../../models/payment';
import{SalesService} from '../../services/sales.service';
import { Invoice } from '../../models/invoice';
import { formatDate } from '@angular/common';
import { Salesregister } from '../../models/salesregister';
import { Response } from '../../models/response';
import * as numeral from 'numeral';
import { MailingService } from '../../services/mailing.service';
import { Router } from '@angular/router';
import { Observable, Observer } from 'rxjs';
import { AccountsetupService } from '../../services/accountsetup.service';
import { AccountSetup } from '../../models/accountSetup';
import { TerminalSetting } from '../../models/terminalSetting';
import { TerminalSetupService } from '../../services/terminal-setup.service';
@Component({
  selector: 'app-quickpayment',
  templateUrl: './quickpayment.component.html',
  styleUrls: ['./quickpayment.component.scss','../../../vendor/libs/ng-select/ng-select.scss']
})
export class QuickpaymentComponent implements OnInit {
  paymentform:FormGroup;
  currentuser:User;
  customers:Customer[];
  payment:Payment;
  tiesale=false;
  invoice:Salesregister;
  amount=0;
  sales:Salesregister[];
  customer:Customer;
  customername="Guest";
  print=false;
  today:number=Date.now();
  paymentid:number;
  base64Image2data:string;
  base64Image2:any;
  public displayAccount;
  chosenAccount;


  constructor( private terminalSetupService:TerminalSetupService, private formbuilder:FormBuilder,private customerservice:CustomerService,private saleservice:SalesService,private mailservice:MailingService,private router:Router,private accountSetupService:AccountsetupService) {
    this.currentuser= <User> JSON.parse(localStorage.getItem("CurrentUser"));
    if (localStorage.getItem("pendingsale")){
      this.invoice=JSON.parse(localStorage.getItem("pendingsale"));
      this.tiesale=true;
      this.customer=this.invoice.invoice.customer;
      this.paymentform= this.formbuilder.group({
        customer:[this.customer,Validators.required],
        payment:['',Validators.required],
        reference:[''],
        tiesale:[true],
        invoice:[this.invoice]
  
      })
    }else{
      this.paymentform= this.formbuilder.group({
        customer:[null,Validators.required],
        payment:['',Validators.required],
        reference:[''],
        tiesale:[false],
        invoice:[]
  
      })
    }
   
   }

  ngOnInit() {
    this.getBase64ImageFromURL('assets/img/simplify.png').subscribe(base64data => {    
    
      // this is the image as dataUrl
      this.base64Image2data=base64data;
      this.base64Image2 = 'data:image/jpg;base64,' + base64data;
    });

  }
  getBase64Image(img: HTMLImageElement) {
    // We create a HTML canvas object that will create a 2d image
    var canvas = document.createElement("canvas");
    canvas.width = img.width;
    canvas.height = img.height;
    var ctx = canvas.getContext("2d");
    // This will draw image    
    ctx.drawImage(img, 0, 0);
    // Convert the drawn image to Data URL
    var dataURL = canvas.toDataURL("image/png");
 return dataURL.replace(/^data:image\/(png|jpg);base64,/, "");
 }

  getBase64ImageFromURL(url: string) {

    return Observable.create((observer: Observer<string>) => {
      // create an image object
      let img = new Image();
      img.crossOrigin = 'Anonymous';
      img.src = url;
      if (!img.complete) {
          // This will call another method that will create image from url
          img.onload = () => {
          observer.next(this.getBase64Image(img));
          observer.complete();
        };
        img.onerror = (err) => {
           observer.error(err);
        };
      } else {
          observer.next(this.getBase64Image(img));
          observer.complete();
      }
    });
 }
 
  searchcustomer(searchterm:string){
   this.customerservice.getcustomer(this.currentuser.companyID,searchterm).subscribe(data=>{
     this.customers=<Customer[]> data;
     this.customers.map((i)=>{i.name=i.name+"/"+i.phoneNo+"/"+i.email})
   },error=>{
     return;
   })
  }
  onsave(){
    if(localStorage.getItem("pendingsale")){
      localStorage.removeItem("pendingsale");
    }
    console.log(this.paymentform.value);
    if(this.paymentform.valid){
   this.payment= new Payment((<Customer>this.paymentform.controls.customer.value).id,this.currentuser.companyID,this.amount,this.paymentform.controls.payment.value,0,formatDate(Date.now(),'d MMM yy HH:mm','en'));
   this.payment.userCreated=this.currentuser.id;
   this.payment.accountID=this.chosenAccount;
  if(this.tiesale){
this.payment.saleRegisterID=this.invoice.id;
  var payment = this.invoice.payments.filter(i=>(i.method==="creditnote"))[0];
 ( <Payment> payment).amount=(<Payment> payment).amount-this.amount;
 this.saleservice.updatepayment(payment).subscribe(res=>{
  this.invoice.invoice.amount= ( <Payment> payment).amount;
  if(this.invoice.invoice.amount===0){
    this.invoice.invoice.isPaid=true;
  }
  this.saleservice.updateinvoice(this.invoice.invoice).subscribe(res=>{
    console.log(res);
    this.saleservice.savepayment(this.payment).subscribe(res=>{
       this.paymentid= (<Payment>(<Response> res).message).id;
      this.customername=this.customer.name.slice(0,this.customer.name.indexOf("/",0));
      this.tiesale=false;
      this.print=true;

    })

  })
 })

  }else{
   this.saleservice.savepayment(this.payment).subscribe(res=>{
    this.paymentid= (<Payment>(<Response> res).message).id;
  this.customername=this.customer.name.slice(0,this.customer.name.indexOf("/",0));
  this.tiesale=false;
  this.print=true;
})
  }
}
}

  ontie(){
    this.tiesale=!this.tiesale;
  
  }

   
  displayAccount_Setup(){
    this.accountSetupService.get_accts(this.currentuser.companyID,this.currentuser.locationID).subscribe(data=>{ console.log(data)
    this.displayAccount = data;
    (<AccountSetup[]>this.displayAccount).map((i)=>{i.bankName=i.bankName+"/"+i.accountNumber});
    
    })
  }
  
  displayTerminal_Setup(){

    this.terminalSetupService.getTerminalSetup(this.currentuser.companyID,this.currentuser.locationID).subscribe(data=>{//console.log(data)
    this.displayAccount = data;
    (<TerminalSetting[]>this.displayAccount).map((i)=>{i.bankName=i.bankName+"/"+i.terminalID});

    
    })
  }

  additionalpay(){
    if(this.paymentform.controls.payment.value=="transfer"){
      this.displayAccount_Setup();
    }else if(this.paymentform.controls.payment.value=="card"){
      this.displayTerminal_Setup();
    }
    
  }

  getinvoices(){
    this.customer=<Customer>this.paymentform.controls.customer.value;
    var today = Date.now();
    var start = '01-01-';
   start=start+ formatDate(today,'yyyy','en');
    this.saleservice.getsalebycustomer(this.currentuser.companyID,(<Customer>this.paymentform.controls.customer.value).id,start,formatDate(today,'dd-MM-yyyy','en')).subscribe(data=>{
    this.sales=<Salesregister[]> data;
    this.sales=this.sales.filter(i=>i.invoice.isPaid===false)
    this.sales.map((i)=>{i.salesDate=i.salesDate+"/#"+i.invoice.invoiceNo})

    })

  }
  chooseinvoice(){
 this.invoice=(<Salesregister>this.paymentform.controls.invoice.value);
 console.log(this.invoice);
  }

  formatPrice(price, dropDecimals = false) {
    return numeral(price).format(dropDecimals ? '0,0' : '0,0.00');
  }

  onprint(){
let printcontent=document.getElementById("reciept").innerHTML
const win = window.open('');
win.document.write(printcontent);
win.focus();
win.print();
win.close();
//this.paymentform.reset();
//this.amount=0;
//this.print=false;
//this.invoice=null;
this.router.navigateByUrl('/layout/VAS');

  }
  onsend(){
this.mailservice.sendpaymentreciept(this.paymentid).subscribe(res=>{
  this.router.navigateByUrl('/layout/VAS');
})
  }

}