import { Component, OnInit } from '@angular/core';
import { NgbModal, ModalDismissReasons } from '@ng-bootstrap/ng-bootstrap';
import { Router } from '@angular/router';
import{Roles} from '../../models/roles';
import{User} from '../../models/user';
import{UserService} from '../../services/user.service';
import{CompanyService} from '../../services/company.service';
import{Company} from '../../models/company';
import{Location} from '../../models/location';

@Component({
  selector: 'app-staff',
  templateUrl: './staff.component.html',
  styleUrls: ['./staff.component.scss']
})
export class StaffComponent implements OnInit {
   
  roles:Roles[];
  stores:Location[];
  response:object;
  staff:User[];
  currentuser:User;
  currentstaff:User;
  loading=true;
  errormsg="";
 error=false;

  constructor(public modalService: NgbModal, private userservice:UserService,private companyservice:CompanyService,private router:Router) { 
  this.currentuser=<User> JSON.parse(localStorage.getItem("CurrentUser"));
  this.loadData();
  this.loadroles();
  this.loadstores(); }
 

  filterVerified = 'Any';
  filterRole = 'Any';
  filterStatus = 'Any';
  filterLatestActivity = [null, null];
  
 
  displayerror(){
    this.error=true;
    setTimeout(()=>this.error=false,20000);
  }
  loadroles(){
    this.userservice.getRoles(this.currentuser.companyID).subscribe(userservice=>{
      this.response=userservice;
      this.roles=<Roles[]> this.response;
     },error=>{
      this.errormsg=(<Response> error).statusText;
      this.displayerror();
 
     })
  }
  loadstores(){
    this.companyservice.getcompany(this.currentuser.companyID).subscribe(companyservice=>{
      this.response=companyservice;
      this.currentuser.company=<Company> this.response;
      
    },error=>{
      this.errormsg=(<Response> error).statusText;
      this.displayerror();
    })
  }
  open(content, options = {}) {
    this.modalService.open(content, options).result.then((result) => {
      console.log(`Closed with: ${result}`);
    }, (reason) => {
      console.log(`Dismissed ${this.getDismissReason(reason)}`);
    });
  }

  private getDismissReason(reason: any): string {
    if (reason === ModalDismissReasons.ESC) {
      return 'by pressing ESC';
    } else if (reason === ModalDismissReasons.BACKDROP_CLICK) {
      return 'by clicking on a backdrop';
    } else {
      return  `with: ${reason}`;
    }
  }

  ngOnInit() {
  }


  searchKeys = ['id', 'username', 'email', 'name','store'];
  sortBy = 'id';
  sortDesc = true;
  perPage = 10;

  filterVal = '';
  currentPage = 1;
  totalItems = 0;

  usersData: User[] = [];
  originalUsersData: User[] = [];
  
 

  loadData() {

    this.userservice.getUsers(this.currentuser.companyID).subscribe(userservice=>{
      this.response=userservice;
 this.originalUsersData=<User[]> this.response;
 this.update();
 this.loading=false;
    },error=>{
      this.errormsg=(<Response> error).statusText;
      this.displayerror();
      

    })
       

  }
  filterstaff(){
    console.log(this.filterStatus)
    this.usersData=this.usersData.filter(data=>data.location.name===this.filterStatus)
   
  }

  get totalPages() {
    return Math.ceil(this.totalItems / this.perPage);
  }

  update() {
    const data = this.filter(this.originalUsersData);

    this.totalItems = data.length;

    this.sort(data);
    this.usersData = this.paginate(data);
  }

  filter(data) {
    const filter = this.filterVal.toLowerCase();
    return !filter ?
      data.slice(0) :
      data.filter(d => {
        return Object.keys(d)
          .filter(k => this.searchKeys.includes(k))
          .map(k => String(d[k]))
          .join('|')
          .toLowerCase()
          .indexOf(filter) !== -1 || !filter;
      });
  }

  sort(data) {
    data.sort((a: any, b: any) => {
      a = typeof(a[this.sortBy]) === 'string' ? a[this.sortBy].toUpperCase() : a[this.sortBy];
      b = typeof(b[this.sortBy]) === 'string' ? b[this.sortBy].toUpperCase() : b[this.sortBy];

      if (a < b) { return this.sortDesc ? 1 : -1; }
      if (a > b) { return this.sortDesc ? -1 : 1; }
      return 0;
    });
  }

  paginate(data) {
    const perPage = parseInt(String(this.perPage), 10);
    const offset = (this.currentPage - 1) * perPage;

    return data.slice(offset, offset + perPage);
  }

  setSort(key) {
    if (this.sortBy !== key) {
      this.sortBy = key;
      this.sortDesc = false;
    } else {
      this.sortDesc = !this.sortDesc;
    }

    this.currentPage = 1;
    this.update();
  }
}


