import { Component, OnInit} from '@angular/core';
import{CustomersComponent} from '../customers/customers.component';
import { NgbModal, ModalDismissReasons } from '@ng-bootstrap/ng-bootstrap';
import { Router } from '@angular/router';
import { createDecipher } from 'crypto';
import{FormBuilder,FormGroup,Validators} from '@angular/forms';
import { User } from '../../models/user';
import { CustomerService } from '../../services/customer.service';
import { Customer } from '../../models/customer';
import { CompanyService } from '../../services/company.service'
import { Country } from '../../models/country';
import { State } from '../../models/state';
import { Lga } from '../../models/lga';
@Component({
  selector: 'app-addcustomer',
  templateUrl: './addcustomer.component.html',
  styleUrls: ['./addcustomer.component.scss',
  '../../../vendor/libs/ng-select/ng-select.scss','../../../vendor/libs/angular-2-dropdown-multiselect/angular-2-dropdown-multiselect.scss']
})
export class AddcustomerComponent implements OnInit {
 
  addcustomerform:FormGroup;
  currentuser:User;
  loading=false;
  dobb;
  countries:Country[];
  states:State[];
  lga:Lga[];
  customerForm:FormGroup;
  public days:any = ["day"]
  public months:any = ["month"]
  public day
  public month

  constructor(private customercomp:CustomersComponent,private formbuilder:FormBuilder,private customerservice:CustomerService, private companyservice:CompanyService) {
    this.dobb = localStorage.getItem("dobb")
    this.currentuser=<User> JSON.parse(localStorage.getItem("CurrentUser"));
    this.addcustomerform= this.formbuilder.group({
      name:['',Validators.required],
      phoneNo:['',Validators.required],
      email:[''],
      companyID:[this.currentuser.companyID],
      birthdate:[''],
      birthmonth:[''],
      birthday:[],
      billingAddress:this.formbuilder.group({
        street:[''],
        city:[''],
        country:[''],
        state:[''],
        lga:[''],
        lgaid:[],
        stateID:[],
        countryID:[]
      }),
    })
    for (let i=1; i<32; i++){
        this.days.push(i)
    }
    for (let i=1; i<13; i++){
      this.months.push(i)
    }
   }

   ngOnInit() {  
     this.loadcountries()
  }
  onclick(){
  console.log(this.customercomp.modalservice.dismissAll("click"));
  }

  loadcountries(){
    this.companyservice.getcountries().subscribe(data=>{
      this.countries=<Country[]> data;
    },error=>{
      return;
    })
  }

  onchangec(){
    this.companyservice.getstates((<Country>this.addcustomerform.controls.billingAddress.value.country).id).subscribe(data=>{
      this.states=<State[]> data;
    },error=>{
      return
    })
   }
   onchangest(){
     this.companyservice.getlga((<State>this.addcustomerform.controls.billingAddress.value.state).id).subscribe(data=>{
       this.lga=<Lga[]> data;
     },error=>{
       return;
     })
   }


  onsave(){
    this.addcustomerform.value.birthday = `${this.addcustomerform.value.birthdate}/${this.addcustomerform.value.birthmonth}`
    this.addcustomerform.value.billingAddress.lgaid=this.addcustomerform.value.billingAddress.lga.id
    this.addcustomerform.value.billingAddress.stateID=this.addcustomerform.value.billingAddress.state.id
    this.addcustomerform.value.billingAddress.countryID=(<Country>this.addcustomerform.controls.billingAddress.value.country).id
    this.loading=true;
    this.customerservice.savecustomer(<Customer> this.addcustomerform.value).subscribe(res=>{
      this.loading=false;
      this.onclick();
      this.customercomp.loaddata();
    },error=>{

    })
    //console.log(this.addcustomerform.value)
  }

}
