// terminal update
post_terminal_update(terminalUpdateData:NgForm){
  //console.log(terminalUpdateData.value);
  (<TerminalSetting>terminalUpdateData.value).companyID=this.user.companyID;
 (<TerminalSetting>terminalUpdateData.value).locationID=this.user.locationID;
 (<TerminalSetting>terminalUpdateData.value).id=this.sid
  this.terminalSetupService.post_update_terminal(<TerminalSetting>terminalUpdateData.value).subscribe(data=>{console.log(data)
    this.displayTerminalSetup()
      })
}

