export class Item {
    id:number;
    cartID:number;
    type:string;
    psid:number;
    name:string;
    code:string;
    price:number;
    quantity:number;
    amount:number;
    constructor(quan:number,name:string,price:number,psid:number,type:string){
        this.quantity=quan;
        this.name=name;
        this.price=price;
        this.psid=psid;
        this.type=type;
        this.amount=this.quantity*this.price;
    }
}
