import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http'
import { Supplier } from '../models/supplier'
import { AuthenticationService } from './authentication.service'

@Injectable({
  providedIn: 'root'
})
export class SuppliersService {

  constructor(private httpclient:HttpClient, private authservice:AuthenticationService) { }

  savesupplier(supplier:Supplier){
    return this.httpclient.post(this.authservice.api+"Supplier/save",supplier);
  }

  getsuppliers(id:number){
    return this.httpclient.get(this.authservice.api+"Supplier/GetByCompany/"+id);
  }

  updatesupplier(supplier:Supplier){
    return this.httpclient.post(this.authservice.api+"Supplier/update",supplier);
  }

}
