import { Component, OnInit } from '@angular/core';
import { User } from '../models/user';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { FormBuilder, Validators } from '@angular/forms';
import { UserService } from '../services/user.service';

@Component({
  selector: 'app-profile',
  templateUrl: './profile.component.html',
  styleUrls: ['./profile.component.scss']
})
export class ProfileComponent implements OnInit {
  userDataIsLoaded: boolean = false;
  loading=false;
  passwrong=false;
  user:User;
  constructor(
    private modalService: NgbModal,
    private formbuilder: FormBuilder,
    private userservice:UserService,
    
  ) { 
    this.user=<User> JSON.parse(localStorage.getItem("CurrentUser"))
  }

  ngOnInit() {
    
  }

  open(content, options = {}) {
    this.modalService.open(content, options).result.then((result) => {
      console.log(`Closed with: ${result}`);
    }, (reason) => {
      //console.log(`Dismissed ${this.getDismissReason(reason)}`);
    });
  }

  changePasswordForm = this.formbuilder.group({
    oldPassword: ['', [Validators.required]],
    newPassword: ['', [Validators.required]],
    confirmPassword: ['', [Validators.required]]
   } );

   passvalidator(){
    if(this.changePasswordForm.controls.newPassword.value===this.changePasswordForm.controls.confirmPassword.value){
      return true;
    }else{
      return false;
    }
     }

  userProfileForm = this.formbuilder.group({
    firstName: ['', ],
    middleName: [''],
    lastName: [''],
    userName: [''],
    phoneNo: [''],
    email: ['', [Validators.email]],
    location: this.formbuilder.group({
      address: this.formbuilder.group({
        street: [],
        city: [''],
        lga: [''],
        state: [''],
        country: ['']
      })
    })
  })


changePassword(){
  if(this.changePasswordForm.invalid){
    return
  }
  this.loading=true;
this.userservice.login(this.user.userName,this.changePasswordForm.controls.oldPassword.value).subscribe(res=>{
var data={"id":this.user.id,"Password":this.changePasswordForm.controls.confirmPassword.value};
this.userservice.updateuser(<User> data).subscribe(res=>{
  console.log(res);
  this.loading=false;
  this.modalService.dismissAll('click');
},error=>{
  this.loading=false;
})
},error=>{
 this.loading=false;
 this.passwrong=true;
})

}
updateForm(){
  // this.service.updateUser(this.userProfileForm.value)
  // .subscribe(data => console.log(data));
 }

}
