import { Component, OnInit } from '@angular/core';
import { FormBuilder, Validators, FormControl } from '@angular/forms';
import { NgbModal, NgbCalendar, NgbDateStruct } from '@ng-bootstrap/ng-bootstrap';
import { ExpenseManagementService } from '../../services/expense-management.service';
import { IExpenseCategory, IExpense } from '../../models/expenseManagement';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { formatDate} from '@angular/common';


@Component({
  selector: 'app-expense-register',
  templateUrl: './expense-register.component.html',
  styleUrls: ['./expense-register.component.scss']
})
export class ExpenseRegisterComponent implements OnInit {

  expense;
  model: NgbDateStruct;
  today = this.calendar.getToday();
  model2: Date;
  expenseCategories: Observable<IExpenseCategory[]>;
  expenses: IExpense[];
  user=JSON.parse(localStorage.getItem('CurrentUser'));
  loading = true;
  constructor(
    private fb: FormBuilder,
    private modalService: NgbModal,
    private calendar: NgbCalendar,
    private expenseService: ExpenseManagementService
  ) {
    this.user = JSON.parse(localStorage.getItem('CurrentUser'));
    this.expenseCategories = this.expenseService.getExpenseCategoryByCompanyID(this.user.companyID);
    this.loadExpense();
   }

  ngOnInit() {
  }

  loadExpense(){
    this.expenseService.getExpenseByCompany(this.user.companyID).pipe(
      map(expenses =>  {
        this.expenses = expenses;
        this.loading = false;
        console.log("Loaded");
      })
    ).subscribe();
  }

  newExpense = this.fb.group({
    expense_categoryID: [0, [Validators.required]],
    tranAmount: [0, [Validators.required]],
    valueDate: ['', [Validators.required]],
    narration: ['', [Validators.required]],
    locationID: [this.user.locationID],
    companyID: [this.user.companyID]

  })

   addExpense(){
     console.log(this.newExpense.get('valueDate').value);
    this.newExpense.patchValue({locationID: this.user.locationID, companyID: this.user.companyID});
    this.expenseService.saveExpense(this.newExpense.value).subscribe(data => {
      this.loadExpense();
      this.modalService.dismissAll('click');
    })
  }

  formatDate(date){
    return formatDate(date,'dd MMM yyy','en'); 
  }



  open(content, options = {}) {
    this.newExpense.reset();
    this.modalService.open(content, options).result.then((result) => {
      this.newExpense.reset();
      console.log(`Closed with: ${result}`);
    }, (reason) => {

    });
  }

  deleteExpense(expense: IExpense){
    this.expenseService.deleteExpense(this.user.companyID, expense.id, this.user.id).subscribe(
      data=> {console.log(data)
      this.loadExpense();}

    )
    
  }
  editExpense(updateModal, expense: IExpense){
    this.expense = expense;
    this.newExpense.patchValue({
      expense_categoryID: expense.expense_CategoryID,
      tranAmount: expense.tranAmount,
      valueDate: formatDate(expense.valueDate, 'yyy-MM-dd', "en"),
      narration: expense.narration
    });
    this.modalService.open(updateModal);

  }

  updateExpense(){
    this.newExpense.addControl('id', this.fb.control(this.expense.id));
    this.newExpense.patchValue({
      locationID: this.user.locationID, 
      companyID: this.user.companyID,
      expense_CategoryID: this.expense.expense_CategoryID});
    this.expenseService.updateExpense(this.newExpense.value).subscribe(data => {
      
      this.modalService.dismissAll('Click');
    this.loadExpense();
    })
  }

}
