import { Injectable } from '@angular/core';
import{HttpClient, HttpHeaders} from '@angular/common/http';
import { Roles } from '../models/roles';
import { User } from '../models/user';
import{AuthenticationService} from './authentication.service'


@Injectable({
  providedIn: 'root'
})
export class UserService {

  constructor(private httpclient:HttpClient,private authservice:AuthenticationService) {

   }
   

   registerUser(firstname:string,lastname:string,email:string,phoneno:string,username:string,password:string){
    return (this.httpclient.post(this.authservice.api+"Registration/save",{"firstName":firstname,"lastName":lastname,"userName":username, 
    "email":email,"phoneNo":phoneno,"password":password}));
   }
   getUser(id:number){
     return (this.httpclient.get(this.authservice.api+"User/" + id))
   }
   getUsers(companyid:number){
     return(this.httpclient.get<User[]>(this.authservice.api+"User/GetByCompany/"+companyid));
   }
   updateuser(user:User){
     return this.httpclient.post(this.authservice.api+"User/update",user);
   }
   login(identifier:string,password:string){
    return (this.httpclient.post(this.authservice.api+"User/Authenticate",{"identifier":identifier,"password":password}))
   }
   createuserrole(role:Roles){
     return(this.httpclient.post(this.authservice.api+"Role/save",{"name":role.name,"code":role.code,"privileges":role.privileges,"companyID":role.companyID}));

   }
   getRoles(companyid:number){
  return(this.httpclient.get(this.authservice.api+"Role/GetByCompany/"+companyid));
   }

   updateRole(role:Roles){
    return this.httpclient.post(this.authservice.api+"Role/Update",role)
   }
   saveUser(firstname:string,lastname:string,username:string,phoneno:string,email:string,password:string,role:number,location:number,status:string,companyid:number,usercreated:number){
    return (this.httpclient.post(this.authservice.api+"User/save",{"firstName":firstname,"lastName":lastname,"userName":username,"phoneNo":phoneno,"email":email,"Password":password,"roleID":role,"locationID":location,"status":status,"companyID":companyid,"userCreated":usercreated}));
   }

   getCashiers(companyid:number){
    return(this.httpclient.get(this.authservice.api+"User/GetByCompanyCashier/"+companyid));
  }
   getAppMenu(){
     return(this.httpclient.get(this.authservice.api+"AppMenu/GetAll"))
   }
}
